import React, { useState, useEffect, useContext } from 'react'
import Table, { Th, Td, Table_A } from 'controls/Table'
import JN from 'JN'
import Container from 'controls/Container'

const UsersPage = props => {
  const { match } = props

  const [state, setState] = useState({
    is_loading: true,
    pagination: {current: 0, total: 0},
  })

  const [users, setUsers] = useState([
    // {username: 'jax12345', score: 200},
    // {username: 'xaj123123', score: 150},
    // {username: 'system', score: 15},
    // {username: 'konho', score: 250},
  ])

  useEffect(() => {
    let contestId = match.params.contest_id

    if (contestId) {
      JN.get(null, `contest/${contestId}/register`, {},
      (data) => {
        // console.log(data)
        setUsers(data.participants)
        setState({...state, is_loading: false})
      },
      (error) => {
        console.log(error)
        setState({...state, is_loading: false})
      })
    }
    else {
      setState({...state, is_loading: false})
    }

  }, [])

  return (
    <div style={{boxShadow: '0 6px 16px 0 rgba(0,0,0,.2)', borderRadius: '8px', maxWidth: '600px', margin: 'auto', marginTop: '56px', paddingTop: '0'}}>
    <Table count={state.pagination.total > 1 && !state.is_loading}>
      <thead></thead>
      <tbody>
        <tr>
          <Th width="90%"></Th>
          <Th></Th>
          <Th></Th>
          {/* <Th center></Th> */}
        </tr>
        {!state.is_loading && !users.length &&
        <tr><td colSpan="5" style={{fontSize: '14px', textAlign: 'left'}}>   <span style={{color: 'red'}}>!</span></td></tr>
        }

        {state.is_loading && 
        <>
          {/* <tr><td colSpan="5" style={{color: '#666', fontSize: '14px', textAlign: 'left'}}>..</td></tr>
          <tr><td colSpan="5" style={{color: '#666', fontSize: '14px', textAlign: 'left'}}>..</td></tr>
          <tr><td colSpan="5" style={{color: '#666', fontSize: '14px', textAlign: 'left'}}>..</td></tr>
          <tr><td colSpan="5" style={{color: '#666', fontSize: '14px', textAlign: 'left'}}>..</td></tr> */}
        </>
        }

      {!state.is_loading && users.map((user, index) =>
        <tr key={user.username}>
          <Td><Table_A to={`/profile/${user.username}`}>{user.username}</Table_A></Td>
          <Td><Table_A to={`/profile/${user.username}`}>{user.name}</Table_A></Td>
          <Td><Table_A to={`/profile/${user.username}`}>{user.surname}</Table_A></Td>
          {/* <Td center>{5}</Td> */}
        </tr>
        )}
      </tbody>
    </Table>

        {/* {!state.is_loading && <PaginationContainer to={`/contests/:page`} pagination={state.pagination} />} */}
    </div>
  )
}

export default UsersPage