import React, { useState, useRef } from 'react'
import styled, { css } from 'styled-components'
import { theme } from 'Themes'
import Flex from './Flex'
import { MdRemoveRedEye, MdVisibilityOff, MdVisibility } from 'react-icons/md'
import B from './B'

const StyledDiv = styled.div`
  flex-grow: 1;
  position: relative;
  display: flex;
  flex-direction: column;
  
  width: ${props => props.width};
  max-width: ${props => props.width};

  padding-top: ${props => props.title && props.title !== '' ? '15px' : '0'};
  box-sizing: border-box;
`

const StyledTitle = styled.label`
  font-family: ${theme.font};
  text-align: left;
  font-size: 14px;
  color: var(--color_inputText);
  margin-bottom: 4px;
`

const StyledInput = styled.input`
  color: var(--color_inputText);
  font-weight: ${props => props.bold ? 'bold' : 'normal'};
  flex-grow: 1;
  width: 100%;
  padding: 10px 15px;
  margin-bottom: 5px;
  height: 46px;
  font-family: ${theme.font};
  font-size: ${props => props.fontSize ? props.fontSize : '16px'};
  background-color: var(--color_inputBackground);
  border-radius: 5px;
  border: 2px solid var(--color_inputBackground);
  outline: none;

  /* ${props => props.darker && css`
    background-color: var(--color_b2);
    border-color: var(--color_b2);
  `} */

  ${props => (props.eyeType || props.withIcon) && css`
    padding-right: 38px;
  `}

  &:focus {
    transition: border-color ease 250ms, background-color ease 250ms;
    border-color:  var(--color_inputFocusBorder);

    /* border: none; */
    background-color: var(--color_inputFocusBackground);
    /* box-shadow: inset 0px 0px 0px 4px var(--color_inputFocusBorder); */
  }

  &:disabled {
    background: transparent;
    /* border-radius: 0; */
    /* border: none; */
    border: 1px solid var(--color_b22);
    /* padding-left: 0; */
  }

  &::-webkit-calendar-picker-indicator {
    display: none;
  }

  ${props => props.reverse && css`
    background-color: var(--color_b2);
    border-color: var(--color_b2);
    box-shadow: 0 6px 16px rgba(0, 0, 0, 0.2);
    
    &:focus {
      background-color: var(--color_b2);
      border-color: var(--color_b2);
    }
  `}

`

const StyledHelperText = styled.span`
  color: var(--color_c2);
  text-align: left;
  font-family: ${theme.font};
  font-size: 12px;
`

const ErrorText = styled.span`
  color: var(--color_red);
  display: ${props => props.isError ? 'block' : 'none'};
  margin-bottom: 2px;
`

const Input = props => {

  const refInput = useRef(null)

  const onChanged = (e) => {
    if (typeof (props.onChange) === 'function') {
      props.onChange(e)
    }
  }

  const [type, setType] = useState(props.password ? "password" : props.type)

  // let eye = (
  //   <ShowPasswordToggle>s</ShowPasswordToggle>
  // )

  const on_passwordVisibility_toggled = e => {
    e.preventDefault()
    setType(type === 'password' ? 'text' : 'password')
    refInput.current.focus()
  }

  const on_icon_clicked = e => {
    if (typeof (props.onIconClicked) !== 'function') {
      return
    }
    e.preventDefault()
    props.onIconClicked()
  }

  const onBlur = e => {
    if (e.currentTarget.contains(e.relatedTarget)) {
      refInput.current.focus()
      return
    }

    e.preventDefault();

    if (typeof (props.onBlur) !== 'function') {
      return
    }

    props.onBlur()
  }

  return (
    <StyledDiv title={props.title} width={props.width} onBlur={onBlur}>
      {props.title !== '' &&
        <StyledTitle>
          <B>{props.title}</B>
        </StyledTitle>
      }
      <Flex>
        <StyledInput
          ref={refInput}
          color={props.color}
          bold={props.bold}
          disabled={props.disabled}
          fontSize={props.fontSize}
          autoFocus={props.focus}
          id={props.id}
          name={props.name}
          isError={props.isError}
          placeholder={props.placeholder}
          value={props.value}
          onChange={onChanged}
          onKeyDown={props.onKeyDown}
          type={type}
          eyeType={props.password || props.type === 'password'}
          required={props.required}
          list={props.list}
          onClick={props.onClick}
          autoComplete={props.autocomplete}
          darker={props.darker}
          reverse={props.reverse}
          withIcon={props.icon}
        />
        {/* {eye} */}
        {(props.password || props.type === 'password') &&
          <div onClick={on_passwordVisibility_toggled} style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', position: 'absolute', border: 'none', outline: 'none', backgroundColor: 'transparent', right: '1px', bottom: '6px', height: '46px', width: '38px', cursor: 'pointer' }}>
            {type === 'password' ? <MdVisibility size="20" color="var(--color_c2)" /> : <MdVisibilityOff size="20" color="var(--color_primary)" />}
          </div>
        }

        {props.icon &&
          <div tabIndex="0" onClick={on_icon_clicked} style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', position: 'absolute', border: 'none', outline: 'none', backgroundColor: 'transparent', right: '1px', bottom: '6px', height: '46px', width: '38px', cursor: 'pointer' }}>
            {props.icon}
          </div>
        }

      </Flex>
      <StyledHelperText>
        <ErrorText isError={props.isError}>*{props.errorText}</ErrorText>
        {props.helper}
      </StyledHelperText>

      {props.children}
    </StyledDiv>
  )
}

export default Input