import React, { useState, useEffect, useContext } from 'react'
import Modal from 'components/Modal'
import { theme } from 'Themes'
import styled from 'styled-components'
import Button from 'controls/Button'
import { MdVisibility, MdVisibilityOff, MdSentimentSatisfied, MdCloudUpload } from 'react-icons/md'
import JN from 'JN'
import UploadButton from 'controls/UploadButton'
import Flex from 'controls/Flex'
import { UserContext } from 'contexts/UserContext'
import { MessageBoxContext } from 'contexts/MessageBoxContext'
import TextArea from 'controls/TextArea'

const Sample = styled.div `
  display: flex;
  grid-template-columns: auto auto 36px;
  grid-gap: 25px;
  width: 100%;
  margin-top: 7px;
  margin-bottom: 7px;
`
const SampleContent = styled.div `
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 4px;
  font-size: 14px;
  &:nth-child(1) {
    padding-right: 20px;
  }

  textarea {
    height: 150px;    
  }
`
const SampleNav = styled.div `
  margin-top: 20px;
  display: flex;
  justify-content: space-around;
  button {
    display: block;
  }
`

const Action_Button = styled.button `
  font-family: inherit;
  font-size: 14px;
  display: flex;
  margin-right: 5px;
  border: none;
  outline: none;
  background: none;
  border-radius: 5px;
  padding: .7em 1em;
  justify-content: center;
  align-items: center;
  color: var(--color_c2);
  cursor: pointer;

  * {
    cursor: pointer;
  }

  label {
    margin-left: 5px;
  }

  &:hover &:enabled {
    transition: background-color ease 250ms;
    background-color: white;
  }
`

const SampleEditDlg = props => {
  const [state, setState] = useState({
    is_loading: false,
    token: localStorage.getItem('access_token'),
  })

  const { user }  = useContext(UserContext)
  const { toast } = useContext(MessageBoxContext)

  const [sample, setSample] = useState({
    is_visible: 0,
    input: '',
    output: '',
    is_input_file: false,
    is_output_file: false,
    input_file_name: '',
    output_file_name: '',
  })

  //   
  useEffect(() => {

    if (props.sample_id !== -1) {
      JN.get(user, `admin/sample`, {sample_id: props.sample_id, is_admin: 1},
      (data) => {
        setSample(data)
      },
      (error) => {
        console.log(error)
      })
    }
  }, [])

  const on_textArea_change = e => {
    e.preventDefault()
    setSample({
      ...sample,
      [e.target.name]: e.target.value,
    })
  }

  const on_visibility_toggled = e => {
    e.preventDefault()
    setSample({
      ...sample,
      is_visible: 1 - sample.is_visible,
    })
  }

  const on_submit = e => {
    e.preventDefault()
  
    setState({
      ...state,
      is_loading: true,
    })

    // 
    if (props.sample_id !== -1) {
      JN.put(user, `admin/sample`, {
        sample_id: props.sample_id,
        problem_id: props.problem_id,
        input: sample.input,
        output: sample.output,
        is_visible: sample.is_visible,
      },
      () => {
        toast(' ', 'success')
        props.close()
      },
      (error) => {
        console.log(error)
        setState({
          ...state,
          is_loading: false,
        })
      })
      return
    }

    // 
    JN.post(user, `admin/sample`, {
      problem_id: props.problem_id,
      input: sample.input,
      output: sample.output,
      is_visible: sample.is_visible,
    },
    (data) => {
      toast(' ', 'success')
      props.sampleAdded({id: data.id, is_visible: sample.is_visible})
    },
    (error) => {
      console.log(error)
      setState({
        ...state,
        is_loading: false,
      })
    })
  }

  const close = () => {
    if (state.is_loading) {
      return
    }

    props.close()
  }

  const on_input_type_changed = e => {
    
    if (e.target.name === "is_input_file") {
      setSample({
        ...sample,
        is_input_file: e.target.checked,
        input: '',
        input_file_name: '',
      })
    }
    else {
      setSample({
        ...sample,
        is_output_file: e.target.checked,
        output: '',
        output_file_name: '',
      })
    }
  }

  //  
  const on_file_picked = (name, e) => {
    e.preventDefault()
    
    const result = name === "upload_input" ? "input" : "output"

    const file = e.target.files.item(0)

    let reader = new FileReader();
    reader.readAsText(file, "UTF-8");

    reader.onload = (event) => {
      if (name === "upload_input") {
        setSample({
          ...sample,
          input: event.target.result,
          input_file_name: file.name,
        })
      }
      else {
        setSample({
          ...sample,
          output: event.target.result,
          output_file_name: file.name,
        })
      }
    }

    reader.onerror = function (evt) {
      console.log("error reading file")
    }
  }

  return (
    <Modal width="780px" title={props.sample_id !== -1 ? " " : " "} isOpen={true} close={close}>
      <form onSubmit={on_submit}>
        <Sample>
        <SampleContent>
          <Flex justifyContent="space-between">
            
            <div style={{display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
              <input id={`in_input`} name="is_input_file" onChange={on_input_type_changed} type="checkbox" checked={sample.is_input_file} />
              <label htmlFor={`in_input`}></label>
            </div>
          </Flex>
          {sample.is_input_file
            ? 
            <UploadButton name="upload_input" height="150px" fileName={sample.input_file_name} fontSize="18px" OnFilePicked={on_file_picked} />
            :
            <TextArea monospace focus name="input" value={sample.input} onChange={on_textArea_change} required />
          }
        </SampleContent>
        <SampleContent>
        <Flex justifyContent="space-between">
          
          <div style={{display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
            <input id={`out_input`} name="is_output_file" onChange={on_input_type_changed} type="checkbox" checked={sample.is_output_file} />
            <label htmlFor={`out_input`}></label>
          </div>
          </Flex>
          {sample.is_output_file
            ? 
            <UploadButton name="upload_output" height="150px" fileName={sample.output_file_name} fontSize="18px" OnFilePicked={on_file_picked} />
            :
            <TextArea monospace name="output" value={sample.output} onChange={on_textArea_change} />
          }
        </SampleContent>
        <SampleNav>
         
        </SampleNav>
      </Sample>
      <Action_Button disabled={sample.is_large} onClick={on_visibility_toggled}>
        {sample.is_visible
        ? <MdVisibility color="var(--color_primary)" size="20"/>
        : <MdVisibilityOff color="var(--color_primary)" size="20"/>
        }
        <label> </label>
      </Action_Button>
      <Button disabled={state.is_loading || sample.is_large}>
        {state.is_loading ? ".." : props.sample_id !== -1 ? "" : ""}
      </Button>
      </form>
    </Modal>
  )
}

export default SampleEditDlg