export const ScrollTo = (ref, ms = 0, left = false) => {
  if (!ref)
    return;

  if (!ref.current)
    return;

  if (left) {
    if (ref.current) setTimeout(() => window.scrollTo(ref.current.offsetLeft, 0, ms));
  }
  else {
    if (ref.current) setTimeout(() => window.scrollTo(0, ref.current.offsetTop, ms));
  }
}

