var romanMatrix = [
  [1000, 'M'],
  [900, 'CM'],
  [500, 'D'],
  [400, 'CD'],
  [100, 'C'],
  [90, 'XC'],
  [50, 'L'],
  [40, 'XL'],
  [10, 'X'],
  [9, 'IX'],
  [5, 'V'],
  [4, 'IV'],
  [1, 'I']
];

export const convertToRoman = num => {
  if (num === 0) {
    return '';
  }
  for (var i = 0; i < romanMatrix.length; i++) {
    if (num >= romanMatrix[i][0]) {
      return romanMatrix[i][1] + convertToRoman(num - romanMatrix[i][0]);
    }
  }
}

export const Get_PaginationArray = (totalPages, currentPage) => {
  totalPages *= 1
  currentPage *= 1

  //#???    ..   visiblePagesCount  

  let list = []

  if (totalPages <= 7) {
    for (let i = 1; i <= totalPages; i++) {
      list.push(i)
    }
  }
  else {
    list.push(1)

    if (currentPage > totalPages - 4) {
      list = [...list, totalPages - 5, totalPages - 4, totalPages - 3, totalPages - 2, totalPages - 1]
    }
    else if (currentPage > 4) {
      list = [...list, currentPage - 2, currentPage - 1, currentPage, currentPage + 1, currentPage + 2]
    }
    else {
      list = [...list, 2, 3, 4, 5, 6]
    }
    list.push(totalPages)
  }

  return list
}

export const Convert_date_to_string = json_date => {
  let date = new Date(json_date);
  if (json_date === 'now') {
    date = new Date()
  }
  let year = date.getFullYear()
  let month = date.getMonth()
  let day = date.getDate()
  let hours = date.getHours()
  let minutes = date.getMinutes()
  let seconds = date.getSeconds()
  return `${day < 10 ? 0 : ''}${day}/${month < 9 ? 0 : ''}${month + 1}/${year} ${hours < 10 ? 0 : ''}${hours}:${minutes < 10 ? 0 : ''}${minutes}`
}

export const Compare_datetimes = (json_first_date, json_second_date) => {
  let firstDate = new Date(json_first_date);
  let secondDate = new Date(json_second_date);

  if (json_first_date === 'now')
    firstDate = new Date()
  if (json_second_date === 'now')
    secondDate = new Date()

  return firstDate > secondDate
}

export const dateAdd = (date, interval, units) => {
  if (!(date instanceof Date))
    return undefined;

  let ret = new Date(date);
  let checkRollover = function () { if (ret.getDate() != date.getDate()) ret.setDate(0); };
  switch (String(interval).toLowerCase()) {
    case 'year': ret.setFullYear(ret.getFullYear() + units); checkRollover(); break;
    case 'quarter': ret.setMonth(ret.getMonth() + 3 * units); checkRollover(); break;
    case 'month': ret.setMonth(ret.getMonth() + units); checkRollover(); break;
    case 'week': ret.setDate(ret.getDate() + 7 * units); break;
    case 'day': ret.setDate(ret.getDate() + units); break;
    case 'hour': ret.setTime(ret.getTime() + units * 3600000); break;
    case 'minute': ret.setTime(ret.getTime() + units * 60000); break;
    case 'second': ret.setTime(ret.getTime() + units * 1000); break;
    default: ret = undefined; break;
  }
  return ret;
}

export const Get_dateDiff_toString = (json_start_date, json_end_time, show_seconds = false, countdown = false) => {
  let startDate = new Date(json_start_date);
  let endDate = new Date(json_end_time);

  if (json_start_date === 'now')
    startDate = new Date()
  if (json_end_time === 'now')
    endDate = new Date()

  let delta = Math.abs(startDate - endDate) / 1000;
  let days = Math.floor(delta / 86400);
  delta -= days * 86400;
  let hours = Math.floor(delta / 3600) % 24;
  delta -= hours * 3600;
  let minutes = Math.floor(delta / 60) % 60;
  delta -= minutes * 60;
  let seconds = Math.floor(delta % 60);

  let diff = { days: null, hours: null, minutes: null, seconds: null }

  if (countdown && startDate > endDate)
    return null
  if (!countdown && startDate < endDate)
    return null
  // return (days < 10 ? "0" : "") + days + ":" + (hours < 10 ? "0" : "") + hours + ":" + (minutes < 10 ? "0" : "") + minutes + ":" + (seconds < 10 ? "0" : "") + seconds
  // let diff = ''
  // if (days !== 0)
  //   diff = days + ' '
  // if (hours !== 0 || days !== 0)
  //   diff += hours + ' '
  // if (minutes !== 0 || days !== 0 || hours !== 0)
  //   diff += minutes + ' '
  // if (show_seconds)
  //   diff += seconds + '


  if (days !== 0)
    diff.days = days
  if (hours !== 0 || days !== 0)
    diff.hours = hours
  if (minutes !== 0 || days !== 0 || hours !== 0)
    diff.minutes = minutes
  if (show_seconds)
    diff.seconds = seconds

  return diff
}

