import React, { useState, useContext } from 'react'
import { GlobalContext } from 'contexts/GlobalContext'

const Img = props => {

	const [isOpen, setOpen] = useState(false)
	const { setOpenedImage } = useContext(GlobalContext)

	const openImage = e => {
		e.preventDefault()
		setOpenedImage(props.src)
	}

	return (
		<div onClick={openImage} style={{ cursor: 'pointer' }}>
			<img style={{userSelect: 'none'}} {...props} />
		</div>
	)
}

export default Img