import React, { useState, useEffect } from 'react'
import styled, { css } from 'styled-components'
import { theme } from 'Themes'
import { MdArrowDownward, MdArrowDropUp, MdArrowDropDown } from 'react-icons/md'

const SContainer = styled.button `
  font-family: ${theme.font};
  font-size: 14px;
  border: none;
  outline: none;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 5px;
  width: 250px;
  height: 24px;
  background-color: var(--color_b2);
  color: var(--color_c2);

  ${props => props.input && css`
    /* color: #333; */
    flex-grow: 1;
    width: 100%;
    padding: 10px 15px;
    margin-bottom: 5px;
    font-size: 16px;
    
    border-radius: 5px;
    border: 4px solid var(--color_inputBackground);
    background-color: var(--color_inputBackground);
    outline: none;
    margin-top: 4px;
    height: 50px;
    width: 175px;
  `}
`

const SDropDown = styled.div `
  font-family: ${theme.font};
  font-size: 14px;
  position: absolute;
  top: 30px;
  left: 0;
  width: 100%;
  padding-top: 5px;
  padding-bottom: 5px;
  border-radius: 5px;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.4);
  background-color: var(--color_b2);
  overflow: hidden;
  z-index: 1;

  ${props => props.input && css`
    top: 50px;
    max-height: 200px;
    overflow: auto;
    background-color: var(--color_inputBackground);
`}
 
`

const SItem = styled.div `
  text-align: left;
  padding: 5px;

  &:hover {
    /* background-color: var(--color_inputFocusBackground); */
  }

  ${props => props.input && css`
    height: 40px;
    display: flex;
    align-items: center;
    font-size: 16px;
  `}
`

const ComboBox = props => {

  const [state, setState] = useState({
    current_item: {name: props.currentItem ? props.currentItem : ''},
    is_open: false,
  })

  useEffect(() => {
    setState({...state, current_item: {name: props.currentItem ? props.currentItem : ''},})
  }, [props.currentItem])

  const on_click = e => {
    e.preventDefault()
    if (e.target === e.currentTarget)
    setState({
      ...state,
      is_open: !state.is_open,
    })
  }

  const on_blur = e => {
    setState({
      ...state,
      is_open: false,
    })
  }

  const on_item_selected = item => e => {
    e.preventDefault()
    setState({
      ...state,
      current_item: {...state.current_item, name: item.name},
      is_open: false,
    })

    props.itemSelected(item)
  }

  return (
    <SContainer input={props.input} onClick={on_click} onBlur={on_blur}>
      {state.current_item.name}
      {state.is_open ? <MdArrowDropUp size="18" /> : <MdArrowDropDown size="18" />}
      {state.is_open && 
      <SDropDown input={props.input}>
        {props.items.map((item, index) => 
          <SItem input={props.input} key={index} onClick={on_item_selected(item)}>{item.name}</SItem>
        )}

      </SDropDown>
      }
    </SContainer>
  )
}

export default ComboBox