import React from 'react'
import styled, { css } from 'styled-components'
import Flex from 'controls/Flex'
import { theme } from 'Themes'

const StyledDiv = styled.div `
  position: relative;
  border: none;
  border-radius: ${props => props.borderRadius ? props.borderRadius : '10px'};
  /* box-shadow: 0px 2px 200px rgba(0, 0, 0, 0.3); */
  margin: auto;
  padding: ${props => props.padding ? props.padding : '30px'};
  padding-top: 15px;
  background-color: ${props => props.bg ? props.bg : '#f5f5f5'};
  background-color: var(--color_b2);
/* border: 3px solid #181818; */
box-shadow: 0 6px 16px 0 rgba(0,0,0,.2);
  width: ${props => props.width ? props.width : '500px'};
  max-width: ${props => props.maxWidth ? props.maxWidth : '100%'};
  min-height: ${props => props.minHeight ? props.minHeight : 'inherit'};
  overflow: hidden;
  box-sizing: border-box;

  ${props =>
    props.white &&
    css`
      box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.4);
      /* background-color: #ffffff; */
      border-radius: 0px;
    `
  };

  @media (max-width: ${theme.mobileSize}) {
    & {
      padding-left: 8px;
      padding-right: 8px;
      margin-top: -50px;
      max-width: 100%;
    }
  }
`

const Title = styled.div `
  color: ${theme.gray};
  color: ${theme.primary};
  color: ${theme.c2};
  color: var(--color_c2);
  font-family: ${theme.fontCaps};
  font-size: 18px;
  margin-top: -10px;
  margin-bottom: 25px;
  padding: 10px;
  width: 100%;
  border: none;
  text-align: center;
  border-bottom: 1px solid var(--color_b22);
  /* font-weight: 600; */
  /* letter-spacing: 1.1px; */
`

const Title_Right = styled.div `

  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */


  position: absolute;
  right: 5px;
  top: 2px;
  font-size: 14px;
  /* color: gray;

  cursor: pointer;

  &:hover {
    color: blue;
  } */
`

const SGray = styled.span `
  color: ${theme.secondary};
  font-style: normal;
  font-weight: 900;
  font-size: 20px;
`


const Container = (props) => {

  return (
    <StyledDiv padding={props.padding} white={props.white} minHeight={props.minHeight} borderRadius={props.borderRadius} maxWidth={props.maxWidth} width={props.width} bg={props.bg}>
      <Flex column>
        <Flex>
        {props.title &&
          <Title>
            {/* <SGray>{'<'}</SGray> */}
              {props.title}
            {/* <SGray>{' />'}</SGray> */}

            <Title_Right>
              {props.titleRight}
            </Title_Right>

          </Title>
        }
        
        </Flex>
        
        {props.children}
      </Flex>
    </StyledDiv>
  )
}

export default Container