import React, { useContext } from 'react'
import styled, { css } from 'styled-components'
import { GlobalContext } from 'contexts/GlobalContext'
import { theme } from 'Themes'
import Img from './Img'
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { atomDark } from 'react-syntax-highlighter/dist/esm/styles/prism';

const VideoDiv = styled.div `
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
`

const Iframe = styled.iframe `
  max-width: 100%;
`

const Imgs = styled(Img) `
  max-width: 600px;
  object-fit: scale-down;
  @media only screen and (max-width: ${theme.mobileSize}) {
    max-width: 100%;
  }

  ${props => props.small && css `
    width: 100%;
    border-radius: 4px;
  `}
`

const ImagesDiv = styled.div `
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
`

const B = styled.span `
  color: ${props => props.darkmode ? 'var(--color_highlight)' : 'var(--color_c2)'};
  font-weight: ${props => props.darkmode ? 'normal' : 'bold'};
`

const A = styled.a `
  text-decoration: none;
  font-style: italic;
  color: ${props => props.reverseLinkColor ? 'rgba(255, 255, 255, 0.7)' : 'var(--color_link)'};
`

const Aa = styled.a `
  text-decoration: none;
  font-style: italic;
  color: ${props => props.reverseLinkColor ? 'rgba(255, 255, 255, 0.7)' : 'var(--color_link)'};

  &:hover {
    text-decoration: underline;
  }
`

const JParser = props => {
  const { global } = useContext(GlobalContext);


  function validURL(str) {
    let pattern = new RegExp('^(https?:\\/\\/)?'+
      '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|'+
      '((\\d{1,3}\\.){3}\\d{1,3}))'+
      '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*'+
      '(\\?[;&a-z\\d%_.~+=-]*)?'+
      '(\\#[-a-z\\d_]*)?$','i');
    return !!pattern.test(str);
  } 


  return (
    <div style={{whiteSpace: 'pre-wrap', tabSize: '4'}}>
      {props.children.split('`').map((s, index) => !index%2 ? 
        s // s.split(' ').map((subS, index) => <>{validURL(subS)  ? <A reverseLinkColor={props.reverseLinkColor} href={!subS.match(/^[a-zA-Z]+:\/\//) ? 'http://' + subS : subS} target='_blank'>{subS}</A> : subS}{index < s.split(' ').length - 1 ? ' ' :''}</> )
      : 
        <span key={index}>
          {
          s[0] === '~'   ? <ImagesDiv><Imgs src={s.substring(1)} small={props.small} /></ImagesDiv>
          : s[0] === 'b' ? <B darkmode={global.mode === 'dark'}>{s.substring(1)}</B>
          : s[0] === 't' ? <h2 style={{margin: 0, padding: 0}}>{s.substring(1)}</h2>
          : s[0] === 'i' ? <i>{s.substring(1)}</i>
          : s[0] === 'v' ? <VideoDiv><Iframe width="560" height="315" src={`https://www.youtube.com/embed/${s.substring(1)}`} frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></Iframe></VideoDiv>
          : s[0] === 'l' ? <Aa target="_blank" href={s.substring(1).split('')[0]}>{s.substring(1).split('')[1]}</Aa>
          : s[0] === 'c' ? <SyntaxHighlighter language={s.substring(1).split('')[0]} style={atomDark}>{s.substring(1).split('')[1]}</SyntaxHighlighter>
          // : validURL(s)  ? <a href={s}>{s}</a> 
          : s
          }
        </span>
      )}
    </div>
  )
}

export default JParser