import React, { useState } from 'react'
import styled from 'styled-components'


const Main = styled.div`
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  width: 200px;
  background-color: red;
`

const Cover = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.3);
`







const Drawer = (props) => {

  const [isMousePressed, setMousePressed] = useState(false)
  const [mousePressedPos, setMousePressedPos] = useState({ x: 0, y: 0, margin: -190 })
  const [marginLeft, setMarginLeft] = useState(-190)

  const onMouseDown = e => {
    setMousePressed(true)
    setMousePressedPos({ x: e.screenX !== undefined ? e.screenX : e.targetTouches[0].pageX, y: e.screenY, margin: marginLeft })
  }

  const onMouseMove = e => {
    if (!isMousePressed) {
      return
    }

    let delta = (e.screenX !== undefined ? e.screenX : e.targetTouches[0].pageX) - mousePressedPos.x
    setMarginLeft(Math.max(-190, Math.min(mousePressedPos.margin + delta, 0)))
  }

  const onMouseUp = e => {
    setMousePressed(false)
    setMarginLeft(marginLeft < -80 ? -190 : 0)
  }

  const onClicked = e => {
    if(e.target !== e.currentTarget || marginLeft !== 0) {
      return
   }
    e.preventDefault()
    console.log("clicked")
    if (marginLeft === 0 && !isMousePressed)
      setMarginLeft(-190)
  }


  return <></>
  return (
    <Cover onClick={onClicked} onTouchEnd={onMouseUp} onMouseUp={onMouseUp} onTouchMove={onMouseMove} onMouseMove={onMouseMove}>
      <Main onMouseDown={onMouseDown} onTouchStart={onMouseDown} style={{ marginLeft: marginLeft, transition: isMousePressed ? '' : 'margin ease 200ms' }}>


      </Main>
    </Cover>
  )
}

export default Drawer