import React, { useState, useEffect, useContext, useRef } from 'react'
import styled, { css } from 'styled-components'
import { Link } from 'react-router-dom'
import Helmet from 'react-helmet'
import Table, { Th, Td, Table_A } from 'controls/Table'
import { UserContext } from 'contexts/UserContext'
import { MessageBoxContext } from 'contexts/MessageBoxContext'
import JN from 'JN'
import FolderEditDlg from './FolderEditDlg'
import A from 'controls/A'
import Button_Float from 'controls/Button_Float'
import { MdEdit, MdAdd, MdDelete, MdKeyboardArrowLeft, MdFolder, MdFolderOpen, MdVisibilityOff } from 'react-icons/md'
import { theme } from 'Themes'
import Input from 'controls/Input'
import { FaBook } from 'react-icons/fa'
import H3 from 'controls/H3'

const Main = styled.div`
  margin: auto;
  margin-top: -50px;
  width: 800px;
  max-width: 100%;
`

const FilterContainer = styled.div`
  position: absolute;
  top: 55px;
  background-color: var(--color_b2);
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  overflow: auto;
  max-height: 250px;
  width: ${props => props.width ? props.width : '100%'};
  z-index: 1;
`

const FilterItem = styled(Link)`
  display: block;
  ${props => props.flex && css`
    display: flex;
    justify-content: flex-start;  

    button {
      margin-left: auto;
    }
  `}

  text-decoration: none;
  color: var(--color_c2);
  font-family: ${theme.font};
  
  padding-left: 10px;
  padding-right: 10px;
  /* height: 50px; */
  line-height: 50px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  outline: none;

  &:hover, &:focus {
    color: var(--color_link);
  }
`

const TableDiv = styled.div`
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  ${props => props.subdirectoriessize === 0 && css`
    margin-top: -50px;
  `};
`

const Header = styled.div`
  overflow: hidden;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.2);
  background-color: var(--color_b2);
  border-radius: 8px;
  margin-bottom: 50px;
`

const ParentFoldersDiv = styled.div`
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */

  display: flex;
  text-align: left;
  /* padding: 25px; */
  margin-top: 5px;
  margin-right: 15px;
  height: 44px;
  width: 100%;
  overflow: auto;
  font-size: 16px;
`

const TagsContainer = styled.div`
  overflow: hidden;
  /* min-height: ${props => props.firstTime ? '112px' : '999px'}; */
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  /* border: 1px solid var(--color_inputFocusBorder); */

  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
`

const Tag = styled(Link)`
  text-decoration: none;
  color: var(--color_c2);
  background-color: var(--color_b2);
  padding-left: 25px;
  padding-right: 25px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border: 1px solid var(--color_b22);
  border-top: none;
  border-left: none;

  height: 56px;
  font-size: 16px;
  transition: color ease 250ms;
  
  &:hover {
    color: var(--color_link);
    /* background-color: rgba(0, 0, 0, 0.1); */
  }

  ${props => props.backbutton && css`
    color: var(--color_red);
    /* background-color: var(--color_red); */
    /* border: none; */
    /* max-width: 100px; */

    &:hover {
      color: var(--color_red);
      /* box-shadow: inset 0 0 28px rgba(0, 0, 0, .3); */
    }
  `}
`

const Tutorials = props => {
  const { match, history } = props;
  const { user } = useContext(UserContext);
  const { toast } = useContext(MessageBoxContext);
  const refLastParentItself = useRef(null);

  const [state, setState] = useState({
    tutorials: [],
    sub_directories: [],
    parent_directories: [],
    is_folderEditDlg_open: false,
  });

  useEffect(() => {
    JN.get(user, 'tutorial_tree', { id: match.params.id ? match.params.id : 1 },
      (data) => {
        if (state.destructed) {
          return;
        }

        setTimeout(() => { if (!refLastParentItself || !refLastParentItself.current) return; refLastParentItself.current.scrollTo(2000, 0, 0) });
        setState({ ...state, ...data });
      },
      (error) => {
        console.log(error);
      });

  }, [match.url])

  const [listLastAddedTutorials, setLastAddedTutorials] = useState([])
  useEffect(() => {
    JN.get(user, 'tutorial/last_added', {}, (data) => {
      setLastAddedTutorials(data.tutorials)
    })
  }, [])

  const onFolderEditDlg_close = () => {
    setState({ ...state, is_folderEditDlg_open: false });
  }

  const onAddFolder = e => {
    e.preventDefault();
    setState({ ...state, editingFolder: { id: -1, parent_dir_id: match.params.id ? match.params.id : 1 }, is_folderEditDlg_open: true });
  }

  const onEditFolder = e => {
    e.preventDefault();
    setState({ ...state, editingFolder: { id: match.params.id ? match.params.id : 1, name: state.name, parent_dir_id: state.parent_directories.length ? state.parent_directories[0].id : 1 }, is_folderEditDlg_open: true });
  }

  const onFolderSubmited = (isAdd, folder) => {

    if (isAdd) {
      setState({ ...state, sub_directories: [...state.sub_directories, folder], is_folderEditDlg_open: false });
    }
    else {
      setState({ ...state, name: folder.name, is_folderEditDlg_open: false });
    }
  }

  const onDeleteFolder = e => {
    e.preventDefault();
    if (!window.confirm('  ?!')) {
      return
    }

    JN.delete(user, 'admin/tutorial_tree', { id: match.params.id ? match.params.id : 1 },
      () => {
        toast(' ', 'success');
        history.push(`/tutorials/${state.parent_directories.length ? state.parent_directories[state.parent_directories.length - 1].id : 1}`);
      },
      error => {
        if (error.status === 400)
          toast('   ', 'error');
        else if (error.status === 403)
          toast('   ', 'error');
      })
  }

  const [filter, setFilter] = useState('');
  const [filteredItems, setFilteredItems] = useState([]);
  const refFilteredProblems = [useRef(null), useRef(null), useRef(null), useRef(null), useRef(null)];

  const onFilterChanged = e => {
    let filterText = e.target.value;

    if (filterText.length > 50)
      return;

    setFilter(filterText)
    if (filterText === '') {
      setFilteredItems([]);
      return;
    }

    JN.get(user, `tutorial/search`, { title: filterText },
      (data) => {
        setFilteredItems(data.tutorials.slice(0, 5));
      },
      () => setFilteredItems([]));
  }

  const onHideFilter = (e) => {
    if (e.currentTarget.contains(e.relatedTarget))
      return;

    setFilteredItems([]);

    if (filter !== '') {
      setFilter('');
    }
  }

  const onKeyDown = e => {
    if (!filteredItems.length)
      return

    let ARROW_UP = 38;
    let ARROW_DOWN = 40;
    if (e.keyCode !== ARROW_UP && e.keyCode !== ARROW_DOWN)
      return;

    e.preventDefault();

    let up = e.keyCode === ARROW_UP;

    let activeElement = document.activeElement;
    let nextIndex = 0;

    let i = refFilteredProblems.findIndex(ref => ref.current === activeElement);

    if (i === -1 || i === 0) {
      nextIndex = up ? filteredItems.length - 1 : i + 1;
    }
    else if (i === filteredItems.length - 1) {
      nextIndex = up ? i - 1 : 0;
    }
    else {
      nextIndex = up ? i - 1 : i + 1;
    }

    if (refFilteredProblems[nextIndex] && refFilteredProblems[nextIndex].current) {
      refFilteredProblems[nextIndex].current.focus();
    }
  }

  return (
    <Main>
      <Helmet>
        <meta charSet="utf-8" />
        <title>{!match.params.id || match.params.id == 1 || state.name === 'tutorials' ? '' : state.name}</title>
        <meta name="description" content="AiaSoft" />
      </Helmet>

      <div style={{ display: 'flex', justifyContent: 'space-between' }}>
        {state.parent_directories &&
          <ParentFoldersDiv ref={refLastParentItself}>
            {state.parent_directories.map((folder, index) =>
              <span style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }} key={index}>
                <A style={{ display: 'flex', whiteSpace: 'nowrap', justifyContent: 'center', alignItems: 'center' }} to={`/tutorials/${folder.id}`} hover="true">
                  <MdFolder size="16" color="var(--color_c2)" />&nbsp;<span style={{ whiteSpace: 'nowrap' }}>{!index ? '' : folder.name}</span>
                </A>
                &nbsp;/&nbsp;
              </span>
            )}
            {state.name && state.name !== '' && <span style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', cursor: 'default', color: 'var(--color_c3)' }}><MdFolder size="16" />&nbsp;<span style={{ whiteSpace: 'nowrap' }}>{state.name === 'tutorials' ? '' : state.name}</span></span>}
          </ParentFoldersDiv>
        }
        <div onBlur={onHideFilter} style={{ display: 'flex', justifyContent: 'flex-end', marginLeft: 'auto', marginBottom: 45, width: '250px', minWidth: '250px' }}>
          {/* {state.parent_directories.length > 0 &&
          <Tag backbutton="true" to={`/tutorials/${state.parent_directories.length ? state.parent_directories[state.parent_directories.length - 1].id : 1}`}>
            <MdKeyboardArrowLeft size="24" />{` `}
          </Tag>
        } */}

          <Input reverse onKeyDown={onKeyDown} fontSize="14px" onChange={onFilterChanged} value={filter} placeholder="..">
            {filter !== '' &&
              <FilterContainer onKeyDown={onKeyDown}>
                {!filteredItems.length ?
                  <div>

                  </div>
                  :
                  <>
                    {filteredItems.map((tutorial, index) =>
                      <FilterItem flex="true" ref={refFilteredProblems[index]} key={tutorial.id} to={`/tutorial/${tutorial.id}`}>
                        <span style={{ overflow: 'hidden', textOverflow: 'ellipsis' }}>{tutorial.title}</span>
                      </FilterItem>
                    )}
                  </>
                }
              </FilterContainer>
            }
          </Input>
        </div>
      </div>

      {user && user.permission !== 'user' &&
        <>
          <Button_Float full="true" count="4" to={`/addtutorial/${match.params.id ? match.params.id : 1}`}><MdAdd color="var(--color_green)" size="22" />&nbsp; </Button_Float>
          <Button_Float full="true" count="3" onClick={onAddFolder}><MdAdd color="var(--color_green)" size="22" />&nbsp; </Button_Float>
          <Button_Float full="true" count="2" onClick={onEditFolder}><MdEdit color="var(--color_orange)" size="20" />&nbsp; </Button_Float>
          <Button_Float full="true" onClick={onDeleteFolder}><MdDelete color="var(--color_red)" size="20" />&nbsp; </Button_Float>
        </>
      }

      <Header>
        <TagsContainer>
          {state.sub_directories && state.sub_directories.map((folder, index) =>
            <Tag key={folder.id} to={`/tutorials/${folder.id}`}>
              <MdFolder color="var(--color_yellow)" size="24" />&nbsp;<span style={{ whiteSpace: 'nowrap', overflow: 'hidden', textOverflow: 'ellipsis' }}>{folder.name}</span>
            </Tag>
          )}
        </TagsContainer>

        {/* {state.parent_directories && state.parent_directories.length > 0 &&
          <TagsFooter>
            {state.parent_directories.map((folder, index) =>
              <span key={index}><A to={`/tutorials/${folder.id}`} hover="true">{!index ? folder.id : folder.name} </A> /&nbsp;</span>
            )}
            <span style={{cursor: 'default', color: 'var(--color_c3)'}}>{state.name}</span>
          </TagsFooter>
        } */}
      </Header>

      {/*  */}
      {state.tutorials.length > 0 && (state.parent_directories.length > 0 || state.sub_directories.length > 0) &&
        <TableDiv subdirectoriessize={state.sub_directories.length}>
          <Table style={{}}>
            <thead></thead>
            <tbody>
              {state.tutorials.map((tutorial) =>
                <tr key={tutorial.id}>
                  {/* <Td>{tutorial.id}</Td> */}
                  <Td style={{ display: 'flex', height: 50, alignItems: 'center' }} ><FaBook />&nbsp;<Table_A to={`/tutorial/${tutorial.id}`}>{tutorial.title}</Table_A>{!tutorial.is_visible && <span style={{ display: 'flex', alignItems: 'center' }}><MdVisibilityOff size="20" color="var(--color_c3)" /></span>}</Td>
                </tr>
              )}
            </tbody>
          </Table>
        </TableDiv>
      }


      {/*   */}
      {listLastAddedTutorials.length > 0 && <>
        <H3 style={{ textAlign: 'left', marginTop: 80 }}> </H3>
        <TableDiv subdirectoriessize={1}>
          <Table>
            <thead></thead>
            <tbody>
              {listLastAddedTutorials.map((tutorial) =>
                <tr key={tutorial.id}>
                  {/* <Td>{tutorial.id}</Td> */}
                  <Td style={{ display: 'flex', height: 50, alignItems: 'center' }} ><FaBook />&nbsp;<Table_A to={`/tutorial/${tutorial.id}`}>{tutorial.title}</Table_A>{!tutorial.is_visible && <span style={{ display: 'flex', alignItems: 'center' }}><MdVisibilityOff size="20" color="var(--color_c3)" /></span>}</Td>
                </tr>
              )}
            </tbody>
          </Table>
        </TableDiv>
      </>}


      {state.is_folderEditDlg_open && <FolderEditDlg submit={onFolderSubmited} folder={state.editingFolder} isOpen={state.is_folderEditDlg_open} close={onFolderEditDlg_close} />}
    </Main>
  )
}

export default Tutorials