import React, { useState, useEffect, useContext, useRef } from 'react'
import Container from 'controls/Container'
import JN from 'JN'
import Input from 'controls/Input';
import Button from 'controls/Button';
import ContainerFooter from 'controls/ContainersFooter';
import styled from 'styled-components'
import TextArea from 'controls/TextArea';
import { theme } from 'Themes'
import CategoryEditDlg from './tags/CategoryEditDlg';
import { MdAdd, MdEdit, MdDelete, MdCheckBox, MdCheckBoxOutlineBlank, MdRemoveRedEye, MdCheck } from 'react-icons/md';
import SamplesPage from './samples/SamplesPage';
import { Redirect } from 'react-router-dom'
import { UserContext } from 'contexts/UserContext'
import Button_Float from 'controls/Button_Float';
import Checkbox from 'controls/Checkbox'
import Checker_Add from './Checker_Add';
import { MessageBoxContext } from 'contexts/MessageBoxContext'
import Grid from 'controls/Grid';
import { ScrollTo } from 'Global_Functions'
import LoadingSvg from 'components/LoadingSvg';
import CoAutors from './CoAuthors';

const RButton = styled.button `
  border: none;
  background: none;
  font-size: 14px;
  font-family: ${theme.font};
  color: var(--color_c3);
  text-decoration: underline;
  cursor: pointer;
  height: 28px;
`

const Header = styled.div `
  text-align: left;
  margin: auto;
  margin-top: 2em;
  margin-bottom: 8px;
  border-bottom: none;
  width: 100%;
  overflow: hidden;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  display: flex;
  justify-content: space-between;
`

const DivTags = styled.div `
  display: flex;
  text-align: left;
  flex-direction: column;
  width: 100%;
  height: 430px;
  overflow: auto;
  margin-bottom: 20px;
  border: 1px solid var(--color_b22);
  border-radius: 8px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
`

const DivTag = styled.div `
  font-size: 14px;
  display: flex;
  padding: 3px;
  padding-left: 10px;
  align-items: center;
  background-color: transparent;
  border-bottom: 1px solid var(--color_b22);

  &, * {
    cursor: pointer;
  }

  &:last-child {
    /* border-bottom: none; */
  }

  label {
    flex-grow: 1;
    margin-left: .5em;
  }

  &:hover {
    transition: background-color ease 250ms;
    background-color: var(--color_b11);

    button {
      transition: opacity ease 250ms;
      opacity: 1;
    }
  }
`
const StyledButton = styled.button `
  border: none;
  outline: none;
  background: none;
  cursor: pointer;
  opacity: 0;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  margin-left: 3px;

  /* &:hover {
    transition: background-color ease 250ms;
    background-color: white;
  } */
`





const StyledForm = styled.div `
  /* animation: .3s ease-in-out both fade-in; */
  @keyframes fade-in {
    0% {
      opacity: 0;
      /* transform: scale(0); */
    }
    
    100% {
      opacity: 1;
      /* transform: scale(1); */
    }
  }
`

const Tab_Container = styled.div `
  display: flex;
`

const Tab_Action = styled.button `
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */

  cursor: pointer;
  border: none;
  outline: none;
  text-decoration: none;
  font-size: 14px;
  font-family: ${theme.fontCaps};
  border: 1px solid var(--color_inputBackground);
  padding: .8em 1.5em;
  outline: none;
  height: 50px;
  border-top-left-radius: 5px;
  border-radius: 5px;
  margin-right: 5px;
  margin-bottom: 50px;
  color: var(--color_c2);
  background-color: transparent;
  cursor: pointer;

  &:disabled {
    background: none;
    color: var(--color_c3);
    border: 1px solid transparent;
  }
`

const AddProblem = (props) => {
  const { match, history } = props
  const refScrollTo  = useRef(null)
  const refStatement = useRef(null)
  const { user }  = useContext(UserContext)
  const { toast } = useContext(MessageBoxContext)

  const [tab, setTab] = useState(1)
  
  const [state, setState] = useState({
    problem: {
      title: '',
      time_limit: '1',
      memory_limit: '256',
      input_format : 'std',
      output_format: 'std',
      statement: '',
      input_description: '',
      output_description: '',
      samples: [],
      problem_comment: '',
      note: '',
      tags: [],
      is_visible: false,
      source: '',
    },
    // tags: [{id: 1, title: 'DFS'}, {id: 2, title: 'BFS'}, {id: 3, title: ' '}, {id: 4, title: ''}, {id: 5, title: 'DFS'}, {id: 7, title: 'BFS'}, {id: 8, title: ' '}, {id: 9, title: ''}, {id: 10, title: 'DFS'}, {id: 12, title: 'BFS'}, {id: 13, title: ' '}, {id: 14, title: ''}],
    tags: [],
    isLoading: false,
    deleting_tag_id: -1,
    isOpen: false,
    editingCategory: null,
  });

   // 
   const updateProblem = (redirect_to_fullProblem = false) => {
    // 
    if (state.problem.id === undefined) {
          
      setState({
        ...state,
        isLoading: true
      })

      JN.post(user, `admin/problem`, {...state.problem, problem_tags: state.problem.tags},
      (data) => {
        toast(' ', 'success')

        setState({
          ...state,
          isLoading: false,
          problem: {
            ...state.problem,
            id: data.problem_id,
            username: user.username,
            coauthors: []
          }
        })
        setTab(2)
      },
      () => {
        setState({...state, isLoading: false})
      })
    }

    // 
    else {
      setState({
        ...state,
        isLoading: true
      })

      JN.put(user, `admin/problem`, {...state.problem, problem_tags: state.problem.tags},
      () => {
        if (redirect_to_fullProblem) {
          history.push(`/problem/${state.problem.id}`);
          return;
        }
        toast(' ', 'success')

        setState({
          ...state,
          isLoading: false
        })
      },
      () => {
        setState({
          ...state,
          isLoading: false
        })
      })
    }
   }

   const submit = e => {
     if (e)
      e.preventDefault()

    updateProblem();
  }

  useEffect(() => {
    if (!user) {
      return
    }

    ScrollTo(refScrollTo)
  
    // 
    let list //#???      
    JN.get(user, 'tags', {},
    (data) => {
      setState({
        ...state,
        tags: data,
        isLoading: false,
      })
      list = data 
    },
    () => {
      setState({
        ...state,
        isLoading: false,
      })
    })

    //   
    if (match.params.id !== undefined) {
      document.title = ` `;
      JN.get(user, `admin/problem/${match.params.id}`, {},
      (data) => {
        let fList = []
        for (let i=0; i<data.tags.length; i++) {
          fList.push(data.tags[i].id)
        }
        setState({
          ...state,
          problem: {
            ...data,
            note: data.note ? data.note : '',
            source: data.source ? data.source : '',
            tags: fList
          },
          tags: list
        })
      })
    }
    else {
      document.title = ` `;
    }
  }, [])

  //state- 
  const change = (e) => {
    setState({
      ...state,
      problem: {
        ...state.problem,
        [e.target.name]: e.target.value
      }
    })
  }
  
  // 
  const onTagToggled = id => e => {
    e.preventDefault()
    // 
    if (!state.problem.tags.includes(id)) {
      setState({
        ...state,
        problem: {
          ...state.problem,
          tags: [...state.problem.tags, id]
        }
      })
    }
    // 
    else {
      setState({
        ...state,
        problem: {
          ...state.problem,
          tags: state.problem.tags.filter((tagId) => tagId !== id)
        }
      })
    }
  }

  const on_close_tagsEditDlg = () => {
    setState({
      ...state,
      isOpen: false,
    })
  }

  const on_open_tagsEditDlg = (e) => {
    e.preventDefault()
    setState({
      ...state,
      editingCategory: null,
      isOpen: true,
    })
  }

  const on_tag_edit = index => e => {
    e.preventDefault()
    e.stopPropagation()
    setState({
      ...state,
      editingCategory: state.tags[index],
      isOpen: true
    })
  }

  // 
  const on_tag_delete = id => e => {
    e.preventDefault()
    e.stopPropagation()

    if (!window.confirm('  ?!')) {
      return
    }

    setState({
      ...state,
      deleting_tag_id: id,
    })

    JN.delete(user, `admin/tags`, {tag_id: id},
    () => {
      toast(' ', 'success')
      setState({
        ...state,
        problem: {
          ...state.problem,
          tags: state.problem.tags.filter((tagId) => tagId !== id),
        },
        tags: state.tags.filter((tag, i) => tag.id !== id),
        deleting_tag_id: -1
      })
    },
    () => {
      setState({
        ...state,
        deleting_tag_id: -1
      })
    })
  }


  const on_visible_changed = () => {
    setState({
      ...state,
      problem: {
        ...state.problem,
        is_visible: !state.problem.is_visible,
      }
    })
  }

  const loadingColor = theme.primary


  const [search, setSearch] = useState('')
  const onSearchChanged = e => {
    setSearch(e.target.value)
  }

  const [isRejudging, setIsRejudging] = useState(false);
  const onResubmissionAllSubmissions = (e) => {
    e.preventDefault();
    if (!window.confirm('        ?')) {
      return
    }

    setIsRejudging(true);
    JN.post(user, 'rejudge', {problem_id: state.problem.id},
    () => {
      toast(' ', 'success');
      setIsRejudging(false);
    },
    (error) => {
      console.log(error);
      toast('', 'error');
      setIsRejudging(false);
    });
  }

  let currentTab = <>
    {state.isOpen && <CategoryEditDlg
      state={state}
      setState={setState}
      category={state.editingCategory}
      isOpen={state.isOpen}
      close={on_close_tagsEditDlg}
    />}
    <StyledForm style={{display: 'flex', flexDirection: 'column'}}>
      <Input    title="" focus required name="title"  value={state.problem.title} onChange={change} />
  
      <TextArea showControls title="" height="250px" name="statement" value={state.problem.statement} onChange={change} required/>
      <TextArea showControls title=" " height="150px" name="input_description" value={state.problem.input_description} onChange={change} />
      <TextArea showControls title=" " height="150px" name="output_description" value={state.problem.output_description} onChange={change} />
      <TextArea showControls title="" name="problem_comment" height="150px" value={state.problem.problem_comment} onChange={change} />
      <TextArea showControls title=" ()" height="150px" name="note" value={state.problem.note} onChange={change} />
      <Input    showControls title="" name="source" value={state.problem.source} onChange={change} />

      <Header>
        <Button type="button" onClick={on_open_tagsEditDlg}>
          <MdAdd size="22" />  
        </Button>
        <Input fontSize="14px" value={search} onChange={onSearchChanged} placeholder=".." width="250px" />
      </Header>
      <DivTags>
        {state.tags && <>
        {state.tags.map((tag, index) =>
          <span key={tag.id}>
          {tag.title.includes(search) &&
            <DivTag onClick={onTagToggled(tag.id)}>
              {state.deleting_tag_id === tag.id ? 
                <svg width="56" height="18" viewBox="0 0 120 30" xmlns="http://www.w3.org/2000/svg" fill="#fff"> <circle fill={loadingColor} cx="15" cy="15" r="15"> <animate attributeName="r" from="15" to="15" begin="0s" dur="0.8s" values="15;9;15" calcMode="linear" repeatCount="indefinite" /> <animate attributeName="fill-opacity" from="1" to="1" begin="0s" dur="0.8s" values="1;.5;1" calcMode="linear" repeatCount="indefinite" /> </circle> <circle fill={loadingColor} cx="60" cy="15" r="9" fillOpacity="0.3"> <animate attributeName="r" from="9" to="9" begin="0s" dur="0.8s" values="9;15;9" calcMode="linear" repeatCount="indefinite" /> <animate attributeName="fill-opacity" from="0.5" to="0.5" begin="0s" dur="0.8s" values=".5;1;.5" calcMode="linear" repeatCount="indefinite" /> </circle> <circle fill={loadingColor} cx="105" cy="15" r="15"> <animate attributeName="r" from="15" to="15" begin="0s" dur="0.8s" values="15;9;15" calcMode="linear" repeatCount="indefinite" /> <animate attributeName="fill-opacity" from="1" to="1" begin="0s" dur="0.8s" values="1;.5;1" calcMode="linear" repeatCount="indefinite" /> </circle> </svg>
              :
              state.problem.tags.includes(tag.id) ? <MdCheck size="20" color="var(--color_primary)" /> : <MdCheckBoxOutlineBlank size="20" color="var(--color_c3)" />}
              {state.deleting_tag_id !== tag.id && <label>{tag.title}</label>}
              <StyledButton type="button" onClick={on_tag_edit(index)}>
                {state.deleting_tag_id !== tag.id && <MdEdit color="var(--color_orange)" size="20" />}
              </StyledButton>
              <StyledButton type="button" onClick={on_tag_delete(tag.id)}>
                {state.deleting_tag_id !== tag.id && <MdDelete color="var(--color_red)" size="20" />}
              </StyledButton>
            </DivTag>}
          </span>
        )}
        </>}
      </DivTags>

      <Grid columns="2" gap="15px">
        <Input required list="time_limit" name="time_limit" title="  ()" value={state.problem.time_limit} onChange={change} />
          <datalist id="time_limit">
            <option value="7" />
            <option value="5" />
            <option value="4" />
            <option value="3" />
            <option value="2" />
            <option value="1" />
          </datalist>
        <Input required autocomplete="off" list="memory_limit" name="memory_limit" title="  ()" value={state.problem.memory_limit} onChange={change} />
        <datalist id="memory_limit">
          <option value="512" />
          <option value="256" />
          <option value="128" />
          <option value="64" />
          <option value="32" />
          <option value="16" />
        </datalist>
      </Grid>

      {/* <div>
        <input id="chck" checked={state.problem.input_format !== "std"} onChange={onCheckChanged} type="checkbox" />
        <label htmlFor="chck"> </label>
      </div>
      {state.problem.input_format !== "std" && 
        <Flex>
          <Input required name="input_format" title="  " value={state.problem.input_format} onChange={change} required={state.problem.input_format !== "std"} />
          <Input required name="output_format" title="  " value={state.problem.output_format} onChange={change} required={state.problem.output_format !== "std"} />
        </Flex>
      } */}

      

      <ContainerFooter>

      <div style={{width: '100%', marginTop: '-25px', marginBottom: 25, display: 'flex', justifyContent: 'space-between'}}>

        {user && user.username === 'system' && state.problem.id ?
          <div style={{display: 'flex', alignItems: 'center'}}>
            <RButton disabled={isRejudging} onClick={onResubmissionAllSubmissions}>  </RButton>
            {isRejudging && <LoadingSvg style={{marginLeft: 15}} dots />}
          </div>
        : <div></div>}

        <Checkbox title=" " checked={state.problem.is_visible} onCheckedChanged={on_visible_changed} />
      </div>

        <Button onClick={submit} type="submit" backgroundColor="primary" disabled={state.isLoading}>
          {state.isLoading ? ".." : state.problem.id ? "" : ""}
        </Button>
      </ContainerFooter>
    </StyledForm>
  </>

  if (tab === 2) {
    currentTab = <SamplesPage id={state.problem.id} />
  }
  else if (tab === 3) {
    currentTab = <Checker_Add problemId={state.problem.id} />
  }
  else if (tab === 4) {
    currentTab = <CoAutors updateCoauthors={(list) => setState({...state, problem: {...state.problem, coauthors: list}})} listCoauthors={state.problem.coauthors} problemId={state.problem.id} />
  }

  const on_tab_changed = t => e => {
    e.preventDefault()
    setTab(t)
  }

  const Tab_Current = {backgroundColor: 'var(--color_inputBackground)', color: 'color(--color_c2)'}

  if (!user || user.permission === "user") {
    return (
      <Redirect to="/404" />
    )
  }

  const viewFullProblemClicked = e => {
    e.preventDefault();
    updateProblem(true);
  }

  return (
    <>
    <div ref={refScrollTo} style={{height: '1px', marginBottom: '25px'}}></div>
    <Container title={match.params.id !== undefined ? ' ' : ' '} width="800px">
      {state.problem.id && 
        <Button_Float onClick={viewFullProblemClicked} count="1" to={`/problem/${state.problem.id}`}>
          <MdRemoveRedEye color="var(--color_primary)" size="22" />
        </Button_Float>
      }
      <Tab_Container>
        <Tab_Action style={tab === 1 ? Tab_Current : {}} onClick={on_tab_changed(1)}></Tab_Action>
        <Tab_Action style={tab === 2 ? Tab_Current : {}} disabled={!state.problem.id} onClick={on_tab_changed(2)}></Tab_Action>
        <Tab_Action style={tab === 3 ? Tab_Current : {}} disabled={!state.problem.id} onClick={on_tab_changed(3)}></Tab_Action>
        <Tab_Action style={tab === 4 ? Tab_Current : {}} disabled={!state.problem.id || state.problem.username !== user.username} onClick={on_tab_changed(4)}></Tab_Action>
      </Tab_Container>
      <div style={{marginTop: '8px'}}>
        {currentTab}
      </div>
    </Container>
    </>
  )
}

export default AddProblem
