import React from 'react'
import styled from 'styled-components'
import Container from '../controls/Container'
import { theme } from 'Themes'

const Styled_Container = styled.div `
  overflow: hidden;
  z-index: 999;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
  /* background-color: rgba(255, 255, 255, 0.8); */
  /* background-color: transparent; */
  animation: .2s ease-in-out both fade-in;
  @keyframes fade-in {
    0% {
      opacity: 0;
    }
    
    100% {
      opacity: 1;
    }
  }
`
const Styled_Content = styled.div `
  overflow: hidden;
  width: ${props => props.width ? props.width : "500px"};
  /* box-shadow: 2px 5px 10px rgba(0, 0, 0, 0.3); */
  /* background-color: rgba(255, 255, 255, 0.4); */
  border-radius: 10px;
  animation: .2s cubic-bezier(0.25, 0.46, 0.45, 0.94) both a_scale;
  @keyframes a_scale {
    0% {
      opacity: 0;
      transform: scale(0.8);
    }
    
    100% {
      opacity: 1;
      transform: scale(1);
    }
  }

  @media (max-width: ${theme.mobileSize}) {
    & {
      max-width: 100%;
    }
  }
`
const Modal = (props) => {
  const onClose = (e) => {
    if (e.target === e.currentTarget) {
      props.close()
      // props.setOpen(false)
    }
  }

  if (props.isOpen) {
    return (
      <Styled_Container onClick={onClose}>
        <Styled_Content width={props.width}>
          <Container width={props.width} title={props.title} modal>
            {props.children}
          </Container>
        </Styled_Content>
      </Styled_Container>
    )
  }
  else {
    return null
  }
}

export default Modal