import React, { useState, useEffect, useContext } from 'react'
import Input from 'controls/Input'
import Button from 'controls/Button'
import Grid from 'controls/Grid'
import ContainerFooter from 'controls/ContainersFooter'
import { theme } from 'Themes'
import Chart from 'react-apexcharts'
import { UserContext } from 'contexts/UserContext'
import { GlobalContext } from 'contexts/GlobalContext'
import Toggle from 'controls/Toggle'
import styled from 'styled-components'
import JN from 'JN'
import A from 'controls/A'
import PasswordEditDlg from './dialogs/passwordEditDlg'
import { FaComment, FaCircle } from 'react-icons/fa'

const SForm = styled.form`
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 25px;
  padding-top: 25px;
  box-shadow: 0 6px 16px 0 rgba(0,0,0,.2);
  margin: auto;
  background-color: var(--color_b2);
  border-radius: 8px;
`

const FooterDiv = styled.div`
  display: flex;
  justify-content: space-between;

  @media (max-width: ${theme.mobileSize}) {
    & {
      flex-direction: column;
    }
  }
`

const ChatButton = styled.button`
  font-family: ${theme.font};
  display: flex;
  align-items: center;
  border: none;
  outline: none;
  background: none;
  cursor: pointer;
  color: var(--color_c3);

  &:hover {
    transition: color ease 250ms;
    color: var(--color_c1);
  }
`

const Info = props => {
  const { user, userUpdate, refreshGlobalState } = useContext(UserContext);
  const { openChatRoom } = useContext(GlobalContext);

  const [state, setState] = useState({
    user: props.user,
    is_current_user: !user ? false : user.username === props.user.username,
    is_uploading: false,
    is_passwordEditDlg_open: false,
    is_friend: false,
  });

  const on_text_changed = e => {
    if (!state.is_current_user) {
      return;
    }

    setState({
      ...state,
      user: {
        ...state.user,
        [e.target.name]: e.target.value,
      }
    })
  }

  const on_submit = e => {
    e.preventDefault()

    setState({
      ...state,
      is_uploading: true,
    })

    JN.put(user, `profile/${state.user.username}`, {
      name: state.user.name,
      surname: state.user.surname,
      email: state.user.email,
    },
      () => {
        userUpdate({
          name: state.user.name,
          surname: state.user.surname,
          email: state.user.email,
        })
        props.userUpdate(state.user)
        setState({
          ...state,
          is_uploading: false,
        })
      },
      () => {
        setState({
          ...state,
          is_uploading: false,
        })
      })
  }

  const options = {
    labels: ['', ''],
    legend: {
      show: false,
      // position: 'bottom',
      // fontFamily: theme.font,
      // fontSize: '16px',
    },
    dataLabels: { enabled: true },
    colors: [theme.primary, theme.red],

    // chart: {
    //   events: {
    //     click: function (event, chartContext, config) {
    //       // console.log(event, chartContext)
    //       console.log(config)
    //       // The last parameter config contains additional information like `seriesIndex` and `dataPointIndex` for cartesian charts
    //     },
    //   }
    // }

  }

  const on_toggle_toggled = () => {

    const newPermission = state.user.permission === 'moderator' ? 'user' : 'moderator'
    setState({
      ...state,
      user: {
        ...state.user,
        permission: newPermission,
      }
    })

    JN.put(user, `admin/${state.user.username}/permission`, { new_permission: newPermission },
      () => {
        props.userUpdate({
          ...props.user,
          permission: newPermission,
        })
      })
  }

  const on_passwordEditDlg_close = () => {
    setState({
      ...state,
      is_passwordEditDlg_open: false,
    })
  }

  const on_changePassword_click = e => {
    e.preventDefault()
    setState({
      ...state,
      is_passwordEditDlg_open: true,
    })
  }

  const on_friends_toggled = () => {
    let isFriend = state.user.is_friend
    setState({ ...state, user: { ...state.user, is_friend: !isFriend } })
    if (isFriend) {
      JN.delete(user, 'user/friends', { friend_username: props.user.username }, null,
        () => {
          setState({ ...state, user: { ...state.user, is_friend: isFriend } })
        })
    }
    else {
      JN.post(user, 'user/friends', { friend_username: props.user.username }, null,
        () => {
          setState({ ...state, user: { ...state.user, is_friend: isFriend } })
        })
    }
  }

  //  
  const openChat = username => {

    JN.post(user, 'chat/private_room', { user1: user.username, user2: username },
      (data) => {
        openChatRoom(data.room, username)
      },
      (error) => {
        openChatRoom(error.data.room, username)
      });
  }

  return (
    <>
      <SForm onSubmit={on_submit}>

        <div>
          <div style={{ position: 'relative', width: '100%', display: 'flex', justifyContent: 'center' }}>

            {/*     */}
            {state.user.online &&
              <div style={{ position: 'absolute', left: -8, top: 2, pointerEvents: 'none' }}>
                <ChatButton><FaCircle size='12' color="var(--color_primary)" />&nbsp;</ChatButton>
              </div>
            }

            {/*  */}
            {user && state.user.username !== user.username &&
              <div style={{ position: 'absolute', right: 0 }}>
                <ChatButton onClick={(e) => { e.preventDefault(); openChat(state.user.username) }}>&nbsp;<FaComment size='20' /></ChatButton>
              </div>
            }

            {/*  */}
            {props.user.attempt > 0 ? <div style={{ textAlign: 'left', margin: 'auto' }}>
              <Chart
                options={options}
                series={[props.user.accepted, props.user.attempt - props.user.accepted]}
                type="pie"
                width="200"
              />
            </div>
              :
              <div style={{ marginTop: 50 }}>

              </div>
            }
          </div>

          {/* <Grid columns="2" gap="5px"> */}
          <Input required disabled={!state.is_current_user} name="name" title="" value={state.user.name} onChange={on_text_changed} />
          <Input required disabled={!state.is_current_user} name="surname" title="" value={state.user.surname} onChange={on_text_changed} />
          {/* </Grid> */}
          {state.user.email && <Input disabled name="email" title="" value={state.user.email} onChange={on_text_changed} />}
          <ContainerFooter>
            {state.is_current_user &&
              <FooterDiv>
                <Button type="button" flat onClick={on_changePassword_click}> </Button>
                <Button type="submit" disabled={state.is_uploading}>{state.is_uploading ? '..' : ''}</Button>
              </FooterDiv>
            }
          </ContainerFooter>
        </div>
        {/* <Grid columns="3" gap="5px">
        <Input bold color={theme.green} disabled={true} name="email" title="" value={user.accepted} />
        <Input bold color={theme.red} disabled={true} name="email" title="" value={user.attempt - user.accepted} />
        <Input bold color={theme.gray} disabled={true} name="email" title="" value={user.attempt} />
      </Grid> */}
        {user && user.username !== state.user.username &&
          <div style={{ marginBottom: '25px', width: '100%', display: 'flex', justifyContent: 'space-between' }}>
            {(user.permission === 'admin' && user.username !== state.user.username) ?
              <Toggle
                right
                width="135px"
                title=""
                toggled={state.user.permission === 'moderator'}
                setToggled={on_toggle_toggled}
              /> : <span></span>}

            <Toggle
              right
              width="130px"
              title=""
              toggled={state.user.is_friend}
              setToggled={on_friends_toggled}
            />
          </div>
        }
      </SForm>
      {state.is_passwordEditDlg_open && <PasswordEditDlg isOpen={state.is_passwordEditDlg_open} close={on_passwordEditDlg_close} />}
    </>
  )
}

export default Info