import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import AceEditor from 'react-ace'
import { theme } from 'Themes'

import 'brace/mode/python'
import 'brace/mode/c_cpp'
import 'brace/mode/pascal'
import 'brace/mode/json'
import 'brace/mode/css'
import 'brace/mode/javascript'
import 'brace/mode/java'
import 'brace/mode/csharp'

import 'brace/theme/ambiance'
import 'brace/theme/chaos'
import 'brace/theme/chrome'
import 'brace/theme/clouds'
import 'brace/theme/clouds_midnight'
import 'brace/theme/cobalt'
import 'brace/theme/crimson_editor'
import 'brace/theme/dawn'
import 'brace/theme/dracula'
import 'brace/theme/dreamweaver'
import 'brace/theme/eclipse'
import 'brace/theme/github'
import 'brace/theme/gob'
import 'brace/theme/gruvbox'
import 'brace/theme/idle_fingers'
import 'brace/theme/iplastic'
import 'brace/theme/katzenmilch'
import 'brace/theme/kr_theme'
import 'brace/theme/kuroir'
import 'brace/theme/merbivore'
import 'brace/theme/merbivore_soft'
import 'brace/theme/mono_industrial'
import 'brace/theme/monokai'
import 'brace/theme/pastel_on_dark'
import 'brace/theme/solarized_dark'
import 'brace/theme/solarized_light'
import 'brace/theme/sqlserver'
import 'brace/theme/terminal'
import 'brace/theme/textmate'
import 'brace/theme/tomorrow'
import 'brace/theme/tomorrow_night'
import 'brace/theme/tomorrow_night_blue'
import 'brace/theme/tomorrow_night_bright'
import 'brace/theme/tomorrow_night_eighties'
import 'brace/theme/twilight'
import 'brace/theme/vibrant_ink'
import 'brace/theme/xcode'
import { MdMemory, MdTimer, MdAccessTime, MdSettings, MdFileUpload, MdCloudUpload } from 'react-icons/md'
import { FaFileUpload } from 'react-icons/fa'

const CodeHeader = styled.div `
  margin: 7px;
  margin-left: 10px;
  display: flex;
  align-items: center;
  font-size: 14px;
  select {
    margin-right: 5px;
  }

  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */
`

const Settings = styled.button `
  position: relative;
  border: none;
  background: none;
  outline: none;
  display: flex;
  font-size: inherit;
  font-family: inherit;

  div {
    position: absolute;
    display: none;
    z-index: 1;
    right: 0;
    top: 0;
    flex-direction: column;
    background: var(--color_b2);
    padding: 15px;
    border-radius: 5px;
    /* box-shadow: 0 0 5px rgba(0, 0, 0, 0.3); */

    select:not(:last-child) {
      margin-bottom: 5px;
    }
  }

  &:hover div {
    display: flex;
  }
`

const StyledButton = styled.button `
  border: none;
  background: none;
  outline: none;
  display: flex;
  font-size: inherit;
  font-family: inherit;

  * {
    cursor: pointer;
  }

  label {
    display: flex;
    font-size: 14px;

    span {
      padding-left: 5px;
    }
  }
`

const Code = styled.div `
  color: var(--color_c2);
  position: relative;
  display: flex;
  flex-direction: column;
  text-align: left;
  margin: auto;
  margin-top: 1em;
  background-color: var(--color_b1);
  border: 1px solid var(--color_b1);
  line-height: 1;
  word-spacing: 0;
  /* max-width: 700px; */
  /* width: ${props => props.width ? props.width : "90%"}; */



  border-radius: ${props => props.borderRadius ? props.borderRadius : '8px'};
  overflow: hidden;
  /* transition: box-shadow ease 250ms; */
  &:focus-within {
    /* border: 1px solid ${theme.primary}; */
    /* box-shadow: 0 0 5px ${theme.primary}; */
  }

  @media only screen and (max-width: ${theme.mobileSize}) {
    width: 100%;
  }
`

const HideOnMobileSpan = styled.span `
  @media only screen and (max-width: ${theme.mobileSize}) {
    display: none;
  }
`

const CodeEditor = (props) => {

  const [state, setState] = useState({
    code: {value: '', language: 'c_cpp', theme: 'merbivore_soft'}
  })

  useEffect(() => {
    // setState({
    //   ...state,
    //   code: props.code,
    // })
  }, [])

  // 
  const onLanguageChanged = (e) => {
    setState({
      ...state,
      code: {...state.code, language: e.nativeEvent.target[e.nativeEvent.target.selectedIndex].text}
    })

    if (props.languageChanged) {
      props.languageChanged(e.target.value)
    }
  }

  //
  const onThemeChanged = (e) => {
    setState({
      ...state,
      code: {...state.code, theme: e.target.value}
    })
  }

  const on_filePicked = e => {
    const file = e.target.files.item(0)
    var reader = new FileReader();
    reader.readAsText(file, "UTF-8");

    reader.onload = (event) => {
      props.onChange(event.target.result)
    }
  }

  return (
    <Code borderRadius={props.borderRadius} width={props.width ? props.width : "100%"}>
      {!props.isPreview &&
      <CodeHeader>
        <StyledButton>
          <input id="code_editor_file_button" style={{display: 'none'}} type="file" onChange={on_filePicked} />
          <label style={{color: "var(--color_c2)"}} htmlFor="code_editor_file_button">
            <MdCloudUpload size="16" />
            <span style={{fontSize: '14px'}}><HideOnMobileSpan></HideOnMobileSpan> </span>
          </label>
        </StyledButton>

        <div style={{marginLeft: 'auto', marginTop: 'auto'}}>
          {props.rightItems}
        </div>
        {false && <Settings>
          <MdSettings />
          <div>
            <select onChange={onLanguageChanged}>
              <option value="cpp">c_cpp</option>
              <option value="python3">python</option>
            </select>
            <select value={state.code.theme} onChange={onThemeChanged} >
              <option>ambiance</option>
              <option>chaos</option>
              <option>chrome</option>
              <option>clouds</option>
              <option>clouds_midnight</option>
              <option>cobalt</option>
              <option>crimson_editor</option>
              <option>dawn</option>
              <option>dracula</option>
              <option>dreamweaver</option>
              <option>eclipse</option>
              <option>github</option>
              <option>gob</option>
              <option>gruvbox</option>
              <option>idle_fingers</option>
              <option>iplastic</option>
              <option>katzenmilch</option>
              <option>kr_theme</option>
              <option>kuroir</option>
              <option>merbivore</option>
              <option>merbivore_soft</option>
              <option>mono_industrial</option>
              <option>monokai</option>
              <option>pastel_on_dark</option>
              <option>solarized_dark</option>
              <option>solarized_light</option>
              <option>sqlserver</option>
              <option>terminal</option>
              <option>textmate</option>
              <option>tomorrow</option>
              <option>tomorrow_night</option>
              <option>tomorrow_night_blue</option>
              <option>tomorrow_night_bright</option>
              <option>tomorrow_night_eighties</option>
              <option>twilight</option>
              <option>vibrant_ink</option>
              <option>xcode</option>
            </select>
        </div>
        </Settings>}
      </CodeHeader>
      }
      {props.isPreview &&
        <>{props.preview}</>
      }
      <AceEditor
        readOnly={props.readOnly}
        theme={state.code.theme}
        value={props.code.value}
        onChange={props.onChange}
        height={props.height ? props.height : "50em"}
        width={props.width}
        mode={props.code.editorName} 
        focus={props.focus}
      /> 
    </Code>
  )
}

export default CodeEditor