import React from 'react'
import styled, { css } from 'styled-components'
import { Link } from 'react-router-dom'
import { theme } from 'Themes'

const Styled_Button = styled(Link) `

-webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */
                                  
  position: fixed;
  right: 15px;
  bottom: ${props => props.count ? 15*props.count + 40*(props.count-1) : 15}px;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: var(--color_b2);
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.4);

  ${props => props.full && css `
    width: 265px;
    border-radius: 20px;
    justify-content: flex-start;
    padding-left: 15px;
    text-decoration: none;
    color: var(--color_c2);
    font-family: ${theme.fontCaps};
  `};

  &:hover {
    transition: box-shadow ease 250ms,
                background-color ease 250ms;
    /* background-color: #f5f5f5; */
    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.4);
  }
`


const Button_Float = props => {

  return (
    <Styled_Button {...props} to={props.to ? props.to : ''}>
      {props.children}
    </Styled_Button>
  )
}

export default Button_Float