import React, { useState, useEffect, useContext } from 'react'
import { UserContext } from 'contexts/UserContext'
import { GlobalContext } from 'contexts/GlobalContext'
import JN from 'JN'
import styled from 'styled-components'
import { theme } from 'Themes'
import { Link } from 'react-router-dom'
import H3 from 'controls/H3'
import B from 'controls/B'
import { FaComment } from 'react-icons/fa'

const MainDiv = styled.section `
  width: 100%;
  display: flex;
  flex-direction: column;
  margin: auto;
  margin-top: 0;
  border-radius: 8px;
  max-height: 607px;
  padding-left: 5px;
  padding-right: 5px;
  overflow: auto;

  @media (max-width: ${theme.mobileSize}) {
    & {
      margin-left: 5px;
      padding-left: 0px;
      padding-right: 10px;
      max-height: 472px;
    }
  }
`

const Section = styled.section `
  text-align: left;
  h3 {
    margin-left: 5px;
  }

  @media (max-width: ${theme.mobileSize}) {
    & {
      margin-top: 50px;
      margin-left: 5px;
      margin-right: 5px;
    }
  }
`

const Table_A = styled(Link) `
  box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.2);
  background-color: var(--color_b2);
  padding: 25px;
  padding-top: 16px;
  padding-bottom: 16px;
  padding-right: 5px;
  margin-bottom: 10px;
  &:last-child {
    margin-bottom: 5px;
  }
  border-radius: 5px;
  text-decoration: none;
  color: var(--color_c2);

  display: flex;
  justify-content: space-between;

  border: 3px solid transparent;

  &:hover {
    transition: border ease 250ms;
    border: 3px solid var(--color_primary);
  }
`

const ChatButton = styled.button `
  border: none;
  outline: none;
  background: none;
  cursor: pointer;
  color: var(--color_c3);

  &:hover {
    transition: color ease 250ms;
    color: var(--color_c1);
  }
`

const TabFriends = props => {
  const { match } = props
  const { user, refreshGlobalState }  = useContext(UserContext)
  const { openChatRoom } = useContext(GlobalContext);

  const [friends, setFriends] = useState([])

  useEffect(() => {

  JN.get(user, 'user/friends', {},
    (data) => {
      setFriends(data.friends)
    },
    (error) => {
      alert(error)
    }
  )

  }, [])


  //  
  const openChat = username => {

    JN.post(user, 'chat/private_room', {user1: user.username, user2: username},
    (data) => {
      openChatRoom(data.room, username)
    },
    (error) => {
      openChatRoom(error.data.room, username)
    });
  }


  if (!user || !friends.length) {
    return (<div></div>)
  }

  return (
    <Section>
      <H3> ({friends.length})</H3>
      <MainDiv>
        {friends.map((friend, index) =>
          <Table_A key={friend.username} to={`/profile/${friend.username}`}>
            <div style={{display: 'flex', flexDirection: 'column', marginRight: 5}}>
              <div><B>{friend.username}</B></div>
              <div>{friend.name} {friend.surname}</div>
            </div>
            <ChatButton onClick={(e) => {e.preventDefault(); openChat(friend.username)}}><FaComment size="20" /></ChatButton>
          </Table_A>
        )}
      </MainDiv>
    </Section>
  )
}

export default TabFriends