import React from 'react'
import styled from 'styled-components'

const Div = styled.div `
  display: inline-block;
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */
`

const Span = styled.span `
  /* color: ${props => props.color ? props.color : 'var(--color_c2)'}; */
  font-size: ${props => props.size ? props.size : '14px'};
  padding-left: 2px;
  padding-right: 2px;
`
const TimeWidget = props => {
  const { time, isDynamic } = props

  if (!time) {
    return <></>
  }

  return (
    <Div>
      {time.days    !== null && (isDynamic || (!isDynamic && time.days !== 0)) && <>{time.days}<Span {...props}></Span> </>}
      {time.hours   !== null && (isDynamic || (!isDynamic && time.hours !== 0)) && <>{time.hours}<Span {...props}></Span> </>}
      {time.minutes !== null && (isDynamic || (!isDynamic && time.minutes !== 0)) && <>{time.minutes}<Span {...props}></Span> </>}
      {time.seconds !== null && (isDynamic || (!isDynamic && time.seconds !== 0)) && <>{time.seconds}<Span {...props}></Span> </>}
    </Div>
  )
}

export default TimeWidget