import React, { useState, useEffect, useContext} from 'react'
import JN from 'JN'
import Container from 'controls/Container'
import LoadingSvg from 'components/LoadingSvg'
import { Get_PaginationArray, Get_Number, Convert_date_to_string } from 'Global_Functions'
import { MdMemory, MdAccessTime, MdKeyboardArrowRight, MdKeyboardArrowLeft } from 'react-icons/md'
import {Link, NavLink } from 'react-router-dom'
import styled, { css } from 'styled-components'
import { theme } from 'Themes'
import SubmissionPreviewDlg from 'pages/submissions/SubmissionPreviewDlg';
import Table, { Th, Td, Table_A } from 'controls/Table'
import PaginationContainer from 'controls/pagination/PaginationContainer'
import { UserContext } from 'contexts/UserContext'

const MDiv = styled.div `
  padding: 56px;
  padding-top: -0px;
  margin: auto;
  max-width: 1366px;
  @media (max-width: ${theme.mobileSize}) {
    padding: 0;
  }
`

const TableContainer = styled.div `
  box-shadow: 0 6px 16px 0 rgba(0,0,0,.2);
  margin-top: 45px;
  border-radius: 8px;
  @media (max-width: ${theme.mobileSize}) {
    padding: 0;
  }
`

const Success = styled.span `

  ${props => props.code === 0 && css`
    color: var(--color_green);
  `}

  ${props => props.code === 1 && css`
    color: var(--color_red);
  `}
`


const Submissions_Page = props => {
  const { user } = useContext(UserContext)
  const { match } = props

  const [state, setState] = useState({
    submissions: [],
    problem_name: '',
    is_loading: true,
    open_submission: false,
    opened_submission_id: -1,
    pagination: {current: 0, total: 0},
  })

  useEffect(() => {
    window.scroll(0, 0);
  }, []);

  useEffect(() => {
    document.title = ` . ${match.params.page}`;
    setState({...state, is_loading: true});
    JN.get(user, match.params.id === '0' ? `submissions/${match.params.page}` : `problemsubmissions/${match.params.id}/${match.params.page}`, {},
    (data) => {
      let listSubmissions = data.submissions
      let problem_name

      if (listSubmissions.length) {
        problem_name = listSubmissions[0].problem_title
      }
      window.scroll(0, 0);
      setState({
        ...state,
        submissions: listSubmissions,
        is_loading: false,
        pagination: {current: match.params.page, total: data.page_number},
        problem_name: problem_name,
      })
    },
    () => {
      setState({
        ...state,
        is_loading: false,
      })
    })
  }, [match.url])

  if (state.is_loading && state.submissions.length === 0) {
    return (
      <Container>
        <div style={{margin: 'auto'}}><LoadingSvg /></div>
      </Container>
    )
  }

  const on_submition_clicked = id => e => {
    e.preventDefault()
    setState({
      ...state,
      open_submission: true,
      opened_submission_id: id,
    })
  }

  const on_submissionsPreviewDlg_close = () => {
    setState({
      ...state,
      open_submission: false,
    })
  }

  return (
    <MDiv>
      <TableContainer>
      <Table count={state.pagination.total > 1}>
        <thead></thead>
        <tbody>
        {state.submissions.length === 0 ? <tr><th></th><th style={{align: 'left'}} colSpan="15"></th></tr> :
        <tr>
          <th></th>
          <Th left></Th>
          <Th autoSize left></Th>
          <Th left></Th>
          <th></th>
          <th></th>
          <th>
            <div style={{display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
              <MdMemory size="16" />&nbsp;
            </div>
          </th>
          <th>
            <div style={{display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
              <MdAccessTime  size="16" />&nbsp;
            </div>
          </th>

        </tr>}
        {!state.is_loading && state.submissions.length === 0 ? 
          <tr><td colSpan="10" style={{color: 'var(--color_c2)', fontSize: '14px', textAlign: 'left'}}>
               <span style={{color: 'red'}}>!</span>
          </td></tr>
          : state.submissions.length === 0 && 
          <>
            <tr><td colSpan="8" style={{fontSize: '14px', textAlign: 'left'}}>..</td><td></td></tr>
            <tr><td></td><td colSpan="8" style={{color: '#666', fontSize: '14px', textAlign: 'left'}}></td></tr>
            <tr><td></td><td colSpan="8" style={{color: '#666', fontSize: '14px', textAlign: 'left'}}></td></tr>
            <tr><td></td><td colSpan="8" style={{color: '#666', fontSize: '14px', textAlign: 'left'}}></td></tr>
            <tr><td></td><td colSpan="8" style={{color: '#666', fontSize: '14px', textAlign: 'left'}}></td></tr>
            <tr><td></td><td colSpan="8" style={{color: '#666', fontSize: '14px', textAlign: 'left'}}></td></tr>
            <tr><td></td><td colSpan="8" style={{color: '#666', fontSize: '14px', textAlign: 'left'}}></td></tr>
            <tr><td></td><td colSpan="8" style={{color: '#666', fontSize: '14px', textAlign: 'left'}}></td></tr>
            <tr><td></td><td colSpan="8" style={{color: '#666', fontSize: '14px', textAlign: 'left'}}></td></tr>
            <tr><td></td><td colSpan="8" style={{color: '#666', fontSize: '14px', textAlign: 'left'}}></td></tr>
            <tr><td></td><td colSpan="8" style={{color: '#666', fontSize: '14px', textAlign: 'left'}}></td></tr>
            <tr><td></td><td colSpan="8" style={{color: '#666', fontSize: '14px', textAlign: 'left'}}></td></tr>
            <tr><td></td><td colSpan="8" style={{color: '#666', fontSize: '14px', textAlign: 'left'}}></td></tr>
          </>
        }
        {state.submissions.map((submission, index) => 
          <tr style={{backgroundColor: user && submission.username === user.username ? 'var(--color_b22)' : 'var(--color_b2)'}} key={submission.id}>
            <Td low="true" onClick={on_submition_clicked(submission.id)}>{Get_Number(submission.id)}</Td>
            <Td low="true" left>
              <Table_A to={`/profile/${submission.username}`}>
                {submission.username}
              </Table_A>
            </Td>
            <Td low="true" left style={{maxWidth: 70, whiteSpace: 'normal'}}>
              <Table_A to={`/problem/${submission.problem_id}`}>
              {submission.problem_id}. {submission.problem_title}
              </Table_A>
            </Td>
            <Td low="true" left onClick={on_submition_clicked(submission.id)}><Success code={submission.verdict_code}>{submission.verdict_ge}</Success></Td>
            <Td low="true" onClick={on_submition_clicked(submission.id)}>{Convert_date_to_string(submission.datetime)}</Td>
            <Td center low="true" onClick={on_submition_clicked(submission.id)}>{submission.language}</Td>
            <Td center low="true" onClick={on_submition_clicked(submission.id)}>{submission.memory}</Td>
            <Td center low="true" onClick={on_submition_clicked(submission.id)}>{submission.time}</Td>
            
          </tr>
          )
        }
        </tbody>
      </Table>


      <PaginationContainer isLoading={state.is_loading} to={`/submissions/${match.params.id}/:page`} pagination={state.pagination} />

      {state.open_submission &&
        <SubmissionPreviewDlg id={state.opened_submission_id} close={on_submissionsPreviewDlg_close} />
      }
      </TableContainer>
    </MDiv>
  )
}

export default Submissions_Page