import React, { useState, useEffect, useContext } from 'react'
import JN from 'JN'
import styled, { css } from 'styled-components'
import { theme } from 'Themes'
import WithMathJax from 'components/WithMathJax';
import Container from 'controls/Container';
import { MessageBoxContext } from 'contexts/MessageBoxContext'
import { UserContext } from 'contexts/UserContext'
import JParser from 'components/JParser';
import Button_Float from 'controls/Button_Float';
import { MdEdit, MdDelete, MdKeyboardArrowRight, MdFolder, MdVisibilityOff } from 'react-icons/md';
import Helmet from 'react-helmet'
import A from 'controls/A';


const ParentFoldersDiv = styled.div `
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */

  display: flex;
  text-align: left;
  /* padding: 25px; */
  margin-top: 5px;
  margin-right: 15px;
  height: 44px;
  width: 100%;
  overflow: auto;
  font-size: 16px;
`

const Tag = styled.div `
  display: inline-flex;
  justify-content: center;
  align-items: center;
  color: var(--color_c2);
  background-color: var(--color_b1);
  border: 3px solid var(--color_b22);
  padding-left: 15px;
  padding-right: 15px;
  margin-right: 8px;
  margin-bottom: 8px;
  height: 36px;
  border-radius: 5px;

  &:hover {
    border-color: var(--color_primary);
  }
`

const Statement = styled.div `
  margin-top: 2em;
  margin-bottom: 4em;
  text-align: justify;
  line-height: 1.6;
  font-size: 14px; 
  color: var(--color_c2);
`

const P = styled.div `
  text-align: left;
  margin-bottom: 4em;
  font-size: 14px;
  color: var(--color_c2);
  text-align: justify;
  & > label {
    display: block;
    font-family: ${theme.fontCaps};
    color: var(--color_c22);
    font-size: 16px;
  }
`

const ProblemsDiv = styled.div `
  /* margin-top: 30px; */
`

const ProblemItem = styled.div `
  display: flex;
  align-items: center;
  /* border-bottom: 1px solid var(--color_b22);
  padding: 5px;
  &:last-child {
    border-bottom: none;
  } */

  button {
    cursor: pointer;
    border: none;
    outline: none;
    background: none;
    margin-left: auto;
    display: flex;
    color: var(--color_c2);

    &:hover {
      color: var(--color_red);
    }
  }
`

const FullTutorial = props => {
  const { match, history } = props;
  const { user }  = useContext(UserContext);
  const { toast } = useContext(MessageBoxContext);
  const [state, setState] = useState({
    tutorial: {

    },
    isDeleting: false,
  });
  const [tutorialTree, setTutorialTree] = useState({
    id: -1,
    title: '',
    sub_directories: [],
    parent_directories: [],
  });

  useEffect(() => {
    JN.get(user, `tutorial/${match.params.id}`, {},
    (data) => {
      JN.get(user, 'tutorial_tree', {id: data.directory_id},
      (d) => {
        setTutorialTree({...d, id: data.directory_id});
      }, null);

      setState({...state, tutorial: data});
    });
    window.scroll(0, 0);

  }, [match.url])

  const on_tutorial_delete = e => {
    e.preventDefault();
    if (!window.confirm('  ?!')) {
      return;
    }

    setState({...state, isDeleting: true});

    JN.delete(user, `admin/tutorial`, {id: match.params.id},
    () => {
      setState({...state, isDeleting: false});
      history.push('/tutorials/1');
    },
    () => {
      setState({...state, isDeleting: false});
    })
  }

  if (!state.tutorial.title) {
    return (<></>);
  }

  return (
    <WithMathJax>
      <Helmet>
        <meta charSet="utf-8" />
        <title>{state.tutorial.title}</title>
        <meta name="description" content="AiaSoft" />
      </Helmet>
      <Container title={state.tutorial.title} width="100%" maxWidth="1000px">
        <Statement>
          {state.tutorial.body && <JParser>{state.tutorial.body}</JParser>}
        </Statement>

        {state.tutorial.note &&
          <P>
            <label></label>
            {state.tutorial.note && <JParser>{state.tutorial.note}</JParser>}
          </P>
        }

        {state.tutorial.tags.length > 0 &&
        <P>
          <label> </label>
          {state.tutorial.tags.map(tag =>
            <A hover="true" key={tag.id} to={`/problems/1/${tag.id}`}>
              <Tag>{tag.title}</Tag>
            </A>
          )}
        </P>
        }

        {state.tutorial.problems.length > 0 &&
        <P>
          <label></label>
          <ProblemsDiv>
            {state.tutorial.problems.map(problem =>
              <ProblemItem style={{textAlign: 'left'}} key={problem.id}>
                <MdKeyboardArrowRight size="18" /><A hover="true" to={`/problem/${problem.id}`}>{problem.title}{!problem.is_visible && <span style={{marginLeft: 5, display: 'flex', alignItems: 'center'}}><MdVisibilityOff size="20" color="var(--color_c3)" /></span>}</A>
              </ProblemItem>
            )}
          </ProblemsDiv>
        </P>
        }

        {state.tutorial.references.length > 0 &&
          <P>
            <label> </label>
            <ProblemsDiv>
              {state.tutorial.references.map(reference =>
                <ProblemItem style={{textAlign: 'left'}} key={reference.id}>
                  <MdKeyboardArrowRight size="18" /><A hover="true" to={`/tutorial/${reference.id}`}>{reference.title}{!reference.is_visible && <span style={{marginLeft: 5, display: 'flex', alignItems: 'center'}}><MdVisibilityOff size="20" color="var(--color_c3)" /></span>}</A>
                </ProblemItem>
              )}
            </ProblemsDiv>
          </P>
        }

        {tutorialTree.parent_directories &&
          <ParentFoldersDiv>
            {tutorialTree.parent_directories.map((folder, index) =>
              <span style={{display: 'flex', justifyContent: 'center', alignItems: 'center'}} key={index}>
                <A style={{display: 'flex', whiteSpace: 'nowrap', justifyContent: 'center', alignItems: 'center'}} to={`/tutorials/${folder.id}`} hover="true">
                  <MdFolder size="16" color="var(--color_c2)" />&nbsp;<span style={{whiteSpace: 'nowrap'}}>{!index ? '' : folder.name}</span>
                </A>
                &nbsp;/&nbsp;
              </span>
            )}
            <A style={{display: 'flex', whiteSpace: 'nowrap', justifyContent: 'center', alignItems: 'center'}} to={`/tutorials/${tutorialTree.id}`} hover="true">
              <MdFolder size="16" color="var(--color_c2)" />&nbsp;<span style={{whiteSpace: 'nowrap'}}>{tutorialTree.name === `tutorials` ? '' : tutorialTree.name}</span>
            </A>
          </ParentFoldersDiv>
        }

      </Container>
      {!state.is_fullscreen && user && user.permission !== "user" &&
      <>
        <Button_Float count="2" to={`/edittutorial/${match.params.id}`}><MdEdit color="var(--color_orange)" size="22" /></Button_Float>
        {!state.isDeleting && <Button_Float onClick={on_tutorial_delete}><MdDelete color="var(--color_red)" size="22" /></Button_Float> }
      </>
      }
    </WithMathJax>
  );
}

export default FullTutorial;