import React, { useState, useEffect, useContext } from 'react'
import { UserContext } from 'contexts/UserContext'
import JN from 'JN'
import ContainerFooter from 'controls/ContainersFooter'
import Input from 'controls/Input'
import Modal from 'components/Modal'
import Button from 'controls/Button'
import { MessageBoxContext } from 'contexts/MessageBoxContext'

const ProblemEditDlg = props => {
  const { match } = props
  const { user }  = useContext(UserContext)
  const { toast } = useContext(MessageBoxContext)
  const [state, setState] = useState({
    problem: {isNew: true, title: '', point: 0},
    isLoading: false,
  })

  useEffect(() => {
    
    const problem = props.problem ? props.problem : {problem_id: '', isNew: true, title: '', point: 0}
    setState({
      ...state,
      problem: problem,
    })
  }, [props.problem]);

  const on_change = e => {
    setState({...state, problem: {...state.problem, [e.target.name]: e.target.value,}})
  }

  const submit = e => {
    e.preventDefault()

    setState({...state, isLoading: true,})
    if (state.problem.isNew) {

      JN.post(user, 'admin/contest/append', {
        contest_id: props.contestId,
        problem_id: state.problem.problem_id,
        score: state.problem.score,
      },
      (data) => {
        props.setState({
          ...props.state,
          problems: [...props.state.problems, {problem_id: state.problem.problem_id, score: state.problem.score, title: data.problem}],
          is_problemEditDlg_visible: false,
        })
      },
      (error) => {
        if (error.status === 400) {
          toast('   ', 'error')
        }
        else if (error.status === 404) {
          toast('   ', 'error')
        }

        setState({...state, isLoading: false,})
      })
    }
    else {

      JN.put(user, `admin/contest/append`, {
        contest_id: props.contestId,
        problem_id: state.problem.problem_id,
        new_score: state.problem.score,
      },
      () => {
        props.setState({
          ...props.state,
          problems: props.state.problems.map((p) => {
            return p.problem_id !== state.problem.problem_id ? p : {...p, score: state.problem.score}
          }),
          is_problemEditDlg_visible: false,
        })
      },
      (error) => {

        if (error.status === 404) {
          toast('   ', 'error')
        }
        setState({...state, isLoading: false,})
      })
    }
  }

  const setOpen = (isOpen) => {
    if (state.isLoading) {
      return
    }
    props.close(isOpen)
  }

  return (
    <Modal width="450px" title={state.problem.isNew ? " " : " "} isOpen={props.isOpen} close={setOpen}>
      <form onSubmit={submit}>
        <Input disabled={!state.problem.isNew} required focus={state.problem.isNew} title="id" name="problem_id" value={state.problem.problem_id} onChange={on_change}/>
        <Input required title="" name="score" value={state.problem.score} onChange={on_change}/>
        <ContainerFooter>
          <Button disabled={state.isLoading} backgroundColor="primary"></Button>
        </ContainerFooter>
      </form>
    </Modal>
  )
}
 
export default ProblemEditDlg