import React from 'react'
import styled, { ThemeProvider, css } from 'styled-components'
import { theme } from 'Themes'

const CIcon = styled.span `
  margin-right: 5px;
`

const StyledButton = styled.button `

  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */

  position: relative;
  overflow: hidden;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-family: ${props => props.NoCap ? theme.font : theme.fontCaps};
  font-size: ${props => props.fontSize ? props.fontSize : '14px'};
  min-width: ${props => props.width ? props.width : '200px'};
  width: ${props => props.width};
  color: ${theme.c2};
  padding: 5px;
  border: none;
  border-radius: 5px;
  padding: .7em 1em;
  outline: none;
  margin: 5px;
  margin-left: ${props => props.marginLeft ? props.marginLeft : '5px'};
  cursor: pointer;
  min-height: 46px;
  background: ${theme.primary};
  color: ${props => props.color ? props.color : "var(--color_tl1)"};
  ${props => 
    props.width &&
    css`
      width: ${props => theme[props.width]};
    `
  };

  &:hover {
    box-shadow: inset 0 0 28px rgba(0, 0, 0, .3);
  }


  background: ${props => props.backgroundColor ? props.backgroundColor : "var(--color_primary)"};

  ${props =>
    props.flat &&
    css`
      & {
        background: var(--color_b1);
        color: var(--color_c2);
        box-shadow: none;
        border: 2px solid var(--color_b1)
      }

      &:hover {
        box-shadow: none;
        border-color: var(--color_primary);
      }
    `
  };

  &:focus {
    border: 0px solid ${theme.primary};
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
    ${props => props.flat &&
    css`
      box-shadow: none;
      border: none;
    `};
  }
  &:disabled {
    /* background-color: ${theme.b1}; */
  }
`

const Button = React.forwardRef((props, ref) => {
    return (
      <ThemeProvider theme={theme}>
      <div>
        <StyledButton {...props} ref={ref}>
          <CIcon>{props.icon}</CIcon>{props.children}
        </StyledButton>
      </div>
      </ThemeProvider>
    )
})

export default Button