import React, { useState, useEffect, useContext } from 'react'
import styled, { css } from 'styled-components'
import Button from 'controls/Button'
import HeaderButton from 'controls/HeaderButton'
import { theme, dark, light } from 'Themes'
import Registration from 'pages/users/Registration';
import Login from '../pages/users/Login';
import { UserContext } from 'contexts/UserContext'
import axios from 'axios'
import { Server } from 'Server'
import { Switch, Route, Link, Redirect, useLocation } from "react-router-dom";
import Problems from 'pages/problems/Problems'
import Tutorials from 'pages/tutorials/Tutorials'
import Profile from 'pages/users/Profile'
import Main from 'pages/Main'
import RestorePassword from 'pages/users/RestorePassword'
import HeaderInput from 'controls/HeaderInput'
import FullProblem from 'pages/problems/FullProblem'
import AddProblem from 'pages/problems/AddProblem'
import page_404 from 'pages/page_404'
import Profile_page from 'pages/users/profile/Profile_page'
import Submissions_Page from 'pages/submissions/Submissions_Page'
import Submission_Page from 'pages/submissions/Submission_Page'
import { MdHome, MdChat, MdChatBubbleOutline } from 'react-icons/md'
import Container from 'controls/Container'
import Contests from 'pages/contests/Contests'
import AddContest from 'pages/contests/AddContest'
import FullContest from 'pages/contests/FullContest/FullContest'
import LoadingSvg from './LoadingSvg'
import UsersPage from 'pages/users/UsersPage'
import Modal from './Modal'
import Input from 'controls/Input'
import ContainerFooter from 'controls/ContainersFooter'
import FreshingTokenDlg from './FreshingTokenDlg'
import Favourites from './Favourites'
import { FaHeart, FaMoon, FaSun, FaComment, FaTimes } from 'react-icons/fa'
import RatingPage from 'pages/rating/RatingPage'
import AddTutorial from 'pages/tutorials/AddTutorial/AddTutorial'
import FullTutorial from 'pages/tutorials/FullTutorial'
import { GlobalContext } from 'contexts/GlobalContext'
import { MessageBoxContext } from 'contexts/MessageBoxContext'
import Chat from 'pages/socketio/chat/Chat'
import Drawer from './Drawer'
import B from 'controls/B'
import ArrayMaker from './ArrayMaker'
import ReactGA from 'react-ga'

const ChangeThemeButton = styled.button`
  font-family: ${theme.font};
  font-size: 14px;
  outline: none;
  border: none;
  background: none;
  color: var(--color_headerText);
  display: flex;
  align-items: center;
  cursor: pointer;
  padding-left: 20px;
  padding-right: 20px;

  svg {
    transition: transform ease 250ms;
  }

  &:hover {
    box-shadow: inset 0 0 28px rgba(0, 0, 0, .3);

    svg {
      transform: rotate(25deg);
    }
  }

  ${props => props.hide && css`
    @media (max-width: ${theme.mobileSize}) {
      display: none;
    }
  `}
`

const HomeIcon = styled.span`
  color: var(--color_headerText);
  display: flex;
  align-items: center;

`

const Styled_Header = styled.nav`
  position: relative;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.2);
  display: flex;
  background-color: #4056a1;
  background-color: var(--color_headerBackground);
  /* box-shadow: 0 0 10px rgba(0, 0, 0, 0.5); */
  margin-bottom: calc(56px + 45px);
  /* margin-bottom: 70px; */

  /* @media (max-width: ${theme.mobileSize}) {
    flex-direction: column;
  } */
`

const StyledLink = styled(Link)`
  text-decoration: none;
  margin: auto;
  margin-left: 15px;
  margin-right: 0;
`

const Icon = styled.button`
  outline: none;
  border: none;
  background: none;
  cursor: pointer;
`


const BtnChatDiv = styled.div`
  z-index: 101;
  position: fixed;
  left: 20px;
  bottom: 20px;
`


const BtnChat = styled.button`
  position: relative;
  cursor: pointer;
  /* width: 80px; */
  padding-left: 16px;
  padding-right: 16px;
  height: 56px;
  border: none;
  border-radius: 999px;
  background: var(--color_primary);
  outline: none;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.2);
  font-weight: bold;
  font-size: 18px;
  color: var(--color_headerText);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform ease 200ms;

  span {
    margin-left: 8px;
  }

  &:active {
    transform: scale(0.90);
  }


  svg {
    transition: transform ease 250ms;
  }
  
  &:hover {
    svg {
    transform: rotate(25deg);
  }
`

const DivImage = styled.div`
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background-color: rgba(0, 0, 0, 0.5);
	z-index: 200;
  display: flex;
  justify-content: center;
  align-items: center;

  animation: .2s cubic-bezier(0.25, 0.46, 0.45, 0.94) both a_fade;
  @keyframes a_fade {
    0% {
      opacity: 0;
    }
    
    100% {
      opacity: 1;
    }
  }

  div {
    animation: .2s cubic-bezier(0.25, 0.46, 0.45, 0.94) both a_scale;
    @keyframes a_scale {
      0% {
        opacity: .7;
        transform: scale(0.98);
      }
      
      100% {
        opacity: 1;
        transform: scale(1);
      }
    }
  }

  img {
    max-width: 90vw;
    max-height: 96vh;
    user-select: none;
  }
`

const DivJMessageBox = styled.div`
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background-color: rgba(0, 0, 0, 0.5);
	z-index: 200;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: left;

  * {
    user-select: none;
  }

  animation: .2s cubic-bezier(0.25, 0.46, 0.45, 0.94) both a_fade;
  @keyframes a_fade {
    0% {opacity: 0;}
    100% {opacity: 1;}
  }

  div {
    max-width: 70vw;
    animation: .2s cubic-bezier(0.25, 0.46, 0.45, 0.94) both a_scale;
    @keyframes a_scale {
      0% {opacity: .7;transform: scale(0.96);}
      
      100% {opacity: 1;transform: scale(1);}
    }

    color: var(--color_c1);
    background: var(--color_b2);
    font-size: 1.2rem;
    border-radius: 4px;
    padding: 10px 20px;

    &:nth-child(2) {
      margin-top: 30px;
      display: flex;
      justify-content: flex-end;
    }

    button {
      border: none;
      font-size: 1rem;
      user-select: none;
      outline: none;
      font-family: ${theme.fontCaps};
      border-radius: 4px;
      padding: 10px 20px;
      padding-top: 12px;
      cursor: pointer;

      color: var(--color_headerText);
      background: var(--color_primary);
      &:first-child {
        color: var(--color_pink);
        background: none;
        margin-right: 20px;
        font-weight: bold;
      }

      transition: transform 250ms cubic-bezier(0.175, 0.885, 0.32, 1.275) 0s;
      &:hover {
        transform: scale(1.12);
      }
      &:active {
        transform: scale(0.9);
      }
    }
  }
`

function Header(props) {
  const { user, login, logout } = useContext(UserContext)
  const { jMb, jMessageBox, closeMessageBox } = useContext(MessageBoxContext)
  const { global, setGlobal, setLanguage, tr, chat, setChatOpen, _unseenCount, setOpenedImage, openedImageSrc } = useContext(GlobalContext)
  const [mode, setMode] = useState(localStorage.getItem('mode'))


  //  
  useEffect(() => {
    ReactGA.initialize('UA-184179454-1')
  }, [])

  const location = useLocation();
  useEffect(() => {
    ReactGA.pageview(location.pathname)
  }, [location]);
  //~  


  //10
  const [isChatVisible, setChatVisible] = useState(false);
  const toggleChatVisibility = () => {
    setChatOpen(!chat.isOpen)
    // setChatVisible(!isChatVisible);
  }

  const closeChat = () => {
    setChatOpen(false)
  }

  const refresh_user = () => {

    axios.get(`${Server.address}/profile`, {
      headers: {
        Authorization: 'Bearer ' + localStorage.getItem('access_token')
      }
    })
      .then(response => {

        login({
          ...response.data,
          access_token: localStorage.getItem('access_token'),
          refresh_token: localStorage.getItem('refresh_token'),
        })
      })
      .catch(error => {

        //refresh_token -  access_token -
        if (error.response && error.response.status === 401 && error.response.data.error_code === 1) {
          axios.post(`${Server.address}/refresh`, {}, { headers: { Authorization: 'Bearer ' + localStorage.getItem('refresh_token') } })
            .then(response => {

              localStorage.setItem('access_token', response.data.access_token)
              refresh_user()
            })
            .catch(error => {
              logout()
              window.location.reload();
            })
        }
        else {
          logout()
          window.location.reload();
        }
      })
  }

  const setTheme = m => {
    let root = document.documentElement.style
    let ob = m === 'light' ? light : dark;

    Object.keys(ob).map((name, index) => {
      root.setProperty(`--color_${name}`, ob[name]);
    })

    // document.body.style.backgroundImage = m === 'light' ? 'url("https://qph.fs.quoracdn.net/main-qimg-ad4efefc4d02affc785e9007bf2f0d87")' : 'url("images/pattern_dark.png")';
    setGlobal({ ...global, mode: m });
  }

  useEffect(() => {

    if (localStorage.getItem('access_token')) {
      refresh_user()
    }

    let mode = localStorage.getItem('mode');
    mode = mode ? mode : 'light';
    setGlobal({ ...global, mode: mode });

  }, [])

  const [isLoggedin, setIsLoggedin] = useState(localStorage.getItem("access_token") !== null)
  const onLoggedStatusChanged = (value) => {
    setIsLoggedin(value)
  }

  //
  const onLogoutClicked = (e) => {
    if (user) {
      ReactGA.event({ category: '', action: ' ', label: `${user.username}` });
    }
    logout()
  }

  let rightPanel = (
    <>
      <HeaderButton bg="var(--color_primary)" to="/login">{tr("")}</HeaderButton>
      <HeaderButton to="/registration">{tr("")}</HeaderButton>
    </>
  )
  if (user) {
    rightPanel = (
      <>
        <HeaderButton bg="var(--color_primary)" to={`/profile/${user.username}`}>{user.username}</HeaderButton>
        <HeaderButton to="/" onClick={onLogoutClicked}>{tr("")}</HeaderButton>
      </>
    )
  }

  if (!user && localStorage.getItem('access_token')) {
    return (
      <div style={{ marginTop: '50px' }}>
        {/* <LoadingSvg color="whites"/> */}
      </div>
    )
  }

  const onChangeTheme = e => {
    e.preventDefault()
    let newMode = mode === 'dark' ? 'light' : 'dark'
    localStorage.setItem('mode', newMode);
    setMode(newMode)
    setTheme(newMode)
  }

  const onChangeLanguage = e => {
    e.preventDefault();
    setLanguage(global.language === "eng" ? "geo" : "eng");
  }

  return (
    <div>

      <Styled_Header>
        <HeaderButton color={theme.primary} to="/"><HomeIcon><MdHome size="28" /></HomeIcon></HeaderButton>
        <HeaderButton color={theme.primary} to="/problems/1">{tr("")}</HeaderButton>
        <HeaderButton color={theme.red} to="/contests">{tr("")}</HeaderButton>
        <HeaderButton color={theme.orange} to="/rating">{tr("")}</HeaderButton>
        <HeaderButton color={theme.green} to="/tutorials">{tr("")}</HeaderButton>

        <div style={{ marginLeft: 'auto' }}></div>

        <ChangeThemeButton hide="true" onClick={onChangeLanguage}>{tr("Eng")}</ChangeThemeButton>
        <ChangeThemeButton onClick={onChangeTheme}>{global.mode === 'dark' ? <FaSun color="var(--color_orange)" size="20" /> : <FaMoon size="20" />}</ChangeThemeButton>

        {rightPanel}
        {/* <HeaderInput /> */}
      </Styled_Header>

      <Drawer>

      </Drawer>


      <BtnChatDiv style={{ display: user ? 'inherit' : 'none' }}>
        <BtnChat onClick={toggleChatVisibility}>
          {!chat.isOpen ? <FaComment size="24" /> : <FaTimes size="24" />}
          {_unseenCount > 0 && <span>{_unseenCount}</span>}
        </BtnChat>
        <Chat close={closeChat} visible={chat.isOpen} />
      </BtnChatDiv>




      <Switch>
        <Route exact path="/" component={Main} />

        {/*    */}
        <Route exact path="/participants/:contest_id/:page" component={UsersPage} />
        <Route exact path="/participants/:page" component={UsersPage} />
        <Redirect from="/participants" to="/participants/1" />

        {/*  */}
        <Route exact path="/rating/:page" component={RatingPage} />
        <Redirect from="/rating" to="/rating/1" />

        {/*  */}
        <Route path="/contest/:id" component={FullContest} />
        <Route exact path="/contests/:page" component={Contests} />
        <Redirect from="/contests" to="/contests/1" />
        <Route exact path="/addcontest" component={AddContest} />
        <Route exact path="/editcontest/:id" component={AddContest} />

        <Route exact path="/problem/:id" component={FullProblem} />
        <Route exact path="/problems/:page/:tagId" component={Problems} />
        <Route exact path="/problems/:page" component={Problems} />
        <Redirect from="/problems" to="/problems/1" />
        <Route exact path="/addproblem" component={AddProblem} />
        <Route exact path="/editproblem/:id" component={AddProblem} />

        <Route exact path="/addtutorial/:dirId" component={AddTutorial} />
        <Route exact path="/edittutorial/:id" component={AddTutorial} />
        <Redirect from="/addtutorial" to="/addtutorial/1" />
        <Route exact path="/tutorials" component={Tutorials} />
        <Route exact path="/tutorials/:id" component={Tutorials} />
        <Route exact path="/tutorial/:id" component={FullTutorial} />

        <Route exact path="/login" component={Login} />
        <Route exact path="/login/:id" component={Login} />

        <Route exact path="/registration" component={Registration} />

        <Route path="/profile/:userName/:page" component={Profile_page} />
        <Route path="/profile/:userName" component={Profile_page} />
        <Route exact path="/restorepassword" component={RestorePassword} />
        <Route exact path="/submissions/:id/:page" component={Submissions_Page} />
        <Redirect exact from="/submissions/:id" to="/submissions/:id/1" />
        <Redirect exact from="/submissions" to="/submissions/0/1" />

        <Route exact path="/submission/:id" component={Submission_Page} />

        <Route exact path="/paint" component={ArrayMaker} />
        <Route path="/" component={page_404} />
      </Switch>


      {user && user.freshTokenRequired && <FreshingTokenDlg />}

      {/*   */}
      {openedImageSrc !== '' &&
        <DivImage onClick={e => { e.preventDefault(); setOpenedImage(''); }}>
          <div><img src={openedImageSrc} /></div>
        </DivImage>
      }


      {/* jMb */}
      {jMessageBox.isOpen &&
        <DivJMessageBox onClick={e => { e.preventDefault(); if (e.target === e.currentTarget) closeMessageBox(); }}>
          <div onClick={e => e.preventDefault()}>
            <div>
              <B>{jMessageBox.text}</B>
            </div>
            <div>
              <button onClick={e => { e.preventDefault(); closeMessageBox(); }} ></button>
              <button onClick={e => { closeMessageBox(); jMessageBox.callBack_accepted(); }}></button>
            </div>
          </div>
        </DivJMessageBox>
      }

    </div>
  )
}

export default Header