import React, { useState, useContext, useEffect, useRef } from 'react'
import Input from 'controls/Input'
import styled from 'styled-components'
import JN from 'JN'
import { theme } from 'Themes'
import { UserContext } from 'contexts/UserContext'
import B from 'controls/B'
import { MdDelete } from 'react-icons/md'
import LoadingSvg from 'components/LoadingSvg'
import A from 'controls/A'
import { MessageBoxContext } from 'contexts/MessageBoxContext'

const DivTags = styled.div`
  display: flex;
  text-align: left;
  flex-direction: column;
  width: 100%;
  max-height: 430px;
  overflow: auto;
  margin-bottom: 20px;
  margin-top: 5px;
  border: 1px solid var(--color_b22);
  border-radius: 8px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
`

const DivTag = styled.div`
  font-size: 14px;
  display: flex;
  padding: 3px;
  padding-left: 10px;
  align-items: center;
  background-color: transparent;
  border-bottom: 1px solid var(--color_b22);

  &, * {
    cursor: pointer;
  }

  &:last-child {
    /* border-bottom: none; */
  }

  label {
    flex-grow: 1;
    margin-left: .5em;
  }

  &:hover {
    transition: background-color ease 250ms;
    background-color: var(--color_b11);

    button {
      transition: opacity ease 250ms;
      opacity: 1;
    }
  }
`
const StyledButton = styled.button`
  border: none;
  outline: none;
  background: none;
  cursor: pointer;
  opacity: 0;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  margin-left: 3px;

  /* &:hover {
    transition: background-color ease 250ms;
    background-color: white;
  } */
`

const FilterContainer = styled.div`
  position: absolute;
  top: 95px;
  background-color: var(--color_b2);
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  overflow: auto;
  max-height: 250px;
  width: ${props => props.width ? props.width : '100%'};
  z-index: 1;
`

const FilterItem = styled.button`
  display: block;
	width: 100%;
  text-align: left;
  height: 45px;
  text-decoration: none;
  color: var(--color_c2);
  font-family: ${theme.font};
	border: none;
	outline: none;
	background: none;
  
  padding-left: 10px;
  padding-right: 10px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  outline: none;
	cursor: pointer;

  button {
    margin-left: auto;
  }

  &:hover, &:focus {
    color: var(--color_link);
  }

  &:first-child {
    margin-top: 10px;
  }
`

const CoAutors = props => {
	const { user } = useContext(UserContext);
	const { toast, jMb } = useContext(MessageBoxContext);

	const [deletingCoauthorId, setDeletingCoauthorId] = useState(-1)
	const [listCoauthors, setListCoauthors] = useState([])

	useEffect(() => {
		setListCoauthors(props.listCoauthors)
	}, [])



	// 
	const onAddCoauthor = coauthor => {

		JN.post(user, `problem/coauthor`, { problem_id: props.problemId, coauthor_id: coauthor.id },
			() => {
				let list = [...listCoauthors, coauthor]
				setListCoauthors(list)
				setFilteredItems([])
				setFilter('')
				props.updateCoauthors(list)
			},
			error => {
				toast(error.data.message, 'error')
			})
	}

	//2 
	const removeCoauthor = user_id => {
		JN.delete(user, `problem/coauthor`, { problem_id: props.problemId, coauthor_id: user_id },
		() => {
			let list = listCoauthors.filter(a => a.id !== user_id)
			setListCoauthors(list)
			props.updateCoauthors(list)
		},)
	}

	const [filter, setFilter] = useState('');
	const [filteredItems, setFilteredItems] = useState([]);
	const refFilteredProblems = [useRef(null), useRef(null), useRef(null), useRef(null), useRef(null)];

	const onFilterChanged = e => {
		let filterText = e.target.value

		if (filterText.length > 50)
			return

		setFilter(filterText)
		if (filterText === '') {
			setFilteredItems([])
			return
		}

		JN.get(user, `user/search`, { username: filterText },
			(data) => {
				setFilteredItems(data.users.slice(0, 5));
			},
			() => setFilteredItems([]))
	}

	const onHideFilter = (e) => {
		if (e.currentTarget.contains(e.relatedTarget))
			return

		setFilteredItems([])

		if (filter !== '') {
			setFilter('')
		}
	}

	const onKeyDown = e => {
		if (!filteredItems.length)
			return

		let ARROW_UP = 38;
		let ARROW_DOWN = 40;
		if (e.keyCode !== ARROW_UP && e.keyCode !== ARROW_DOWN)
			return;

		e.preventDefault();

		let up = e.keyCode === ARROW_UP;

		let activeElement = document.activeElement;
		let nextIndex = 0;

		let i = refFilteredProblems.findIndex(ref => ref.current === activeElement);

		if (i === -1 || i === 0) {
			nextIndex = up ? filteredItems.length - 1 : i + 1;
		}
		else if (i === filteredItems.length - 1) {
			nextIndex = up ? i - 1 : 0;
		}
		else {
			nextIndex = up ? i - 1 : i + 1;
		}

		if (refFilteredProblems[nextIndex] && refFilteredProblems[nextIndex].current) {
			console.log(refFilteredProblems[nextIndex].current)
			refFilteredProblems[nextIndex].current.focus()
		}
	}

	return (
		<div style={{ height: 500 }}>
			{/* {props.problemId} */}

			<div onBlur={onHideFilter} style={{ display: 'flex', justifyContent: 'flex-end', marginLeft: 'auto' }}>
				<Input focus title=" " onKeyDown={onKeyDown} fontSize="14px" onChange={onFilterChanged} value={filter} placeholder="  ,   ">
					{filter !== '' &&
						<FilterContainer onKeyDown={onKeyDown}>
							{!filteredItems.length ?
								<div>

								</div>
								:
								<>
									{filteredItems.map((user, index) =>
										<FilterItem onClick={() => onAddCoauthor(user)} ref={refFilteredProblems[index]} key={user.id}>
											<span style={{ overflow: 'hidden', textOverflow: 'ellipsis', display: 'block' }}><B colorInherit>{user.username}</B></span>
											<div>{user.name} {user.surname}</div>
										</FilterItem>
									)}
								</>
							}
						</FilterContainer>
					}
				</Input>
			</div>




			{listCoauthors.length > 0 && 
			<><B style={{ display: 'block', textAlign: 'left', marginTop: 30 }}></B>
			<DivTags>

				{listCoauthors.map(author =>
					<span key={author.id}>
						<DivTag>
							{deletingCoauthorId === author.id ?
								<LoadingSvg dots />
								:
								<label><A hover="true" to={`/profile/${author.username}`}>{author.username}</A></label>}


							<StyledButton type="button" onClick={() => jMb('   ?', () => removeCoauthor(author.id))}>
								{deletingCoauthorId !== author.id && <MdDelete color="var(--color_red)" size="20" />}
							</StyledButton>
						</DivTag>
					</span>
				)}

			</DivTags>
			</>}







		</div>
	)
}

export default CoAutors