import React, { useState, useEffect, useContext, useRef } from 'react'
import styled, { css } from 'styled-components'
import { theme } from 'Themes'
import JN from 'JN'
import SubmissionPreviewDlg from 'pages/submissions/SubmissionPreviewDlg';
import { Get_Number } from 'Global_Functions'
import LoadingSvg from 'components/LoadingSvg';
import {Link, NavLink } from 'react-router-dom'
import { MdKeyboardArrowLeft, MdKeyboardArrowRight, MdAccessTime, MdMemory } from 'react-icons/md';
import { Get_PaginationArray, Convert_date_to_string } from 'Global_Functions'
import { MessageBoxContext } from 'contexts/MessageBoxContext'
import { UserContext } from 'contexts/UserContext'
import H3 from 'controls/H3';
import B from 'controls/B';
import BoldA from 'controls/BoldA';
import PaginationContainer from 'controls/pagination/PaginationContainer';

const Section = styled.section `
  text-align: left;
  margin-top: 50px;
  
  @media (max-width: ${theme.mobileSize}) {
    & {
      margin-top: 50px;
      margin-left: 5px;
      margin-right: 5px;
    }
    h3 {
    margin-left: 5px;
  }
  }
`

const MainDiv = styled.section `
  width: 100%;
  display: flex;
  flex-direction: column;
  margin: auto;
  margin-top: 0;
  border-radius: 8px;
  padding-left: 5px;
  padding-right: 5px;
  overflow: auto;
  max-height: calc(100vh - 180px);
  overflow: auto;

  @media (max-width: ${theme.mobileSize}) {
    & {
      margin-left: 5px;
      padding-left: 0px;
      padding-right: 10px;
      max-height: none;
    }
  }
`

const Submission = styled.div `
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */

  box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.2);
  width: 100%;
  background-color: var(--color_b2);
  padding: 12px;
  padding-top: 8px;
  padding-bottom: 10px;
  margin-bottom: 10px;
  &:last-child {
    margin-bottom: 5px;
  }
  border-radius: 5px;
  text-decoration: none;
  color: var(--color_c2);
  
  border: 3px solid transparent;

  &:hover {
    transition: border ease 250ms;
    border: 3px solid var(--color_primary);
  }

  cursor: pointer;
  * {
    cursor: pointer;
  }
`

const Success = styled.span `
  color: var(--color_c2);

  ${props => props.code === 0 && css`
    color: var(--color_green);
  `}

  ${props => props.code === 1 && css`
    color: var(--color_red);
  `}
`

const Submissions = props => {
  const { user } = useContext(UserContext)
  const { match } = props
  const { toast } = useContext(MessageBoxContext)
  const refStart = useRef(null);

  const [state, setState] = useState({
    submissions: [],
    isLoading: true,
    open_submission: false,
    opened_submission_id: -1,
    pagination_pages: {},
  })

  useEffect(() => {
    setState({
      ...state,
      isLoading: true,
    })

    JN.get(user, `submissions/${match.params.userName}/${match.params.page ? match.params.page : 1}`, {},
    (data) => {

      if (refStart.current) {

        let mobileWidth = theme.mobileSize;
        mobileWidth.slice(0, -2);

        if (window.innerWidth <= parseInt(mobileWidth)) {
          window.scrollTo(0, refStart.current.offsetTop);
        }
        else {
          refStart.current.scrollTop = 0;
        }
      }

      setState({
        ...state,
        submissions: data.submissions,
        isLoading: false,
        pagination: {current: match.params.page ? match.params.page : 1, total: data.page_number}
      })
    },
    (error) => {
      toast(error.message, 'error')
      setState({...state, isLoading: false})
    })
  }, [match.url])

  const on_submition_clicked = id => e => {
    setState({
      ...state,
      open_submission: true,
      opened_submission_id: id,
    })
  }

  const on_submissionsPreviewDlg_close = () => {
    setState({
      ...state,
      open_submission: false,
    })
  }

  if (!state.submissions.length) {
    return (
      <div style={{margin: 'auto'}}>
        {/* <LoadingSvg /> */}
      </div>
    )
  }

  return (

    <Section>
      <H3></H3>
      <MainDiv ref={refStart}>
        {state.submissions.map((submission, index) => 
          <Submission key={submission.id} onClick={on_submition_clicked(submission.id)}>
            <div style={{marginBottom: 5, display: 'flex', justifyContent: 'space-between'}}>
              <span>{submission.id} - <BoldA to={`/problem/${submission.problem_id}`}>{submission.problem_id}. {submission.problem_title}</BoldA></span>
              <span style={{color: 'var(--color_c3)', fontSize: 14}}>{submission.language}</span>
            </div>
            <div style={{marginBottom: 5, display: 'flex', justifyContent: 'space-between'}}>
              <Success code={submission.verdict_code}>{submission.verdict_ge}</Success>
              <div style={{display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                {submission.time} <span style={{color: 'var(--color_c3)', fontSize: 14, marginLeft: 3}}></span>
              </div>
            </div>
            <div style={{display: 'flex', justifyContent: 'space-between'}}>
              <span style={{color: 'var(--color_c3)', fontSize: 14}}>{Convert_date_to_string(submission.datetime)}</span>
              <div style={{display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                {submission.memory} <span style={{color: 'var(--color_c3)', fontSize: 14, marginLeft: 3}}></span>
              </div>
            </div>
          </Submission>
        )}
      </MainDiv>

      <div style={{paddingLeft: 5, paddingRight: 5, marginTop: 15}}>
        <PaginationContainer noBackground isLoading={state.isLoading} to={`/profile/${match.params.userName}/:page`} pagination={state.pagination} />
      </div>

    
      
      {state.open_submission && 
         <SubmissionPreviewDlg id={state.opened_submission_id} close={on_submissionsPreviewDlg_close} />
       }
    </Section>
  )
}

export default Submissions
