import React, { userState} from 'react'
import styled from 'styled-components'
import { theme } from 'Themes'
import { Link } from "react-router-dom";

const StyledLink = styled(Link) `
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */

  color: var(--color_headerText);
  font-weight: 500;
  text-decoration: none;
  font-family: ${theme.fontCaps};
  font-size: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  /* padding: 1em 1em; */
  height: 50px;
  padding-left: 16px;
  padding-right: 16px;
  margin-right: 5px;

  &:first-child {
    margin-right: 0;
    padding-left: 10px;
    padding-right: 10px;
  }

  &:last-child {
    margin-right: 0;
  }

  border: none;
  outline: none;
  background-color: ${props => props.bg ? props.bg : 'none'};
  margin-left: ${props => props.margin_left ? props.margin_left : '0'};

  &:hover {

    span {
      transition: opacity ease 200ms;
      opacity: 1;
    }
    svg {
    transition: transform ease 250ms;
  }

 
    svg {
      transform: rotate(25deg);
    }
    box-shadow: inset 0 0 28px rgba(0, 0, 0, .3);
    /* transition: background-color ease 200ms; */
    /* color: var(--color_headerHover); */
    /* background-color: var(--color_headerHover); */

  }

  @media (max-width: ${theme.mobileSize}) {
    margin: 0;
    display: none;
    padding-left: 10px;
    padding-right: 10px;

    &:first-child, :nth-last-child(1), :nth-last-child(2) {
      display: flex;
    }
  }
`

const StyledSpan = styled.span `
  font-weight: 900;
  opacity: 0;
  color: ${theme.secondary};
`

function HeaderButton(props) {
  return (
    <StyledLink {...props} color={props.color} onClick={props.onClick} to={props.to} margin_left={props.margin_left}>
      {/* <StyledSpan>{"<"}</StyledSpan> */}
      {props.children}
      {/* <StyledSpan>{" />"}</StyledSpan> */}
      
    </StyledLink>
  )
}

export default HeaderButton