import React, { useState } from 'react'
import Toggle from 'controls/Toggle'
import styled from 'styled-components'
import { theme } from 'Themes'
import Button from 'controls/Button'
import { MdAdd } from 'react-icons/md'

const SButton = styled.button `
  position: relative;
  font-family: ${theme.fontCaps};
  font-size: 14px;
  cursor: pointer;
  padding: .5em 1em;
  padding-left: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  outline: none;
  background-color: ${theme.green};
  color: white;
  border-radius: 5px;

  &::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(255, 255, 255, 0.15);
    
    transition: opacity ease 250ms;
    opacity: 0;
  }

  &:hover {
      /* color: red; */
      &::after {
        opacity: 1;
      }
  }
`

const SContainer = styled.div `
  width: 450px;
  display: flex;
  flex-direction: column;
  padding: 25px;
  padding-top: 25px;
  /* border: 1px solid ${theme.gray}; */
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.4);
  margin: auto;
  
  background-color: #ffffff;
  /* box-shadow:  0 0 3px #b8b8b8; */
  border-radius: 8px;
`

const SHeader = styled.div `
  display: flex;
  margin-bottom: 10px;
`

const SToggle = styled.div `
  display: flex;
  justify-content: space-between;
  padding: 8px;
  border: 1px solid transparent;
  border-radius: 5px;

  &:nth-child(even) {
    background-color: #fafafa;
  }
`

const Permissions = props => {

  return (
    <SContainer>
      <span style={{color: theme.red, fontSize: '14px', marginBottom: '15px'}}>.  .     :3</span>
      <SHeader>
        <SButton backgroundColor="green"><MdAdd color="white" size="22" /> </SButton>
      </SHeader>
      <SToggle><Toggle title=" " right /></SToggle>
      <SToggle><Toggle title=" " right /></SToggle>
      <SToggle><Toggle title=" " right /></SToggle>
      <SToggle><Toggle title=" " right /></SToggle>
      <SToggle><Toggle title=" " right /></SToggle>
      <SToggle><Toggle title=" " right/></SToggle>
      <SToggle><Toggle title=" " right /></SToggle>
      <SToggle><Toggle title=" " right /></SToggle>
      <SToggle><Toggle title=" " right /></SToggle>
      <SToggle><Toggle title=" " right /></SToggle>
      <SToggle><Toggle title=" " right /></SToggle>
      <SToggle><Toggle title=" " right /></SToggle>
    </SContainer>
  )
}

export default Permissions