import React, { useState, useEffect, useContext } from 'react'
import Modal from 'components/Modal'
import Input from 'controls/Input'
import Button from 'controls/Button'
import ContainerFooter from 'controls/ContainersFooter'
import JN from 'JN'
import { UserContext } from 'contexts/UserContext'
import { MessageBoxContext } from 'contexts/MessageBoxContext'

const FolderEditDlg = props => {
  const { match } = props;
  const { user }  = useContext(UserContext);
  const { toast } = useContext(MessageBoxContext);

  const [state, setState] = useState({
    folder: {id: -1, name: "", parent_dir_id: 1},
    isLoading: false,
  })

  useEffect(() => {
    const folder = props.folder.id !== -1 ? props.folder : {id: -1, name: "", parent_dir_id: props.folder.parent_dir_id}
    setState({...state, folder: folder});
  }, [props.folder]);

  const onChange = (e) => {
    setState({
      ...state,
      folder: {...state.folder, [e.target.name]: e.target.value},
    })
  };

  const setOpen = (isOpen) => {
    if (state.isLoading) {
      return
    }
    props.close(isOpen)
  }

  const submit = e => {
    e.preventDefault()
    // 
    if (state.folder.id === -1) {
      
      setState({...state, isLoading: true});
      JN.post(user, `admin/tutorial_tree`, state.folder,
      (data) => {
        toast(' ', 'success');
        props.submit(true, {...state.folder, id: data.id});
      },
      (error) => {
        if (error.status === 400)
          toast('   ', 'error');
        setState({...state, isLoading: false});
      })
    }
    //
    else {
      setState({...state, isLoading: true});
      console.log(state.folder);
      JN.put(user, 'admin/tutorial_tree', {...state.folder, new_name: state.folder.name},
      (data) => {
        toast(' ', 'success')
        props.submit(false, {...state.folder, id: data.id});
      },
      (error) => {
        if (error.status === 400)
          toast('   ', 'error');
        setState({...state, isLoading: false});
      })
    }
  }

  return (
    <Modal width="450px" title={state.folder.id === -1 ? " " : " "} isOpen={props.isOpen} close={setOpen}>
      <form onSubmit={submit}>
        <Input required focus title="" name="name" value={state.folder.name} onChange={onChange}/>
        <ContainerFooter>
          <Button disabled={state.isLoading} backgroundColor="primary">{state.isLoading ? '..' : ''}</Button>
        </ContainerFooter>
      </form>
    </Modal>
  )
}

export default FolderEditDlg
