import React from 'react'
import { theme } from 'Themes'
import p404 from 'images/p404.svg'
import styled from 'styled-components'
import A from 'controls/A'
import { FaBook, FaStar, FaTrophy, FaBrain } from 'react-icons/fa'
import { Link } from 'react-router-dom'

const SA = styled(Link) `
  text-decoration: none;
  color: ${props => props.color};
`

const CartContainer = styled.div `
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-column-gap: 12px;
  grid-row-gap: 12px;
  width: 650px;
  margin: auto;
`

const Cart = styled.div `
  border-radius: 8px;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.4);
  /* background-color: #f7f7f7; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2em 3em;

  transition: box-shadow ease 250ms;
  &:hover {
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.4);
  }

  &:active {
    box-shadow: inset 1px 1px 3px rgba(0, 0, 0, 0.4);
  }
`


const Container_Main = styled.div `
  display: flex;
  flex-direction: column;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: white;
`

const Content = styled.div `
  padding: 25px;
  width: 100%;
`

const page_404 = props => {

  return (
    <Container_Main>

      <CartContainer>
        
        <SA to="/problems" color={theme.primary}>
          <Cart>
            <FaBrain size="42" />
            <span style={{marginTop: '10px'}}></span>
          </Cart>
        </SA>

        <SA to="/contests" color={theme.red}>
          <Cart>
            <FaTrophy size="42" />
            <span style={{marginTop: '10px'}}></span>
          </Cart>
        </SA>

        <SA to={`/rating`} color={theme.orange}>
          <Cart>
            <FaStar size="42" />
            <span style={{marginTop: '10px'}}></span>
          </Cart>
        </SA>

        <SA to="/tutorials" color={theme.green}>
          <Cart>
            <FaBook size="42" />
            <span style={{marginTop: '10px'}}></span>
          </Cart>
        </SA>

        
      </CartContainer>
      <Content>
        <img style={{margin: 'auto'}} src={p404} height="400px" />
      </Content>
      
    </Container_Main>
  )
}

export default page_404