import React, { useReducer, createContext } from 'react'
import { UserReducer, LOG_IN, LOG_OUT, USER_UPDATE } from './UserReducer'
import JN from 'JN'

export const UserContext = createContext({})

export const UserProvider = props => {

  const initialState = null

  const [state, dispatch] = useReducer(UserReducer, initialState)

  const login = user => {
    dispatch({
      type: LOG_IN,
      user: {
        ...user,
        favourites: [
          { id: 1, title: ' 1' },
          { id: 2, title: ' 2' },
          { id: 3, title: ' 3' },
          { id: 4, title: ' 4' },
          { id: 5, title: ' 5' },
        ],
        update_user: (newUser) => userUpdate(newUser),
        update_token: (callAfterFunction, user, url, data, on_finished, on_error) => {
          let temp = user;
          temp.access_token = user.refresh_token
          JN.post(temp, 'refresh', {}, (d) => {
            let updatedUser = { ...user, access_token: d.access_token }
            userUpdate(updatedUser)
            console.log('~token updated')

            callAfterFunction(updatedUser, url, data, on_finished, on_error)

          }, (error) => console.log('#error refreshing token:', error))
        }
      },
    })

  }

  const logout = () => {
    const user = state
    JN.post(user, 'logout', {},
      () => {
        JN.post({ access_token: user.refresh_token }, 'logout2', {})
      })
    dispatch({ type: LOG_OUT })
  }

  const userUpdate = user => {
    dispatch({ type: USER_UPDATE, user: user });
  }


  //.         .  .      
  const refreshGlobalState = () => {
    dispatch({ type: USER_UPDATE, user: state });
  }

  return (
    <UserContext.Provider value={{
      user: state,
      login: login,
      logout: logout,
      userUpdate: userUpdate,
      refreshGlobalState: refreshGlobalState
    }}>
      {props.children}
    </UserContext.Provider>
  )
}