import React from 'react'
import styled, { css } from 'styled-components'
import { theme } from 'Themes'
import { MdCheckBox, MdCheckBoxOutlineBlank, MdCheck, MdClear } from 'react-icons/md'
import { FaTimes } from 'react-icons/fa'

const Container = styled.div `
  display: inline-flex;
  font-size: 14px;
  justify-content: flex-start;
  align-items: center;

  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */

  cursor: pointer;
  * {
    cursor: pointer;
  }

  label {
    margin-left: 5px;
    font-size: 14px;
  }

  ${props => props.disabled && css `
    pointer-events: none;
  `}
`

const Checkbox = props => {

  const on_check_changed = e => {
    e.preventDefault()
    
    if (props.onCheckedChanged) {
      props.onCheckedChanged()
    }
  }

  return (
    <Container disabled={props.disabled} onClick={props.onClick ? props.onClick : on_check_changed}>

      {props.checked ? 
        <MdCheck size={props.size ? props.size : "22"} color={props.disabled ? "var(--color_c3)" : props.color ? props.color : "var(--color_primary)"} />
        :
        props.disabled ? <MdClear size={props.size ? props.size : "22"} color={"var(--color_c3)"} /> : <MdCheckBoxOutlineBlank size={props.size ? props.size : "22"} color={"var(--color_c2)"} />
      }

      <label>{props.title}</label>
    </Container>
  )
}

export default Checkbox