import React, { useState, useEffect, useContext, useRef } from 'react'
import JN from 'JN'
import Container from 'controls/Container';
import TextArea from 'controls/TextArea';
import styled, { css } from 'styled-components'
import { theme } from 'Themes'
import Input from 'controls/Input';
import { MessageBoxContext } from 'contexts/MessageBoxContext'
import { UserContext } from 'contexts/UserContext'
import { MdDelete, MdEdit, MdCheckBoxOutlineBlank, MdAdd, MdCheck, MdRemoveRedEye } from 'react-icons/md';
import ContainerFooter from 'controls/ContainersFooter';
import Button from 'controls/Button';
import CategoryEditDlg from 'pages/problems/tags/CategoryEditDlg';
import Helmet from 'react-helmet';
import Button_Float from 'controls/Button_Float';
import { FaCheck, FaTimes } from 'react-icons/fa';
import { Redirect } from 'react-router-dom'
import A from 'controls/A';
import Checkbox from 'controls/Checkbox';

const Header = styled.div `
  text-align: left;
  margin: auto;
  margin-top: 2em;
  margin-bottom: 8px;
  border-bottom: none;
  width: 100%;
  overflow: hidden;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  display: flex;
  justify-content: space-between;
`

const Tab_Container = styled.div `
  display: flex;
`

const Tab_Action = styled.button `
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */

  cursor: pointer;
  border: none;
  outline: none;
  text-decoration: none;
  font-size: 14px;
  font-family: ${theme.fontCaps};
  border: 1px solid var(--color_inputBackground);
  padding: .8em 1.5em;
  outline: none;
  height: 50px;
  border-top-left-radius: 5px;
  border-radius: 5px;
  margin-right: 5px;
  margin-bottom: 50px;
  color: var(--color_c2);
  background-color: transparent;
  cursor: pointer;

  &:disabled {
    background: none;
    color: var(--color_c3);
    border: 1px solid transparent;
  }
`

const Samples_Container = styled.div `
  display: flex;
  text-align: left;
  flex-direction: column;
  width: 100%;
  height: 430px;
  overflow: auto;
  margin-bottom: 20px;
  border: 1px solid var(--color_b22);
  border-radius: 8px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
`

const Sample_Card = styled.div `
  font-size: 14px;
  display: flex;
  padding: 3px;
  padding-left: 10px;
  align-items: center;
  background-color: transparent;
  border-bottom: 1px solid var(--color_b22);

  &, * {
    cursor: pointer;
  }

  label {
    flex-grow: 1;
    margin-left: .5em;
  }

  &:hover {
    transition: background-color ease 250ms;
    background-color: var(--color_b11);

    button {
      transition: opacity ease 250ms;
      opacity: 1;
    }
  }
`
const StyledButton = styled.button `
  border: none;
  outline: none;
  background: none;
  cursor: pointer;
  opacity: 0;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  margin-left: 3px;
`

const FilterContainer = styled.div `
  position: absolute;
  top: 95px;
  /* background-color: var(--color_b3); */
  /* box-shadow: 0 1px 5px rgba(0, 0, 0, .5); */
  /* box-shadow: 0 3px 8px rgba(0, 0, 0, 0.2); */
  border-radius: 5px;
  overflow: auto;
  max-height: 260px;
  width: ${props => props.width ? props.width : '100%'};
  z-index: 1;
`

const FilterItem = styled.button `
  border: none;
  font-size: 14px;
  width: 100%;
  display: block;
  background-color: var(--color_inputBackground);
  cursor: pointer;

  ${props => props.flex && css `
    display: flex;
    justify-content: flex-start;  

    button {
      margin-left: auto;
    }
  `}

  text-decoration: none;
  color: var(--color_c2);
  font-family: ${theme.font};
  
  padding-left: 10px;
  padding-right: 10px;
  line-height: 50px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  outline: none;

  &:hover, &:focus {
    color: var(--color_link);
  }
`

const ProblemsDiv = styled.div `
  margin-top: 30px;
`

const ProblemItem = styled.div `
  display: flex;
  border-bottom: 1px solid var(--color_b22);
  padding: 5px;
  &:last-child {
    border-bottom: none;
  }

  button {
    cursor: pointer;
    border: none;
    outline: none;
    background: none;
    margin-left: auto;
    display: flex;
    color: var(--color_c2);

    &:hover {
      color: var(--color_red);
    }
  }
`

const AddTutorial = props => {
  const { match, history } = props;
  const { user }  = useContext(UserContext);
  const { toast } = useContext(MessageBoxContext);
  const [state, setState] = useState({
    tutorial: {
      title: '',
      body: '',
      note: '',
      directory_id: match.params.dirId ? match.params.dirId : 1,
      tags: [],
      problems: [],
      references: [],
      is_visible: false,
    },
    isLoading: false,
  });
  const [tags, setTags]     = useState([]);
  const [tab, setTab]       = useState(1);
  const [search, setSearch] = useState('');
  const onSearchChanged = e => {
    setSearch(e.target.value);
  }
  const [filter, setFilter] = useState('');
  const [filteredProblems, setFilteredProblems] = useState([]);

  useEffect(() => {

    JN.get(user, 'tags', {},
    (data) => {
      if (match.params.id === undefined)
        setState({...state, isLoading: false});
      setTags(data);
    },
    () => {
      if (match.params.id === undefined)
        setState({...state, isLoading: false});
    })

    if (match.params.id !== undefined) {
      JN.get(user, `tutorial/${match.params.id}`, {},
      (data) => {
        // console.log(data);
        let fList = [];
        for (let i=0; i<data.tags.length; i++) {
          fList.push(data.tags[i].id);
        }
        setState({...state, tutorial: {...data, id: match.params.id, tags: fList}});
      });
    }
    else if (match.params.dirId) {
      setState({...state, tutorial: {...state.tutorial, directory_id: match.params.dirId}});
    }

  }, []);

  const on_visible_changed = () => {
    setState({
      ...state,
      tutorial: {
        ...state.tutorial,
        is_visible: !state.tutorial.is_visible,
      }
    })
  }

  const change = e => {
    setState({...state, tutorial: {...state.tutorial, [e.target.name]: e.target.value}});
  }

  const submit = e => {
    e.preventDefault();
    updateTutorial();
  }

  // 
  const updateTutorial = (redirect_to_fullTutorial = false) => {
    // 
    if (state.tutorial.id === undefined) {
console.log(state.tutorial)
      setState({...state, isLoading: true});

      JN.post(user, `admin/tutorial`, state.tutorial,
      (data) => {
        toast(' ', 'success');
        setState({...state, isLoading: false, tutorial: {...state.tutorial, id: data.tutorial_id}});
      },
      () => {
        setState({...state, isLoading: false});
      })
    }

    // 
    else {
      setState({...state, isLoading: true});
      JN.put(user, `admin/tutorial`, state.tutorial,
      () => {
        if (redirect_to_fullTutorial) {
          history.push(`/tutorial/${state.tutorial.id}`);
          return;
        }
        toast(' ', 'success')

        setState({...state, isLoading: false});
      },
      () => {
        setState({...state, isLoading: false});
      })
    }
  }

  // 
  const onTagToggled = id => e => {
    e.preventDefault();
    // 
    if (!state.tutorial.tags.includes(id)) {
      setState({...state, tutorial: {...state.tutorial,
          tags: [...state.tutorial.tags, id]
        }
      });
    }
    // 
    else {
      setState({...state, tutorial: {...state.tutorial,
          tags: state.tutorial.tags.filter((tagId) => tagId !== id)
        }
      });
    }
  }

  const on_close_tagsEditDlg = () => {
    setState({...state, isOpen: false});
  }

  const on_open_tagsEditDlg = (e) => {
    e.preventDefault();
    setState({...state, editingCategory: null, isOpen: true});
  }

  const on_tag_edit = index => e => {
    e.preventDefault();
    e.stopPropagation();
    setState({...state, editingCategory: tags[index], isOpen: true});
  }

  // 
  const on_tag_delete = id => e => {
    e.preventDefault();
    e.stopPropagation();

    if (!window.confirm('  ?!')) {
      return;
    }

    setState({...state, deleting_tag_id: id});

    JN.delete(user, `admin/tags`, {tag_id: id},
    () => {
      toast(' ', 'success')
      setState({
        ...state,
        tutorial: {
          ...state.tutorial,
          tags: state.tutorial.tags.filter((tagId) => tagId !== id),
        },
        deleting_tag_id: -1
      })
      setTags(tags.filter((tag, i) => tag.id !== id));
    },
    () => {
      setState({...state, deleting_tag_id: -1});
    })
  }

  const onTagSubmited = (tag, isNew) => {
    if (isNew) {
      toast(' ', 'success');
      setState({...state, isOpen: false,
        tutorial: {...state.tutorial, tags: [...state.tutorial.tags, tag.id]}
      });
      setTags([...tags, tag]);
    }
    else {
      toast(' ', 'success');
      setState({...state, isOpen: false});
      setTags(tags.map((c) => {return c.id !== tag.id ? c : {...c, title: tag.title}}));
    }
  }

  const viewFullTutorialClicked = e => {
    e.preventDefault();
    updateTutorial(true);
  }

  const onFilterChanged = e => {
    let filterText = e.target.value

    if (filterText.length > 50)
      return

    setFilter(filterText)
    if (filterText === '') {
      setFilteredProblems([])
      return
    }

    JN.get(user, `problems/1`, {template: filterText},
    (data) => {
      setFilteredProblems(data.problems.slice(0, 5));
    },
    () => setFilteredProblems([]));
  }

  const onHideFilter = (e) => {
    if (e.currentTarget.contains(e.relatedTarget))
      return;

      setFilteredProblems([]);

    if (filter !== '') {
      setFilter('');
    }
  }

  const refFilteredProblems = [useRef(null), useRef(null), useRef(null), useRef(null), useRef(null)]

  const onKeyDown = e => {
    if (!filteredProblems.length)
      return

    let ARROW_UP   = 38;
    let ARROW_DOWN = 40;
    if (e.keyCode !== ARROW_UP && e.keyCode !== ARROW_DOWN)
      return;

    e.preventDefault();

    let up = e.keyCode === ARROW_UP;

    let activeElement = document.activeElement;
    let nextIndex = 0;

    let i = refFilteredProblems.findIndex(ref => ref.current === activeElement);

    if (i === -1 || i === 0) {
      nextIndex = up ? filteredProblems.length - 1 : i + 1;
    }
    else if (i === filteredProblems.length - 1) {
      nextIndex = up ? i - 1: 0;
    }
    else {
      nextIndex = up ? i - 1 : i + 1;
    }

    if (refFilteredProblems[nextIndex] && refFilteredProblems[nextIndex].current) {
      refFilteredProblems[nextIndex].current.focus();
    }
  }

  const onProblemAdd = problem => e => {
    e.preventDefault();
    setFilteredProblems([]);
    setFilter('');

    if (!state.tutorial.problems.some(p => p.id === problem.id)) {
      setState({...state, tutorial: {...state.tutorial, problems: [...state.tutorial.problems, {id: problem.id, title: problem.title}]}});
    }
    else {
      toast('  ', 'error');
      return;
    }
    
    JN.post(user, 'admin/tutorial/problem', {tutorial_id: state.tutorial.id, problem_id: problem.id}, null,
    (error) => {
      setState({...state, tutorial: {...state.tutorial, problems: state.tutorial.problems.filter(p => p.id !== problem.id)}});
      if (error.status === 403) {
        toast('  ', 'error');
      }
    });
  }

  const onProblemDelete = problem => e => {
    e.preventDefault();
    if (!window.confirm(`${problem.title}\n   ?`)) {
      return
    }

    setState({...state, tutorial: {...state.tutorial,
      problems: state.tutorial.problems.filter(p => p.id !== problem.id)
    }});

    JN.delete(user, 'admin/tutorial/problem', {tutorial_id: state.tutorial.id, problem_id: problem.id},
    null,
    error => {
      console.log(error);
      setState({...state, tutorial: {...state.tutorial,
        problems: [...state.tutorial.problems, problem]
      }});
    });
  }
  let currentTab = <>
    {state.isOpen && <CategoryEditDlg
      submited={onTagSubmited}
      category={state.editingCategory}
      isOpen={state.isOpen}
      close={on_close_tagsEditDlg}
    />}

    <div style={{display: 'flex',flexDirection: 'column'}}>
      <Input    title="" focus required name="title"  value={state.tutorial.title} onChange={change} />
      <TextArea showControls title="" required height="250px" name="body" value={state.tutorial.body} onChange={change} />
      <TextArea showControls title="" name="note" height="150px" value={state.tutorial.note} onChange={change} />

      <Header>
        <Button type="button" onClick={on_open_tagsEditDlg}>
          <MdAdd size="22" />  
        </Button>
        <Input fsontSize="14px" value={search} onChange={onSearchChanged} placeholder=".." width="250px" />
      </Header>
      <Samples_Container>
        {tags.map((tag, index) =>
          <span key={tag.id}>
          {tag.title.includes(search) &&
            <Sample_Card onClick={onTagToggled(tag.id)}>
              {state.deleting_tag_id === tag.id ? 
                <svg width="56" height="18" viewBox="0 0 120 30" xmlns="http://www.w3.org/2000/svg" fill="#fff"> <circle fill={loadingColor} cx="15" cy="15" r="15"> <animate attributeName="r" from="15" to="15" begin="0s" dur="0.8s" values="15;9;15" calcMode="linear" repeatCount="indefinite" /> <animate attributeName="fill-opacity" from="1" to="1" begin="0s" dur="0.8s" values="1;.5;1" calcMode="linear" repeatCount="indefinite" /> </circle> <circle fill={loadingColor} cx="60" cy="15" r="9" fillOpacity="0.3"> <animate attributeName="r" from="9" to="9" begin="0s" dur="0.8s" values="9;15;9" calcMode="linear" repeatCount="indefinite" /> <animate attributeName="fill-opacity" from="0.5" to="0.5" begin="0s" dur="0.8s" values=".5;1;.5" calcMode="linear" repeatCount="indefinite" /> </circle> <circle fill={loadingColor} cx="105" cy="15" r="15"> <animate attributeName="r" from="15" to="15" begin="0s" dur="0.8s" values="15;9;15" calcMode="linear" repeatCount="indefinite" /> <animate attributeName="fill-opacity" from="1" to="1" begin="0s" dur="0.8s" values="1;.5;1" calcMode="linear" repeatCount="indefinite" /> </circle> </svg>
              :
              state.tutorial.tags.includes(tag.id) ? <MdCheck size="20" color="var(--color_primary)" /> : <MdCheckBoxOutlineBlank size="20" color="var(--color_c3)" />}
              {state.deleting_tag_id !== tag.id && <label>{tag.title}</label>}
              <StyledButton type="button" onClick={on_tag_edit(index)}>
                {state.deleting_tag_id !== tag.id && <MdEdit color="var(--color_orange)" size="20" />}
              </StyledButton>
              <StyledButton type="button" onClick={on_tag_delete(tag.id)}>
                {state.deleting_tag_id !== tag.id && <MdDelete color="var(--color_red)" size="20" />}
              </StyledButton>
            </Sample_Card>}
          </span>
        )}
      </Samples_Container>
      <div style={{marginTop: '0px', textAlign: 'right'}}>
        <Checkbox title=" " checked={state.tutorial.is_visible} onCheckedChanged={on_visible_changed} />
      </div>
      <ContainerFooter>
        <Button type="submit" onClick={submit} backgroundColor="primary" disabled={state.isLoading}>
          {state.isLoading ? ".." : state.tutorial.id ? "" : ""}
        </Button>
      </ContainerFooter>
    </div>
  </>;

  if (tab === 2) {
    currentTab =
      <div style={{minHeight: '450px'}}>
      <div style={{position: 'relative'}} onBlur={onHideFilter}>
        <Input focus title=" " placeholder="    " onKeyDown={onKeyDown} fontSize="14px" onChange={onFilterChanged} value={filter} />
          {filter !== '' &&
            <FilterContainer onKeyDown={onKeyDown}>
              {!filteredProblems.length ? <div></div> :
              <>
                {filteredProblems.map((problem, index) =>
                  <FilterItem flex="true" ref={refFilteredProblems[index]} key={problem.id} onClick={onProblemAdd(problem)}>
                    {user && <>
                      {problem.verdict === 1 ?
                        <span style={{display: 'flex', alignItems: 'center'}}>
                          <FaCheck color="var(--color_green)" size="12" />&nbsp;
                        </span>
                        : 
                        problem.verdict === 2 ?
                        <span style={{display: 'flex', alignItems: 'center'}}>
                          <FaTimes color="var(--color_red)" size="12" />&nbsp;
                        </span>
                        : <span style={{width: '18px'}}></span>
                      }
                    </>}
                    
                    {problem.id}.&nbsp;<span style={{overflow: 'hidden', textOverflow: 'ellipsis'}}>{problem.title}</span>
                  </FilterItem>
                )}
              </>
              }
            </FilterContainer>
          }
      </div>
      {state.tutorial.problems.length > 0 &&
        <ProblemsDiv>
          {state.tutorial.problems.map(problem =>
            <ProblemItem style={{textAlign: 'left'}} key={problem.id}>
              <A hover="true" to={`/problem/${problem.id}`}><span style={{width: 28, marginRight: 5}}>{problem.id}</span> {problem.title}</A><button onClick={onProblemDelete(problem)}><FaTimes /></button>
            </ProblemItem>
          )}
        </ProblemsDiv>
      }
      
      </div>
  }

  const [linkedTutorialId, setLinkedTutorialId] = useState('');
  const onLinkedTutorialIdChanged = e => {
    setLinkedTutorialId(e.target.value);
  }
  const onTutorialAdd = e => {
    e.preventDefault();

    if (state.tutorial.references.some(r => r.id == linkedTutorialId)) {
      toast('  ', 'error');
      return;
    }

    JN.post(user, 'admin/tutorial/reference', {tutorial_id: state.tutorial.id, reference_id : linkedTutorialId},
    (data) => {
      setState({...state, tutorial:  {...state.tutorial,
          references: [...state.tutorial.references, {id: data.reference.id, title: data.reference.title}]
        }
      });
      setLinkedTutorialId('');
    }
    ,
    (error) => {
      if (error.status === 403) {
        toast('  ', 'error');
      }
      else if (error.status === 404) {
        toast('   ', 'error');
      }
    });
  }

  const onReferenceDelete = reference => e => {
    e.preventDefault();
    if (!window.confirm(`${reference.title}\n   ?`)) {
      return
    }
    JN.delete(user, 'admin/tutorial/reference', {tutorial_id: state.tutorial.id, reference_id : reference.id},
    () => {
      setState({...state, tutorial: {...state.tutorial, references: state.tutorial.references.filter(ref => ref.id !== reference.id)}});
    },
    (error) => {
      toast('', 'error');
      console.log(error);
    });
  }

  if (tab === 3) {
    currentTab =
      <div style={{minHeight: '450px'}}>
        <form onSubmit={onTutorialAdd}>
          <Input focus title=" " placeholder="     Enter-" fontSize="14px" value={linkedTutorialId} onChange={onLinkedTutorialIdChanged} />
        </form>

        {state.tutorial.references.length > 0 &&
        <ProblemsDiv>
          {state.tutorial.references.map(reference =>
            <ProblemItem style={{textAlign: 'left'}} key={reference.id}>
              <A hover="true" to={`/tutorial/${reference.id}`}><span style={{width: 28, marginRight: 5}}>{reference.id}</span> {reference.title}</A><button onClick={onReferenceDelete(reference)}><FaTimes /></button>
            </ProblemItem>
          )}
        </ProblemsDiv>
        }
      </div>;
  }

  const on_tab_changed = index => e => {
    e.preventDefault();
    setTab(index);
  }

  const Tab_Current = {backgroundColor: 'var(--color_inputBackground)', color: 'color(--color_c2)'}
  const loadingColor = "var(--color_primary)";

  if (!user || user.permission === "user") {
    return (
      <Redirect to="/404" />
    )
  }

  return (
    <Container title={` ${match.params.id ? '' : ''}`} width="800px">
      <Helmet>
        <meta charSet="utf-8" />
        <title> {match.params.id ? '' : ''}</title>
        <meta name="description" content="AiaSoft" />
      </Helmet>

      <Tab_Container>
        <Tab_Action style={tab === 1 ? Tab_Current : {}} onClick={on_tab_changed(1)}></Tab_Action>
        <Tab_Action style={tab === 2 ? Tab_Current : {}} disabled={!state.tutorial.id} onClick={on_tab_changed(2)}></Tab_Action>
        <Tab_Action style={tab === 3 ? Tab_Current : {}} disabled={!state.tutorial.id} onClick={on_tab_changed(3)}> </Tab_Action>
      </Tab_Container>

      {currentTab}

      {state.tutorial.id && 
        <Button_Float onClick={viewFullTutorialClicked} count="1" to={`/tutorial/${state.tutorial.id}`}>
          <MdRemoveRedEye color="var(--color_primary)" size="22" />
        </Button_Float>
      }
    </Container>
  )
}

export default AddTutorial