import React, { useState, useEffect, useContext, useRef } from 'react'
import { UserContext } from 'contexts/UserContext'
import JN from 'JN'
import Table, { Tr, Th, Td, Table_A } from 'controls/Table'
import PaginationContainer from 'controls/pagination/PaginationContainer'
import Input from 'controls/Input'
import Helmet from 'react-helmet'
import { theme } from 'Themes'
import styled, { css } from 'styled-components'
import { Link } from 'react-router-dom'
import B from 'controls/B'

const MDiv = styled.div`
  padding: 56px;
  padding-top: -0px;
  margin: auto;
  margin-top: -56px;
  max-width: 900px;
  @media (max-width: ${theme.mobileSize}) {
    padding: 0;
  }
`

const FilterContainer = styled.div`
  position: absolute;
  top: 55px;
  background-color: var(--color_b2);
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  overflow: auto;
  max-height: 250px;
  width: ${props => props.width ? props.width : '100%'};
  z-index: 1;
`

const FilterItem = styled(Link)`
  display: block;
  text-align: left;
  height: 45px;
  text-decoration: none;
  color: var(--color_c2);
  font-family: ${theme.font};
  
  padding-left: 10px;
  padding-right: 10px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  outline: none;

  button {
    margin-left: auto;
  }

  &:hover, &:focus {
    color: var(--color_link);
  }

  &:first-child {
    margin-top: 10px;
  }
`

const RatingPage = props => {
  const { user } = useContext(UserContext);
  const { match } = props;
  const [state, setState] = useState({
    pagination: [1, 2, 3, 4],
    is_loading: true,
  });
  const [users, setUsers] = useState([]);

  useEffect(() => {

    setState({ ...state, is_loading: true })
    window.scroll(0, 0);
    JN.get(null, `rating/${match.params.page}`, {},
      (data) => {
        setUsers(data.users)
        setState({ ...state, is_loading: false, pagination: { current: match.params.page, total: data.page_number } })
      },
      (error) => {
        console.log(error)
        setState({ ...state, is_loading: false })
      })
  }, [match.url])

  const [filter, setFilter] = useState('');
  const [filteredItems, setFilteredItems] = useState([]);
  const refFilteredProblems = [useRef(null), useRef(null), useRef(null), useRef(null), useRef(null)];

  const onFilterChanged = e => {
    let filterText = e.target.value

    if (filterText.length > 50)
      return

    setFilter(filterText)
    if (filterText === '') {
      setFilteredItems([])
      return
    }

    JN.get(user, `user/search`, { username: filterText },
      (data) => {
        setFilteredItems(data.users.slice(0, 5));
      },
      () => setFilteredItems([]))
  }

  const onHideFilter = (e) => {
    if (e.currentTarget.contains(e.relatedTarget))
      return

    setFilteredItems([])

    if (filter !== '') {
      setFilter('')
    }
  }

  const onKeyDown = e => {
    if (!filteredItems.length)
      return

    let ARROW_UP = 38;
    let ARROW_DOWN = 40;
    if (e.keyCode !== ARROW_UP && e.keyCode !== ARROW_DOWN)
      return;

    e.preventDefault();

    let up = e.keyCode === ARROW_UP;

    let activeElement = document.activeElement;
    let nextIndex = 0;

    let i = refFilteredProblems.findIndex(ref => ref.current === activeElement);

    if (i === -1 || i === 0) {
      nextIndex = up ? filteredItems.length - 1 : i + 1;
    }
    else if (i === filteredItems.length - 1) {
      nextIndex = up ? i - 1 : 0;
    }
    else {
      nextIndex = up ? i - 1 : i + 1;
    }

    if (refFilteredProblems[nextIndex] && refFilteredProblems[nextIndex].current) {
      refFilteredProblems[nextIndex].current.focus();
    }
  }

  return (
    <MDiv>
      <Helmet>
        <meta charSet="utf-8" />
        <title>{match.params.page == 1 ? '' : ` . ${match.params.page}`}</title>
        <meta name="description" content="AiaSoft" />
      </Helmet>
      <div onBlur={onHideFilter} style={{ display: 'flex', justifyContent: 'flex-end', marginLeft: 'auto', width: '300px' }}>
        <Input reverse onKeyDown={onKeyDown} fontSize="14px" onChange={onFilterChanged} value={filter} placeholder="..">
          {filter !== '' &&
            <FilterContainer onKeyDown={onKeyDown}>
              {!filteredItems.length ?
                <div>

                </div>
                :
                <>
                  {filteredItems.map((user, index) =>
                    <FilterItem ref={refFilteredProblems[index]} key={user.id} to={`/profile/${user.username}`}>
                      <span style={{ overflow: 'hidden', textOverflow: 'ellipsis', display: 'block', marginBottom: -7 }}><B colorInherit>{user.username}</B></span>
                      <div>{user.name} {user.surname}</div>
                    </FilterItem>
                  )}
                </>
              }
            </FilterContainer>
          }
        </Input>
      </div>
      <div style={{ boxShadow: '0 6px 16px 0 rgba(0,0,0,.2)', maxWidth: '900px', margin: 'auto', marginTop: '45px', borderRadius: '8px', paddingTop: '0' }}>
        <Table count={state.pagination.total > 1 && !state.is_loading}>
          <thead></thead>
          <tbody>
            {!state.is_loading ?
              <tr>
                <Th></Th>
                <Th autoSize></Th>
                <Th></Th>
                <Th></Th>
                <Th></Th>
              </tr>
              :
              <tr>
                <Th></Th>
                <Th width="90%"></Th>
                <Th></Th>
                <Th></Th>
                <Th></Th>
              </tr>}
            {!state.is_loading && !users.length &&
              <tr><td colSpan="5" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}>   <span style={{ color: 'red' }}>!</span></td></tr>
            }

            {state.is_loading &&
              <>
                <tr><td colSpan="5" style={{ fontSize: '14px', textAlign: 'left' }}></td></tr>
                <tr><td colSpan="5" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></tr>
                <tr><td colSpan="5" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></tr>
                <tr><td colSpan="5" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></tr>
                <tr><td colSpan="5" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></tr>
                <tr><td colSpan="5" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></tr>
                <tr><td colSpan="5" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></tr>
                <tr><td colSpan="5" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></tr>
                <tr><td colSpan="5" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></tr>
                <tr><td colSpan="5" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></tr>
                <tr><td colSpan="5" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></tr>
                <tr><td colSpan="5" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></tr>
                <tr><td colSpan="5" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></tr>
                <tr><td colSpan="5" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></tr>
                <tr><td colSpan="5" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></tr>
                <tr><td colSpan="5" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></tr>
                <tr><td colSpan="5" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></tr>
                <tr><td colSpan="5" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></tr>
                <tr><td colSpan="5" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></tr>
              </>
            }

            {!state.is_loading && users.map((user, index) =>
              <tr key={user.username}>
                <Td><Table_A to={`/profile/${user.username}`}>{user.rank}</Table_A></Td>
                <Td><Table_A to={`/profile/${user.username}`}>{user.username}</Table_A></Td>
                <Td low="true"><Table_A to={`/profile/${user.username}`}>{user.name}</Table_A></Td>
                <Td low="true"><Table_A to={`/profile/${user.username}`}>{user.surname}</Table_A></Td>
                <Td><Table_A to={`/profile/${user.username}`}>{user.attempts === 0 ? '0' : <>{user.accepted}/{user.attempts} ({(user.accepted / user.attempts * 100).toFixed(2)}%)</>}</Table_A></Td>
              </tr>
            )}
          </tbody>
        </Table>

        {!state.is_loading && <PaginationContainer to={`/rating/:page`} pagination={state.pagination} />}
      </div>
    </MDiv>
  )
}

export default RatingPage