import React, { useState, useEffect } from 'react'
import styled, { css } from 'styled-components'
import { theme } from 'Themes'

const MessageBoxContainer = styled.div `

  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */


  animation: .2s ease-in-out both fade-in;
  @keyframes fade-in {
    0% {
      opacity: 0;
    }
    
    100% {
      opacity: 1;
    }
  }

  z-index: 1000;
  font-size: 14px;
  width: 100%;
  padding: 20px;
  border: none;
  border-radius: 5px;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;

  background-color: #1e1e1e;
  box-shadow: 0px 0px 4px var(--color_primary);
  border-right: 8px solid var(--color_primary);
  color: white;
  margin: 10px;
  margin-top: 5px;

  ${props => props.type === 'error' && css`
    border-color: var(--color_red);
    box-shadow: 0px 0px 4px var(--color_red);
  `}

  ${props => props.type === 'success' && css`
  border-color: var(--color_green);
    box-shadow: 0px 0px 4px var(--color_green);
  `}

  /* pointer-events: auto; */
`

const MessageBox = props => {
  const { toast, type } = props

  const [timeLeft, setTimeLeft] = useState(3000)

  const timeout = () => {
    // let time = timeLeft - 1
    // setTimeLeft(time)
   
    // if (timeLeft <= 0) {
      props.timeout(toast.id)
    // }
  }

  useEffect(() => {
    // const interval = setInterval(timeout, 1000);
    const timer = setTimeout(timeout, timeLeft)
    return () => clearTimeout(timer);
  })

  const on_messagebox_clicked = e => {
    e.preventDefault()
    props.timeout(toast.id)
  }

  return (
    <MessageBoxContainer type={type} onClick={on_messagebox_clicked}>
      {toast.message} {type === 'error' && <span style={{color: theme.red}}>!</span>}
    </MessageBoxContainer>
  )
}

export default MessageBox