import React, { useState, useEffect } from 'react'
import { theme } from 'Themes'

const LoadingSvg = props => {

  const [state, setState] = useState({
    width: props.width,
    height: props.height,
  })

  useEffect(() => {
    if (!props.width) {
      setState({
        width: props.size,
        height: props.size,
      })
    }
  }, [])

  const loadingColor = props.color ? props.color : 'var(--color_c2)'
  const puffColor    = props.color ? props.color : 'var(--color_c2)';

  if (props.dots) {
    return (
      <svg width="57" height="57" viewBox="0 0 120 30" xmlns="http://www.w3.org/2000/svg" fill={loadingColor}>
        <circle cx="15" cy="15" r="15">
          <animate attributeName="r" from="15" to="15"
            begin="0s" dur="0.8s"
            values="15;9;15" calcMode="linear"
            repeatCount="indefinite" />
          <animate attributeName="fill-opacity" from="1" to="1"
            begin="0s" dur="0.8s"
            values="1;.5;1" calcMode="linear"
            repeatCount="indefinite" />
        </circle>
        <circle cx="60" cy="15" r="9" fillOpacity="0.3">
          <animate attributeName="r" from="9" to="9"
            begin="0s" dur="0.8s"
            values="9;15;9" calcMode="linear"
            repeatCount="indefinite" />
          <animate attributeName="fill-opacity" from="0.5" to="0.5"
            begin="0s" dur="0.8s"
            values=".5;1;.5" calcMode="linear"
            repeatCount="indefinite" />
        </circle>
        <circle cx="105" cy="15" r="15">
          <animate attributeName="r" from="15" to="15"
            begin="0s" dur="0.8s"
            values="15;9;15" calcMode="linear"
            repeatCount="indefinite" />
          <animate attributeName="fill-opacity" from="1" to="1"
            begin="0s" dur="0.8s"
            values="1;.5;1" calcMode="linear"
            repeatCount="indefinite" />
        </circle>
      </svg>
    )
  }

  if (props.oval) {
    return (
      <svg width="38" height="38" viewBox="0 0 38 38" xmlns="http://www.w3.org/2000/svg" stroke="#fff">
        <g fill="none" fill-rule="evenodd">
          <g transform="translate(1 1)" stroke-width="2">
            <circle stroke-opacity=".5" cx="18" cy="18" r="18"/>
            <path d="M36 18c0-9.94-8.06-18-18-18">
              <animateTransform
                attributeName="transform"
                type="rotate"
                from="0 18 18"
                to="360 18 18"
                dur="1s"
                repeatCount="indefinite"/>
            </path>
          </g>
        </g>
      </svg>
    )
  }
  if (props.circle) {
    return (
      <svg width="18" height="18" viewBox="0 0 38 38" xmlns="http://www.w3.org/2000/svg">
        <defs>
          <linearGradient x1="8.042%" y1="0%" x2="65.682%" y2="23.865%" id="a">
            <stop stopColor="red" stopOpacity="0" offset="0%"/>
            <stop stopColor="red" stopOpacity=".631" offset="63.146%"/>
            <stop stopColor="red" offset="100%"/>
          </linearGradient>
        </defs>
        <g fill="none" fillRule="evenodd">
          <g transform="translate(1 1)">
            <path d="M36 18c0-9.94-8.06-18-18-18" id="Oval-2" stroke="url(#a)" strokeWidth="2">
              <animateTransform
                attributeName="transform"
                type="rotate"
                from="0 18 18"
                to="360 18 18"
                dur="0.9s"
                repeatCount="indefinite" />
            </path>
              <circle fill="red" cx="36" cy="18" r="1">
                <animateTransform
                  attributeName="transform"
                  type="rotate"
                  from="0 18 18"
                  to="360 18 18"
                  dur="0.9s"
                  repeatCount="indefinite" />
              </circle>
          </g>
        </g>
      </svg>
    )
  }

  if (props.puff) {
    return (
      <svg width={props.size ? props.size : '44'} height={props.size ? props.size : '44'} viewBox="0 0 44 44" xmlns="http://www.w3.org/2000/svg" stroke={puffColor}>
        <g fill="none" fillRule="evenodd" strokeWidth="2">
          <circle cx="22" cy="22" r="1">
            <animate attributeName="r"
              begin="0s" dur="1.8s"
              values="1; 20"
              calcMode="spline"
              keyTimes="0; 1"
              keySplines="0.165, 0.84, 0.44, 1"
              repeatCount="indefinite" />
            <animate attributeName="stroke-opacity"
              begin="0s" dur="1.8s"
              values="1; 0"
              calcMode="spline"
              keyTimes="0; 1"
              keySplines="0.3, 0.61, 0.355, 1"
              repeatCount="indefinite" />
          </circle>
          <circle cx="22" cy="22" r="1">
            <animate attributeName="r"
              begin="-0.9s" dur="1.8s"
              values="1; 20"
              calcMode="spline"
              keyTimes="0; 1"
              keySplines="0.165, 0.84, 0.44, 1"
              repeatCount="indefinite" />
            <animate attributeName="stroke-opacity"
              begin="-0.9s" dur="1.8s"
              values="1; 0"
              calcMode="spline"
              keyTimes="0; 1"
              keySplines="0.3, 0.61, 0.355, 1"
              repeatCount="indefinite" />
          </circle>
        </g>
      </svg>
    )
  }
  return (
    <svg width={state.width ? state.width : "57"} height={state.height ? state.height : "57"} viewBox="0 0 57 57" xmlns="http://www.w3.org/2000/svg" stroke="#fff"><g fill="none" fillRule="evenodd"><g transform="translate(1 1)"stroke={loadingColor} strokeWidth="2"><circle fill={loadingColor} cx="5" cy="50" r="5"><animate attributeName="cy"begin="0s" dur="2.2s"values="50;5;50;50"calcMode="linear"repeatCount="indefinite" /><animate attributeName="cx"begin="0s" dur="2.2s"values="5;27;49;5"calcMode="linear"repeatCount="indefinite" /></circle><circle fill={loadingColor} cx="27" cy="5" r="5"><animate attributeName="cy"begin="0s" dur="2.2s"from="5" to="5"values="5;50;50;5"calcMode="linear"repeatCount="indefinite" /><animate attributeName="cx"begin="0s" dur="2.2s"from="27" to="27"values="27;49;5;27"calcMode="linear"repeatCount="indefinite" /></circle><circle fill={loadingColor} cx="49" cy="50" r="5"><animate attributeName="cy"begin="0s" dur="2.2s"values="50;50;5;50"calcMode="linear"repeatCount="indefinite" /><animate attributeName="cx"from="49" to="49"begin="0s" dur="2.2s"values="49;5;27;49"calcMode="linear"repeatCount="indefinite" /></circle></g></g></svg>
  )
}

export default LoadingSvg