import React, { useState, useContext } from 'react'
import Form from '../../controls/Form'
import Input from '../../controls/Input'
import Button from '../../controls/Button'
import { Validate_email } from 'Global_Functions'
import { Redirect } from 'react-router-dom'
import { MessageBoxContext } from 'contexts/MessageBoxContext'
import { UserContext } from 'contexts/UserContext'
import JN from 'JN'

function RestorePassword(props) {
  const { toast } = useContext(MessageBoxContext)
  const { user }  = useContext(UserContext)

  //
  const { history } = props
  const [email, setEmail] = useState('')
  const [error, setError] = useState('')
  const [success, setSuccess] = useState(false)
  const [loading, setLoading] = useState(false)

  const onEmailChanged = (e) => {
    setEmail(e.target.value)
  }

  //
  const onSubmit = () => {
    if (!email) {
      setError('  ');
      return
    }

    if (!Validate_email(email)) {
      setError('  ');
      return
    }
    setError('')
    setLoading(true)
    JN.post(null, 'profile/restore', {email: email},
    () => {
      toast('   ', 'success')
      setSuccess(true)
    },
    (error) => {
      if (error.status === 403) {
        setError('   ')
        setLoading(false)
      }
    })
  }

  if (user) {
    return (<Redirect to={`/profile/${user.username}`} />)
  }

  if (success) {
    return (<Redirect to="/login" />)
  }

  return (
    <Form title=" " onSubmit={onSubmit}>
      <Input focus id="email" title="" value={email} onChange={onEmailChanged} isError={error !== ''} errorText={error} helper="  ,   .     " />
      <div style={{marginTop: '50px'}}>
        <Button disabled={loading}>{loading && '..'}</Button>
      </div>
    </Form>
  )
}

export default RestorePassword