import React, {useState, useEffect, useContext } from 'react'
import JN from 'JN'
import Modal from '../../components/Modal'
import CodeEditor from '../../components/CodeEditor'
import LoadingSvg from '../../components/LoadingSvg'
import { MdDateRange, MdLink } from 'react-icons/md'
import { Code_Editor_Names, Copy } from 'Global_Functions'
import { UserContext } from 'contexts/UserContext'
import { Convert_date_to_string } from 'Global_Functions'
import { Redirect } from 'react-router-dom'
import styled from 'styled-components'
import { MessageBoxContext } from 'contexts/MessageBoxContext'
import A from 'controls/A'

const BButton = styled.button `  
  cursor: pointer;
  border: none;
  background: none;
  outline: none;
  margin-right: 10px;

  &:hover {
    transition: transform ease 250ms;
    transform: scale(1.4);
  }
`

const SubmissionPreviewDlg = props => {
  const { user } = useContext(UserContext);
  const {id} = props;
  const { toast } = useContext(MessageBoxContext);

  const [state, setState] = useState({
    token: localStorage.getItem('access_token'),
    submission: {language: 'c_cpp'},
    is_loading: true,
    is_contest_running: false,
  });

  useEffect(() => {

    JN.get(user, `submission`, {id: id},
    (data) => {
      let language = Code_Editor_Names[data.language]

      setState({
        ...state,
        submission: {...data, language: language},
        is_loading: false,
      })
    },
    (error) => {
      console.log(error);

      setState({
        ...state,
        is_loading: false,
        is_contest_running: error.status === 403,
      });
    })
  }, [])

  const onCopyLink = e => {
    e.preventDefault();
    if (Copy(`aiasoft.ge/submission/${id}`)) {
      toast(' ');
    }
    else {
      toast('  ', 'error');
    }
  }

  if (state.redirect_to_404) {
    return (<Redirect to="/404" />)
  }

  if (state.is_contest_running) {
    if (user) {
      return (
        <Modal width="550px" isOpen={true} close={props.close}>
          <div style={{fontSize: '16px', margin: 'auto', marginTop: '15px', padding: '15px'}}>
               <span style={{color: 'var(--color_red)'}}>!</span>
          </div>
        </Modal>
      )
    }

    return (
      <Modal isOpen={true} close={props.close}>
        <div style={{fontSize: '16px', margin: 'auto', marginTop: '15px', padding: '15px'}}>
             <A to={`/login`}></A>
        </div>
      </Modal>
    )
  }

  if (state.is_loading) {
    return (
      <Modal isOpen={true} close={props.close}>
        <div style={{margin: 'auto'}}><LoadingSvg /></div>
      </Modal>
    )
  }
  else {
    return (
      <Modal width="550px" title={state.submission.verdict_ge} isOpen={true} close={props.close}>

        <CodeEditor readOnly isPreview width="500px" height="400px" code={{value: state.submission.code, editorName: state.submission.language, theme: 'twilight'}}
          preview={
            <div>
              <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', fontSize: '14px', padding: '5px'}}>
                <span style={{display: 'flex', alignItems: 'center'}}><MdDateRange size="16" />{Convert_date_to_string(state.submission.datetime)}</span>
                <div style={{display: 'flex', alignItems: 'center'}}>
                  <BButton onClick={onCopyLink}><MdLink color="var(--color_c2)" size="18" /></BButton>
                  <A to={`/submission/${id}`}></A>
                </div>
              </div>
            </div>
          }
        />
      </Modal>
    )
  }
}

export default SubmissionPreviewDlg
