import React, { useState, useContext } from 'react'
import { UserContext } from 'contexts/UserContext'
import Modal from './Modal'
import Input from 'controls/Input'
import ContainerFooter from 'controls/ContainersFooter'
import Button from 'controls/Button'
import { MessageBoxContext } from 'contexts/MessageBoxContext'
import axios from 'axios'
import { Server } from 'Server'

const FreshingTokenDlg = props => {
  const { toast } = useContext(MessageBoxContext)
  const { user, login } = useContext(UserContext)
  const { callAfter } = user
  const [state, setState] = useState({
    isLoading: false,
    isError: false,
  })

  const [password, setPassword] = useState('')

  const onClose = () => {
    // user.update_user({...user, freshTokenRequired: false})
  }

  const onPasswordChanged = e => {
    setPassword(e.target.value)
  }

  const onSubmit = (e) => {
    e.preventDefault()

    setState({...state, isLoading: true})
    axios.post(`${Server.address}/login`, {username: user.username, password: password})
    .then(response => {

      const newUser = {
        ...user,
        password: '',
        access_token: response.data.access_token,
        refresh_token: response.data.refresh_token,
        freshTokenRequired: false,
        ...response.data.user_info
      }
      login(newUser)

      callAfter.func(newUser, callAfter.url, callAfter.data, callAfter.on_finished, callAfter.on_error)
      // history.goBack()
    })
    .catch(error => {
      setState({...state, isLoading: false})
      if (!error.response) {
        toast('corsp policy- ', error)
        return
      }

      if (error.response.status === 400) {
        setState({...state, isError: true})
        return
      }
    })

  }

  if (!user.freshTokenRequired) {
    return null
  }

  return (

    <Modal width="450px" isOpen={true} close={onClose} title="  ">
      <form onSubmit={onSubmit}>
        <Input focus title="" password value={password} onChange={onPasswordChanged} errorText=" " isError={state.isError} />
        <ContainerFooter>
          <Button disabled={state.isLoading || password === ''}>{state.isLoading && '..'}</Button>
        </ContainerFooter>
      </form>
    </Modal>
 
  )
}

export default FreshingTokenDlg