import React, { useState, useEffect, useContext } from 'react'
import { BrowserRouter as Router, Redirect } from 'react-router-dom'
import Form from 'controls/Form'
import Input from 'controls/Input'
import Button from 'controls/Button'
import A from 'controls/A'
import { UserContext } from 'contexts/UserContext'
import JN from 'JN'
import { MessageBoxContext } from 'contexts/MessageBoxContext'
import { GlobalContext } from 'contexts/GlobalContext'
import ReactGA from 'react-ga'

const Login = props => {
  const { tr } = useContext(GlobalContext);
  const { history } = props;
  const { toast } = useContext(MessageBoxContext);
  
  const {user, login} = useContext(UserContext)

  const [state, setState] = useState({
    user: {
      username: '',
      password: '',
    },
    is_loading: false,
    was_loggedIn: user && user !== null,
    error: {
      username: '',
      password: '',
    },
  })


  const on_change = e => {
    setState({
      ...state,
      user: {...state.user, [e.target.name]: e.target.value},
    })
  }

  const on_submit = () => {

    if (localStorage.getItem('access_token')) {
      window.location.reload();
      return
    }

    setState({
      ...state,
      is_loading: true,
    })

    JN.post(null, `login`, {username: state.user.username, password: state.user.password},
    (data) => {
      login({
        ...state.user,
        password: '',
        access_token: data.access_token,
        refresh_token: data.refresh_token,
        ...data.user_info
      })
      ReactGA.event({category: '', action: ' ', label: `${state.user.username}`});
      history.goBack()
    },
    (error) => {

      if (error.status === 400) {
        setState({
          ...state,
          is_loading: false,
          error: {
            password: tr(' '),
          }
        })
        toast(tr(' '), 'error')
      }
      else if (error.status === 404) {
        setState({
          ...state,
          is_loading: false,
          error: {
            username: tr('  '),
          }
        })
        toast(tr('  ', 'error'))
      }
    })

  }

  if (user || state.was_loggedIn) {
    return (
      <Redirect to={`/profile/${user.username}`} />
    )
  }

  return (
    <Form title={tr("")} onSubmit={on_submit}>

      <Input required focus
        name="username"
        isError={state.error.username}
        errorText={state.error.username}
        onChange={on_change}
        title={tr("   ")}
      />

      <Input required
        name="password"
        isError={state.error.password}
        errorText={state.error.password}
        onChange={on_change}
        title={tr("")}
        type="password"
      />

      <span style={{color: "var(--color_c2)", fontSize: '14px'}}>
        {tr(" ")}? <A to="/restorepassword">{tr("")}</A>
      </span>

      <div style={{marginTop: '50px'}}>
        <Button disabled={state.is_loading}>{tr('')}{state.is_loading ? '..' : ''}</Button>
      </div>
    </Form>
  )
}

export default Login