import React, { useState, useEffect, useContext, useRef } from 'react'
import { Code_Editor_Names } from 'Global_Functions'
import { UserContext } from 'contexts/UserContext'
import { Convert_date_to_string } from 'Global_Functions'
import { MdDateRange } from 'react-icons/md'
import CodeEditor from '../../components/CodeEditor'
import JN from 'JN'

const Submission_Page = props => {
	const { user } = useContext(UserContext);
	const { match } = props;

	const [state, setState] = useState({
    token: localStorage.getItem('access_token'),
    submission: {language: 'c_cpp'},
    is_loading: true,
    is_contest_running: false,
  });

	const refStart = useRef(null);

	useEffect(() => {
		if (refStart.current) {
			// window.scrollTo(0, refStart.current.offsetTop);
			window.scrollTo(0, 0);
		}

    JN.get(user, `submission`, {id: match.params.id},
    (data) => {
      let language = Code_Editor_Names[data.language]
// console.log(data)
      setState({
        ...state,
        submission: {...data, language: language},
        is_loading: false,
      })
    },
    (error) => {
      console.log(error)

      setState({
        ...state,
        is_loading: false,
        is_contest_running: error.status === 403,
      })
    })
  }, [])

	return (
		<div ref={refStart} style={{width: '100%'}}>
			<div style={{width: 1000, margin: 'auto',}}>
				<div style={{height: 12, textAlign: 'left'}}>
					{state.submission.datetime && Convert_date_to_string(state.submission.datetime)}
				</div>
				<CodeEditor readOnly isPreview width="1000px" height="1000px" code={{value: state.submission.code, editorName: state.submission.language, theme: 'twilight'}}/>
			</div>
		</div>
	)
}

export default Submission_Page;