import React, { useState, useEffect, useContext, useRef } from 'react'
import Flex from '../../controls/Flex'
import Button from '../../controls/Button'
import Input from '../../controls/Input'
import Form from '../../controls/Form'
import JN from 'JN'
import { withRouter, Link, Redirect } from 'react-router-dom'
import Container from '../../controls/Container'
import ContainerFooter from '../../controls/ContainersFooter'
import FacebookLogin from 'react-facebook-login';
import A from '../../controls/A'
import { UserContext } from 'contexts/UserContext'
import { MessageBoxContext } from 'contexts/MessageBoxContext'
import { Validate_email, Validate_username, Validate_password } from 'Global_Functions'
import Grid from 'controls/Grid'
import { theme } from 'Themes'
import { GlobalContext } from 'contexts/GlobalContext'
import ReactGA from 'react-ga'

function Registration(props) {
  const { history } = props;
  const refForm = useRef(null);
  const { tr } = useContext(GlobalContext);
  const { user }  = useContext(UserContext);
  const { toast } = useContext(MessageBoxContext);

  const [state, setState] = useState({
    user: {
      username: "",
      name: "",
      surname: "",
      email: "",
      password: "",
    },
    error: {username: false, name: false, surname: false, email: false, password: false,},
    errorMessage: {email: tr('   ')},
    isLoading: false,
    thankYouPageVisible: false,
  })

  useEffect(() => {
    if (refForm.current) window.scrollTo(0, refForm.current.offsetTop)
  }, [])
  const onChange = (e) => {
    setState({
      ...state,
      user: {
        ...state.user,
        [e.target.name]: e.target.value,
      }
    })
  }

  //
  const onSubmit = () => {
    // 
    if (!Validate_username(state.user.username)) {
      toast(tr('    '), 'error')
      setState({...state, error: {username: true}, errorMessage: {username: '  3- 20       ,   '}})
      return
    }

    if (!Validate_email(state.user.email)) {
      toast(tr('  '), 'error')
      setState({...state, error: {email: true}, errorMessage: {email: '  '}})
      return
    }

    if (!Validate_password(state.user.password)) {
      toast(tr('  '), 'error')
      setState({...state, error: {password: true}, errorMessage: {password: '   5  20 '}})
      return
    }

    // 
    setState({
      ...state,
      isLoading: true,
    })

    JN.post(null, `register`, state.user,
    () => {
      setState({
        ...state,
        isLoading: false,
        thankYouPageVisible: true,
      })
      ReactGA.event({category: '', action: ' ', label: `${state.user.username}`});
    },
    (error) => {
      let error_code = error.data.error_code

      if (error_code === 1) {
        toast(tr('  '), 'error')
        setState({...state, isLoading: false, error: {email: true}, errorMessage: {email: '  '}})
      }
      else if (error_code === 2) {
        toast(tr('  '), 'error')
        setState({...state, isLoading: false, error: {password: true}, errorMessage: {password: '  '}})
      }
      else if (error_code === 3) {
        toast(tr('   '), 'error')
        setState({...state, isLoading: false, error: {password: true}, errorMessage: {password: '   '}})
      }
      else if (error_code === 4) {
        toast('  ', 'error')
        setState({...state, isLoading: false, error: {email: true}, errorMessage: {email: '  '}})
      }
      else if (error_code === 5) {
        toast(tr('  '), 'error')
        setState({...state, isLoading: false, error: {username: true}, errorMessage: {username: '  '}})
      }
    })
  }

  const on_facebook_login = (response) => {
    console.log(response)

    const name    = response.name.split(' ')[0]
    const surname = response.name.split(' ')[1]

    setState({
      ...state,
      user: {
        ...state.user,
        username: response.name.replace(' ', ''),
        name: name,
        surname: surname,
        email: response.email,
        password: response.id,
      }
    })
  }

  if (user) {
    return (<Redirect to="/" />)    
  }

  if (state.thankYouPageVisible)
    return (
      <div>
        <Container title={tr(" ")}>
          <div>
            {tr("   !")}
            <br />
            {tr("  ")} <A to="login">{tr('')}</A>
          </div>
        </Container>
      </div>
    )
  else return (
    <>
    <div style={{height: '1px', marginBottom: '25px'}} ref={refForm}></div>
    <Form title={tr("")} onSubmit={onSubmit}>
    {/* <FacebookLogin
        appId="2632898383472476"
        autoLoad={false}
        fields="name,email,picture"
        onClick={componentClicked}
        callback={on_facebook_login}
        scope="user_friends"
      /> */}

      <Input focus required name="username" title={tr(" ")} value={state.user.username} errorText={state.errorMessage.username} isError={state.error.username} onChange={onChange} />
      <Grid columns="2" gap="15px">
        <Input required name="name" title={tr("")} ph="" errorText={tr(" ")} isError={state.error.name} value={state.user.name} onChange={onChange} />
        <Input required name="surname" title={tr("")} errorText={tr(" ")} isError={state.error.surname} value={state.user.surname} onChange={onChange} />
      </Grid>
      <Input required name="email" title={tr("")} errorText={state.errorMessage.email} isError={state.error.email} value={state.user.email} onChange={onChange} />
      <Input required name="password" type="password" title={tr("")} errorText={tr(" ")} errorText={state.errorMessage.password} isError={state.error.password} value={state.user.password} onChange={onChange} />
      <ContainerFooter>
        <Button disabled={state.isLoading}>{tr('')}</Button>
      </ContainerFooter>
    </Form>
    </>
  )
}

export default withRouter(Registration)