import React, { useState, useEffect } from 'react'
import { Get_dateDiff_toString } from 'Global_Functions'
import TimeWidget from './TimeWidget'

const CountDown = props => {

  const [state, setState] = useState({
    from: props.from,
    timeDiff: Get_dateDiff_toString(props.from ? props.from : 'now', props.to ? props.to : 'now', true, props.to)
  })


  const timeout = () => {
    let diff = Get_dateDiff_toString(props.from ? props.from : 'now', props.to ? props.to : 'now', true, props.to)
    setState({timeDiff: diff})

    if (!diff) {
      props.timeout()
    }
  }

  useEffect(() => {
    const interval = setInterval(timeout, 1000);
    return () => clearInterval(interval);
  }, [props])

  return (
    <TimeWidget color={props.color} size={props.size} time={state.timeDiff} isDynamic={true} />
  )
}

export default CountDown