import React, { useState, useEffect, useContext } from 'react'
import Modal from '../../../components/Modal'
import Input from '../../../controls/Input'
import Button from '../../../controls/Button'
import ContainerFooter from '../../../controls/ContainersFooter'
import JN from 'JN'
import { UserContext } from 'contexts/UserContext'
import { MessageBoxContext } from 'contexts/MessageBoxContext'

const CategoryEditDlg = props => {

  const { user }  = useContext(UserContext)
  const { toast } = useContext(MessageBoxContext)

  const [state, setState] = useState({
    category: {title: ""},
    isLoading: false,
  })

  useEffect(() => {
    const category = props.category ? props.category : {id: -1, title: ""}
    setState({
      ...state,
      category: category,
    })
  }, [props.category]);

  const onChange = (e) => {
    setState({
      ...state,
      category: {...state.category, [e.target.name]: e.target.value},
    })
  };

  const setOpen = (isOpen) => {
    if (state.isLoading) {
      return
    }
    props.close(isOpen)
  }

  const submit = e => {
    e.preventDefault()
    // 
    if (state.category.id === -1) {
      setState({
        ...state,
        isLoading: true
      })
      JN.post(user, `admin/tags`, {tag_title: state.category.title},
      (data) => {
        if (props.submited) {
          props.submited({...state.category, id: data.tag_id}, true);
        }
        else {
          toast(' ', 'success');
          props.setState({
            ...props.state,
            problem: {...props.state.problem, tags: [...props.state.problem.tags, data.tag_id]},
            tags: [...props.state.tags, {id: data.tag_id, title: state.category.title}],
            isOpen: false,
          });
        }
      },
      (error) => {
        console.log(error)
        setState({
          ...state,
          isLoading: false
        })
      })
    }
    //
    else {
      setState({
        ...state,
        isLoading: true
      })
      JN.put(user, 'admin/tags', {tag_id: state.category.id, new_title: state.category.title},
      () => {
        if (props.submited) {
          props.submited(state.category, false);
        }
        else {
          toast(' ', 'success');
          setState({
            ...state,
            isLoading: false
          })
          props.setState({
            ...props.state,
            tags: props.state.tags.map((c) => {
              return c.id !== state.category.id ? c : {...c, title: state.category.title}
            }),
            isOpen: false,
          });
        }
      },
      () => {setState({...state, isLoading: false})}
      )
      
    }
  }

  return (
    <Modal width="450px" title={state.category.id === -1 ? " " : " "} isOpen={props.isOpen} close={setOpen}>
      <form onSubmit={submit}>
        <Input required focus title="" name="title" value={state.category.title} onChange={onChange}/>
        <ContainerFooter>
          <Button disabled={state.isLoading} backgroundColor="primary">{state.isLoading && '..'}</Button>
        </ContainerFooter>
      </form>
    </Modal>
  )
}

export default CategoryEditDlg
