import React, { useState, useEffect } from 'react'
import { MdKeyboardArrowLeft, MdKeyboardArrowRight } from 'react-icons/md'
import { theme } from 'Themes'
import { Link, NavLink, Redirect } from 'react-router-dom'
import { Get_Number, Get_PaginationArray } from 'Global_Functions'
import styled, { css } from 'styled-components'
import LoadingSvg from 'components/LoadingSvg'

const Span = styled.span `
  @media (max-width: ${theme.mobileSize}) {
    display: none;
  }
`

const Pagination_Container = styled.div `

  -webkit-touch-callout: none; /* iOS Safari */
      -webkit-user-select: none; /* Safari */
      -khtml-user-select: none; /* Konqueror HTML */
        -moz-user-select: none; /* Old versions of Firefox */
          -ms-user-select: none; /* Internet Explorer/Edge */
              user-select: none; /* Non-prefixed version, currently
                                    supported by Chrome, Opera and Firefox */

  display: grid;
  justify-content: start;
  align-items: flex-end;
  padding-bottom: 15px;
  padding-left: 15px;
  grid-auto-flow: column;
  /* grid-template-columns: repeat(auto-fill, minmax(36px, 64px)); */
  grid-column-gap: 5px;
  margin-bottom: 15px;
  background-color: var(--color_b2);
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  height: 100px;
  /* border: 3px solid #e9ecf6; */
  border-top: 0;

  ${props => props.noBackground && css `
    height: 65px;
    box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    overflow: hidden;
  `};

  @media (max-width: ${theme.mobileSize}) {
    padding-left: 0;
  }
`

const Pagination_Button = styled(NavLink) `
  font-family: ${theme.fontCaps};
  font-size: 14px;
  color: var(--color_c2);
  background-color: transparent;
  padding: 5px 10px;
  text-decoration: none;
  height: 36px;

  ${props => props.primary && css`
    
    border: 1px solid var(--color_red);
    border-radius: 5px;
  `}

  display: flex;
  justify-content: center;
  align-items: center;

  transition: color ease 250ms;

  ${props => props.disabled && css `  
    pointer-events: none;
    color: var(--color_c3);
  `}

  ${props => !props.disabled && css `
    &:hover {
      color: var(--color_red);
    }
  `}
`

const Styled_NavLink = styled(NavLink) `
  color: var(--color_c2);
  font-family: ${theme.fontCaps};
  font-size: 16px;
  padding: 5px 10px;
  height: 36px;
  background: transparent;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;

  &:hover {
    color: var(--color_red);
  }
`

const PaginationContainer = props => {

  const [state, setState] = useState({
    current: 0,
    total: 0,
    visiblePages: [],
  })

  useEffect(() => {
    setState({...props.pagination, visiblePages: Get_PaginationArray(props.pagination.total, props.pagination.current)})
  }, [props.pagination])

  if (props.pagination.total <= 1) {
    return (<></>)
  }
  
  return (
   
    <Pagination_Container noBackground={props.noBackground}>
      {state.current > 1 ?
        <Pagination_Button disabled={state.current <= 1} to={props.to.replace(':page', state.current - 1)}>
          <MdKeyboardArrowLeft /> 
          {/* <Span></Span> */}
        </Pagination_Button>
      : <span style={{marginLeft: 5}}></span>
      }
      {state.visiblePages.map(page => 
        <Styled_NavLink key={page} to={props.to.replace(':page', page)}
          activeStyle={{
            pointerEvents: 'none',
            borderBottom: `2px solid var(--color_red)`,
            color: 'var(--color_red)',
          }}
          
          isActive={(match, location) => {
            if (!match) {

              return state.current === 1 && page === 1;
            }
        
            return true;
          }}
          >
            {Get_Number(page)}
          </Styled_NavLink>
        )}
        {props.pagination.current < props.pagination.total &&
          <Pagination_Button disabled={props.pagination.current >= props.pagination.total} to={props.to.replace(':page', parseInt(state.current) + 1)} >
            {/* <Span></Span> */}
            <MdKeyboardArrowRight />
          </Pagination_Button>
        }
        {props.isLoading && <div style={{display: 'flex', justifyContent: 'center', alignItems: 'center', height: '36px'}}><LoadingSvg dots size="36" /></div>}

    </Pagination_Container>
  )
}

export default PaginationContainer