import React, { Component } from 'react'
import axios from 'axios';
import { Server } from 'Server'

const onFinished = (response, on_finished) => {

  if (!response.data) {
    return
  }

  if (typeof(on_finished) === 'function') {
    on_finished(response.data)
  }

}

const onError = (user, url, data, on_finished, error, on_error, callAfterFunction) => {

  //  
  if (!error.response) {
    console.log(error);
    alert('  !')
    // window.location.assign("/404")
    return;
  }

  // 
  if (user && error.response.status === 401) {

    if (error.response.data.error_code === 1) {
      user.update_token(callAfterFunction, user, url, data, on_finished, on_error)
      return;
    }

    //  
    if (error.response.data.error_code === 5) {
      user.update_user({...user, freshTokenRequired: true, callAfter: {func: callAfterFunction, url: url, data: data, on_finished: on_finished, on_error: on_error}})
      return;
    }

    //   
    if (!localStorage.getItem('access_token')) {
      window.location.reload()
      return
    }
  }

  if (typeof(on_error) === 'function') {
    on_error(error.response);
  }
}

const Get = (user, url, data, on_finished, on_error)  => {

  axios.get(`${Server.address}/${url}`, {
    headers: user ? {Authorization: 'Bearer ' +  user.access_token} : {},
    params: data,
  })
  .then(response => {
    onFinished(response, on_finished)
  })
  .catch(error => {
    onError(user, url, data, on_finished, error, on_error, Get)
  })  
}

const Post = (user, url, data, on_finished, on_error)  => {

  axios.post(`${Server.address}/${url}`, data, user ? {headers: {Authorization: 'Bearer ' + user.access_token}} : {})
  .then(response => {
    onFinished(response, on_finished)
  })
  .catch(error => {
    onError(user, url, data, on_finished, error, on_error, Post)
  })
}

const Put = (user, url, data, on_finished, on_error)  => {

  axios.put(`${Server.address}/${url}`, 
    data,
    user ? {headers: {Authorization: 'Bearer ' +  user.access_token}} : {}
  ).then(response => {
    onFinished(response, on_finished)
  })
  .catch(error => {
    onError(user, url, data, on_finished, error, on_error, Put)
  })  
}

const Delete = (user, url, data, on_finished, on_error)  => {

  axios.delete(`${Server.address}/${url}`, {headers: user ? {Authorization: 'Bearer ' + user.access_token} : {}, data: data})
  .then(response => {
    onFinished(response, on_finished)
  })
  .catch(error => {
    onError(user, url, data, on_finished, error, on_error, Delete)
  })
}

const Head = (user, url, data, on_finished, on_error)  => {

  axios.head(`${Server.address}/${url}`, {headers: user ? {Authorization: 'Bearer ' + user.access_token} : {}, params: data})
  .then(response => {
    onFinished(response, on_finished)
  })
  .catch(error => {
    onError(user, url, data, on_finished, error, on_error, Head)
  })
}

class JN {

  static get(user, url, data, on_finished, on_error) {
    Get(user, url, data, on_finished, on_error)
  }

  static post(user, url, data, on_finished, on_error) {
    Post(user, url, data, on_finished, on_error)
  }

  static put(user, url, data, on_finished, on_error) {
    Put(user, url, data, on_finished, on_error)
  }

  static delete(user, url, data, on_finished, on_error) {
    Delete(user, url, data, on_finished, on_error)
  }

  static head(user, url, data, on_finished, on_error) {
    Head(user, url, data, on_finished, on_error)
  }
}

export default JN