import React, { useState, createContext } from 'react'
import translates from 'JTranslate'

export const GlobalContext = createContext({})

export const GlobalProvider = props => {

  const [state, setState] = useState({
    mode: 'light',
    language: '',

    chat: {
      isOpen: false,
      currentRoomName: '',
    },


    images: {
      openedImageSrc: '',
    },
  });

  const [unseenCount, setUnseenCount] = useState(0);

  const openChatRoom = (room_name, username) => {
    setState({ ...state, chat: { ...state.chat, isOpen: true, currentRoomName: room_name, user1: username } });
  }

  const setChatOpen = is_chat_open => {
    setState({ ...state, chat: { ...state.chat, isOpen: is_chat_open } });
  }

  const updateUnseenCount = count => {
    setUnseenCount(count)
  }

  const setOpenedImage = src => {
    setState({...state, images: {...state.images, openedImageSrc: src}})
  }

  const tr = (word) => {
    if (!translates[state.language])
      return word;

    let translatedWord = translates[state.language][word];
    if (!translatedWord || translatedWord === '')
      return word;

    return translatedWord;
  }

  const setLanguage = (language) => {
    setState({ ...state, language: language })
  }

  return (
    <GlobalContext.Provider value={{
      global: state, setGlobal: setState,
      tr: tr, setLanguage: setLanguage,
      chat: state.chat,
      setChatOpen: setChatOpen,
      openChatRoom: openChatRoom,
      _unseenCount: unseenCount, _updateUnseenCount: updateUnseenCount,
      setOpenedImage: setOpenedImage,
      openedImageSrc: state.images.openedImageSrc,
    }}>

      {props.children}
    </GlobalContext.Provider>
  )
}