import React, { useState, useEffect, useContext } from 'react'
import JN from 'JN'
import { UserContext } from 'contexts/UserContext'
import LoadingSvg from 'components/LoadingSvg'
import { theme } from 'Themes'
import styled, { css } from 'styled-components'
import { Link } from 'react-router-dom'
import { Get_Number, convertToRoman } from 'Global_Functions'
import PaginationContainer from 'controls/pagination/PaginationContainer'
import Modal from 'components/Modal'
import { Convert_date_to_string } from 'Global_Functions'
import SubmissionPreviewDlg from 'pages/submissions/SubmissionPreviewDlg'
import { MdAccessTime, MdMemory } from 'react-icons/md'

const Table_A = styled(Link) `
  font-size: 16px;
  color: red;
  text-decoration: none;
  color: var(--color_c2);
  display: inline-block;

  ${props => 
    props.th &&
    css`
      color: var(--color_headerText);
      font-weight: normal;
    `
  };

  &:hover {
    color: var(--color_link);
  }
`

const Success = styled.span `

  color: var(--color_c2);

  ${props => props.code === 0 && css`
    color: var(--color_green);
  `}

  ${props => props.code === 1 && css`
    color: var(--color_red);
  `}
`

const Table = styled.table `
  border-collapse: collapse;
  overflow: auto;
  margin-top: 15px;
  width: 100%;

  tr {
    cursor: pointer;
    /* border-bottom: 1px solid var(--color_b22); */

    &:last-child {
      border: none;
    }
  }

  th {
    font-weight: normal;
    border-bottom: 1px solid var(--color_b22);
    padding: 5px;

    &:nth-child(2) {
      text-align: left;
    }
  }

  td {
    padding: 5px;
    padding-left: 5px;
    padding-right: 5px;
    width: 1px;
    white-space: nowrap;

    :nth-child(2) {
      text-align: left;
      width: inherit;
      white-space: wrap;
    }
  }
`

const Problem_table = styled.table `

  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */

  border-collapse: collapse;
  overflow: hidden;
  margin-top: 15px;
  width: 100%;
  padding: 25px;
  padding-top: 25px;
  /* box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);  */
  background-color: var(--color_b11);
  border-radius: 8px;
  font-size: 16px;

  tr {
    border-bottom: 1px solid var(--color_b2);
    height: 50px;

    &:first-child {
      border: none;
      color: var(--color_headerText);
      background-color: var(--color_headerBackground);
    }
  }

  th {
    padding: 10px;
    font-weight: normal;

    &:nth-child(2) {
      text-align: left;
    }


    &:nth-child(2) {
      width: 50%;
    }
  }

  td {
    padding: 5px;
    padding-left: 15px;
    padding-right: 15px;
    color: var(--color_c2);
    background-color: var(--color_b11);
    white-space: nowrap;

    &:nth-child(1) {
      font-size: 14px;
    }
    text-align: center;
    
    &:nth-child(2) {
      text-align: left;
      padding: 0;
      width: 100%;

      a {
        width: 100%;
        padding: 8px;
      }
    }
    
    border: 1px solid var(--color_b2);
    border-top: none;
    &:nth-child(1), &:nth-child(2), &:nth-last-child(1), &:nth-last-child(2) {
      border: none;
    }
    /* &:nth-child(3) {
      text-align: left;
    }     */
  }
`

const TabStandings = props => {
  const { match } = props;
  const { user } = useContext(UserContext);
  const [state, setState] = useState({
    users: [],
    pagination: {current: match.params.page, total: 0},
    is_loading: true,
    is_dlgSubmissions_open: false,
    submissions: [],
    open_submission: false,
    opened_submission_id: 0,
  });

  useEffect(() => {

    JN.get(user, `contest/${match.params.id}/standing/${match.params.page}`, {},
    (data) => {
      setState({...state, is_loading: false, users: data.standings.users, pagination: {current: match.params.page, total: data.page_number}})
      window.scroll(0, 0);
    },
    () => {
      setState({...state, is_loading: false})
    })

  }, [match.url]);


  if (state.is_loading) {
    return (
      <div style={{textAlign: 'center'}}>
        <LoadingSvg />
      </div>
    )
  }

  const closeDlgSubmissions = () => {
    setState({...state, is_dlgSubmissions_open: false});
  }

  const onViewSubmissions = (userr, problem) => {
    JN.get(user, `contest/${match.params.id}/${problem.problem_id}/${userr.user_id}`, {}, 
    data => {
      setState({...state, is_dlgSubmissions_open: true, submissions: data.submissions});
    },
    error => {

    });
  }

  const onSubmissionClicked = submission => {
    setState({...state, open_submission: true, opened_submission_id: submission.id});
  }

  const on_submissionsPreviewDlg_close = () => {
    setState({...state, open_submission: false});
  }

  return (
    <>
      {state.users.length > 0 &&
      <Problem_table>
        <thead></thead>
        <tbody>
          <tr style={{height: 20}}>
            <th></th>
            <th></th>
       
            {state.users.length && state.users[0].problems_info.map((problem, index) => 
              <th key={index}><Table_A th="true" to={`/problem/${problem.problem_id}`}>{convertToRoman(index + 1)}</Table_A></th>
            )}

            <th></th>
            <th></th>

          </tr>
        {state.users.map((user, index) =>
          <tr key={user.user_info} style={{height: 0}}>
            <td>
              {user.rank}
            </td>
            <td>
              <Table_A to={`/profile/${user.user_info}`}>{user.user_info}</Table_A>
            </td>
           

            {user.problems_info.map((problem, index) => 
            <td key={index}>
              {(problem.accepted || problem.wrong > 0) &&
              <button onClick={() => onViewSubmissions(user, problem)} style={{display: 'flex', justifyContent: 'center', alignItems: 'center', width: 13, background: 'none', fontFamily: theme.font, fontSize: '16px', color: problem.accepted ? 'var(--color_green)' : 'var(--color_red)', border: 'none', outline: 'none', cursor: 'pointer'}}>
                <span style={{paddingRight: '0px'}}>{problem.accepted ? '+' : '-'}</span>{problem.wrong ? problem.wrong : ''}
              </button>
              }
            </td>
            )}

            <td style={{color: 'var(--color_c2)', fontSize: '14px'}}>
              {user.penalty}
            </td>
            <td>
              {user.score}
            </td>

          </tr>
          )}
        </tbody>
      </Problem_table>
      }
      {!state.is_loading && <PaginationContainer to={`/contest/${match.params.id}/standing/:page`} pagination={state.pagination} />}

      {state.is_dlgSubmissions_open &&
        <Modal width="620px" isOpen={true} close={closeDlgSubmissions}>
        <div style={{overflow: 'auto', height: '480px'}}>
          <Table>
          <thead></thead>
          <tbody>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th>
                <div style={{display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                  <MdMemory size="16" />
                </div>
              </th>
              <th>
                <div style={{display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                  <MdAccessTime  size="16" />
                </div>
              </th>
            </tr>
            {state.submissions.map((submission, index) => 
              <tr key={submission.id} onClick={() => onSubmissionClicked(submission)}>
                <td>{index + 1}</td>
                <td><Success code={submission.verdict_code}>{submission.verdict_ge}</Success></td>
                <td>{Convert_date_to_string(submission.datetime)}</td>
                <td>{submission.language}</td>
                <td>{submission.memory}</td>
                <td>{submission.time}</td>
              </tr>
            )}
          </tbody>
        </Table>
        </div>
        </Modal>
      }

      {state.open_submission &&
        <SubmissionPreviewDlg id={state.opened_submission_id} close={on_submissionsPreviewDlg_close} />
      }
    </>
  )
}

export default TabStandings