import React, { useState, useContext } from 'react'
import Modal from 'components/Modal'
import Input from 'controls/Input'
import ContainerFooter from 'controls/ContainersFooter'
import Button from 'controls/Button'
import JN from 'JN'
import { UserContext } from 'contexts/UserContext'

const PasswordEditDlg = props => {

  const { user } = useContext(UserContext)

  const [state, setState] = useState({
    password_old: '',
    password_new: '',
    is_error_oldPassword: false,
    is_updating: false,
  })

  const on_close = () => {
    if (!state.is_updating) {
      props.close()
    }
  }

  const on_change = e => {
    setState({
      ...state,
      [e.target.name]: e.target.value,
    })
  }

  const on_submit = e => {
    e.preventDefault()

    setState({
      ...state,
      is_updating: true,
    })

    JN.put(user, `profile/password`, {old_password: state.password_old, new_password: state.password_new},
    () => {
      props.close()
    },
    (error) => {
      setState({
        ...state,
        is_error_oldPassword: error.status === 401,
        is_updating: false,
      })
    })
  }

  return (
    <Modal title=" " isOpen={props.isOpen} close={on_close}>
      <form onSubmit={on_submit}>
        <Input required password focus title=" " name="password_old" value={state.password_old} onChange={on_change} isError={state.is_error_oldPassword} errorText=" " />
        <Input required password title=" " name="password_new" value={state.password_new} onChange={on_change} />

        <ContainerFooter>
          <Button disabled={state.is_updating}>{state.is_updating ? '..' : ''}</Button>
        </ContainerFooter>
      </form>
    </Modal>
  )
}

export default PasswordEditDlg