import React, { useState } from 'react'
import styled from 'styled-components'
import { theme } from 'Themes'
import { MdCloudUpload } from 'react-icons/md'

const Upload = styled.label `
  position: relative;
  padding: 10px;
  border: 1px dotted ${theme.primary};
  border-radius: 5px;
  width: 100%;
  height: ${props => props.height ? props.height : "150px"};
  margin-top: 5px;
  font-size: ${props => props.fontSize ? props.fontSize : "20px"};
  display: flex;
  justify-content: center;
  align-items: center;
  color: ${theme.primary};
  &, * {
    cursor: pointer;
  }
  & > label {
    position: absolute;
    left: 0%;
    right: 0;
    top: 0;
    bottom: 0;
    text-align: center;
  }
  svg {
    margin-right: 1em;
  }
`

const UploadButton = (props) => {
  const [state, setState] = useState({
    fileName: '',
  })

  const OnFilePicked = (e) => {
    
    const file = e.target.files.item(0)
    if (!file)
      return
    
    props.OnFilePicked(props.name, e)

    setState({
      ...state,
      fileName: file.name
    })
  }

  return (
    <Upload htmlFor={props.name} fontSize={props.fontSize} height={props.height}>
      <input id={props.name} style={{display: 'none'}} type="file" onChange={OnFilePicked} />
      <MdCloudUpload size="26" color={theme.primary} />
      {props.fileName !== '' ? props.fileName : '   '}
    </Upload>
  )
}

export default UploadButton