import React, { useState, useEffect, useContext } from 'react'
import { BrowserRouter as Router, Switch, Route, Link, NavLink, Redirect } from 'react-router-dom'
import LoadingSvg from 'components/LoadingSvg'
import Container from 'controls/Container';
import Avatar from './components/Avatar';
import page_404 from '../../page_404'
import Info from './Info'
import A from 'controls/A'
import styled from 'styled-components'
import { theme } from 'Themes'
import Submissions from './Submissions';
import Toggle from 'controls/Toggle';
import Permissions from 'pages/users/profile/Permissions';
import ContainerFooter from 'controls/ContainersFooter';
import { UserContext } from 'contexts/UserContext'
import TabFriends from './TabFriends';
import JN from 'JN'
import H3 from 'controls/H3';

const MDiv = styled.div `
  position: relative;
  max-width: 500px;
  margin: auto;

  @media (max-width: ${theme.mobileSize}) {
    & {
      width: 100%;
    }
  }
`

const FriendsContainer = styled.div `
  position: absolute;
  right: -290px;
  top: -53px;

  @media (max-width: ${theme.mobileSize}) {
    & {
      position: relative;
      right: 0;
      top: 0;
    }
  }
`

const Profile_Nav = styled.div `
  width: 200px;
  max-width: 200px;
  min-width: 200px;
  /* border: 1px solid ${theme.primary}; */
  border-left: none;
  border-bottom: none;
  margin-left: -15px;
  margin-right: 15px;
  padding-left: 15px;
  padding-top: 25px;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.4);
  background: var(--color_b11);
  border-radius: 8px;
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
  /* margin-right: 50px; */
`

const Tab_Container = styled.div `
  display: flex;
  flex-direction: column;
  margin-top: 50px;
  margin-bottom: 50px;
`

const Tab_Action = styled(NavLink) `
  text-decoration: none;
  border: none;
  font-size: 15px;
  font-family: ${theme.fontCaps};
  padding: .8em 1.5em;
  background-color: transparent;
  outline: none;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  color: var(--color_c2);
  cursor: pointer;

  &:disabled {
    background: none;
    color: rgba(0, 0, 0, 0.4);
  }
`

const Profile_page = props => {
  const {match, history} = props
  
  const { user } = useContext(UserContext)

  const [state, setState] = useState({
    user: {
      username: match.params.userName,
      name: '',
      surname: '',
      email: '',
      accepted: 5,
      attempt: 7,
      permission: '',
      online: false,
    },
    // is_loading: user.username !== match.params.userName,
    is_loading: true,
    is_fetching: false,
  })

  const [permission, setPermission] = useState('')

  useEffect(() => {
    if (state.is_fetching) {
      return
    }

    setState({...state, is_loading: true, is_fetching: true})

    document.title = `${match.params.userName}`;
    JN.get(user, `profile/${match.params.userName}`, {},
    (data) => {
      setState({
        ...state,
        user: {username: match.params.userName, ...data},
        is_loading: false,
        is_fetching: false,
      })
    },
    (error) => {

      if (error.status === 404) {
        history.push('/404')
        return
      }
      setState({
        ...state,
        is_loading: false,
        is_fetching: false,
      })
    })

  }, [match.params.userName])

  const on_user_update = updatedUser => {

    setState({
      ...state,
      user: updatedUser,
    })
  }
 
  const set_toggled = () => {
    setPermission(permission === '' ? '' : '')
  }

  const on_update_permission = updatedPermission => {
    setPermission(updatedPermission)
  }

  if (!state.user || state.is_loading) {
    return (
      <Container>
        <div style={{margin: 'auto'}}><LoadingSvg /></div>
      </Container>
    )
  }

  return (
    <>
      <MDiv>
        <div style={{display: 'flex'}}>
          <Info user={state.user} userUpdate={on_user_update} />
        </div>
        
        <FriendsContainer>
          {user && user.username === match.params.userName &&
            <TabFriends />
          }
        </FriendsContainer>

        <Submissions match={match} />

      </MDiv>
    </>
  )
}

export default Profile_page