import React from 'react'
import styled from 'styled-components'
import { theme } from 'Themes'

const MContainer = styled.div `
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */

  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 14px;
  width: ${props => props.width ? props.width : '100%'};


  cursor: pointer;
  * {
    cursor: pointer;
  }
`

const TContainer = styled.div `
  display: flex;
  align-items: center;
  background-color: ${props => props.toggled ? 'var(--color_primary)' : '#a9a9a9'};
  border-radius: 999px;
  height: 22px;
  width: 45px;
  margin-left : 5px;
  margin-right: 5px;
`

const TButton = styled.div `
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background-color: white;
  margin-left: ${props => props.toggled ? '25px' : '2px'};
  transition: margin-left ease 250ms,
              background-color ease 250ms;
`



const Toggle = props => {
  
  
 

  const on_toggle_toggled = e => {
    e.preventDefault()
    props.setToggled()
  }

  return (
    <MContainer width={props.width} onClick={on_toggle_toggled}>
      {props.right ? props.title : ''}
      <TContainer {...props}>
        <TButton {...props} >

        </TButton>
      </TContainer>
      {!props.right ? props.title : ''}
    </MContainer>
  )
}

export default Toggle