import styled, { css } from 'styled-components'
import { Link } from 'react-router-dom'
import { theme } from 'Themes'

export const Table_A = styled(Link) `
  /* font-size: 16px; */
  color: red;
  text-decoration: none;
  color: var(--color_c2);
  display: inline-block;

  &:hover {
    color: var(--color_link);
  }
`

export const Th = styled.th `
  text-align: left;
  ${props => props.center && css `
    text-align: center;
  `}
  ${props => props.autoSize && css `
    width: 99%;
  `}
  width: ${props => props.width};
`

export const Td = styled.td `
  text-align: left;
  ${props => props.center && css `
    text-align: center;
  `}
  font-size: ${props => props.low ? '14px' : '16px'};
`

const Table = styled.table `

  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */


  border-collapse: collapse;
  overflow: hidden;
  margin-top: ${props => props.marginTop ? props.marginTop : '15px'};
  width: 100%;
  padding: 25px;
  padding-top: 25px;
  background-color: transparent;
  border-radius: 8px;

  ${props => props.count > 0 && css`
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  `}

  tr {
    height: 56px;
    background-color: var(--color_b2);
    border-bottom: 1px solid var(--color_b22);

    /* &:hover {
      background-color:  var(--color_b22);
    } */

    

    &:last-child {
      border: none;
    }
  }

  th {
    padding: 8px;
    padding-left: 15px;
    padding-right: 15px;
    font-weight: normal;
    font-size: 16px;
    font-family: ${theme.fontCaps};
    white-space: nowrap;

    color: var(--color_headerText);
    background-color: var(--color_headerBackground);
    border: none;
    
    &:last-child {
      border: none;
    }
  }

  td {
    a {
      width: 100%;
      /* padding: 8px; */
    }

    padding: 8px;
    padding-left: 15px;
    padding-right: 15px;

    white-space: nowrap;
  }
`

export default Table