import React, { useState, useContext, useEffect } from 'react'
import styled, { css } from 'styled-components'
import SampleEditDlg from './SampleEditDlg'
import { theme } from 'Themes'
import { MdDelete, MdAdd } from 'react-icons/md'
import JN from 'JN'
import LoadingSvg from 'components/LoadingSvg'
import { Get_Number } from 'Global_Functions'
import { MessageBoxContext } from 'contexts/MessageBoxContext'
import { UserContext } from 'contexts/UserContext'
import Button from 'controls/Button'
import Checkbox from 'controls/Checkbox'

const Container = styled.div `
  margin: auto;
  margin-top: 10px;
  width: 400px;

  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */

 
`

const Header = styled.div `
  display: flex;

  text-align: left;
  margin: auto;
  margin-top: 1em;
  margin-bottom: 8px;
  border-bottom: none;
  width: 100%;
  overflow: hidden;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
`

const SButton = styled.button ` 
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  background: none;
  width: 100%;
  cursor: pointer;
  color: white;
  padding: .8em 1em;
  font-size: 14px;
  font-family: ${theme.fontCaps};
`

const SamplesContainer = styled.div `
  display: flex;
  text-align: left;
  flex-direction: column;
  width: 100%;
  max-height: 430px;
  overflow: auto;
  margin-bottom: 20px;
  border: 1px solid var(--color_b22);
  border-radius: 8px;
`

const SampleCard = styled.div `
  font-size: 14px;
  display: flex;
  padding: 3px;
  padding-left: 10px;
  align-items: center;
  background-color: transparent;
 
  border-bottom: 1px solid var(--color_b22);

  &, * {
    cursor: pointer;
  }

  &:last-child {
    border-bottom: none;
  }

  label {
    flex-grow: 1;
    margin-left: .5em;
  }

  &:hover {
    transition: background-color ease 250ms;
    background-color: var(--color_b11);

    button {
      transition: opacity ease 250ms;
      opacity: 1;
    }
  }
`
const StyledButton = styled.button `
  border: none;
  outline: none;
  background: none;
  cursor: pointer;
  opacity: 0;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  margin-left: 3px;

  ${props => props.visible && css `
    transition: opacity ease 200ms;
    opacity: 1;
    width: 14;
    margin-left: -7px;
  `};

  &:disabled {
    opacity: 0;
    cursor: default;
  }
`

const SamplesPage = props => {
  const [state, setState] = useState({
    samples: [],
    is_sampleEditDlg_open: false,
    editing_sample_id: -1,
    is_loading: false,
    token: localStorage.getItem('access_token'),
  })

  const { toast } = useContext(MessageBoxContext)
  const { user  } = useContext(UserContext)

  useEffect(() => {
    setState({
      ...state,
      is_loading: true
    })

    //  
    JN.get(user, `admin/problem/${props.id}/samples`, {
      is_admin: 1
    },
    (data) => {
      setState({
        ...state,
        samples: data.samples,
        is_loading: false,
      })
    },
    (error) => {
      console.log(error)
      setState({
        ...state,
        is_loading: false
      })
    })
  }, [])

  const on_sampleEditDlg_close = () => {
    setState({
      ...state,
      is_sampleEditDlg_open: false,
    })
  }

  const on_add_sample = e => {
    setState({
      ...state,
      is_sampleEditDlg_open: true,
      editing_sample_id: -1,
    })
  }

  const on_sample_added = sample => {
    setState({
      ...state,
      samples: [ ...state.samples, sample],
      is_sampleEditDlg_open: false,
    })
  }

  const on_sample_edit = id => e => {
    e.preventDefault()
    setState({
      ...state,
      is_sampleEditDlg_open: true,
      editing_sample_id: id,
    })
  }

  const on_sample_remove = id => e => {
    e.preventDefault()
    if (!window.confirm('  ?!')) {
      return
    }

     //
     JN.delete(user, `admin/sample`, {sample_id: id},
     (data) => {
      toast(' ', 'success')
      setState({
       ...state,
       samples: state.samples.filter((sample) => sample.id !== id)
      })
     },
     (error) => {
      console.log(error)
     })
  }

  const [listDeletingSamples, setListDeletingSamples] = useState([]);
  const [isAllSamplesChecked, setIsAllSamplesChecked] = useState(false);
  const [isDeletingAllSamples, setDeletingAllSamples] = useState(false);

  const onSampleCheckToggled = sample => {

    //
    if (listDeletingSamples.includes(sample.id)) {
      let list = listDeletingSamples.filter(id => id !== sample.id);
      setListDeletingSamples(list);
      if (!list.length) {
        setIsAllSamplesChecked(false);
      }
    }
    //
    else {
      let list = [...listDeletingSamples, sample.id];
      setListDeletingSamples(list);
      if (list.length === state.samples.length) {
        setIsAllSamplesChecked(true);
      }
    }
  }

  const onToggleAllSamplesChecked = () => {
    let checked = !isAllSamplesChecked;

    if (checked) {
      let list = []
      state.samples.map(sample => list.push(sample.id));
      setListDeletingSamples(list);
    }
    else {
      setListDeletingSamples([]);
    }


    setIsAllSamplesChecked(checked);
  }

  
  const on_samples_remove = e => {
    e.preventDefault();

    if (!window.confirm(` ${listDeletingSamples.length}  ?`)) {
      return;
    }

    //  
    setDeletingAllSamples(true);
    JN.delete(user, `/admin/problem/${props.id}/samples`, {samples_list: listDeletingSamples},
      () => {
        setState({...state, samples: state.samples.filter(sample => !listDeletingSamples.includes(sample.id))});
        setListDeletingSamples([]);
        setIsAllSamplesChecked(false);
        setDeletingAllSamples(false);
      },
      () => {
        setDeletingAllSamples(false);
        toast('  ', 'error');
      });
  }

  return (  
    <Container>
      {state.is_loading ? 
      <div style={{marginTop: '10px'}}>
        <LoadingSvg />
      </div>
      :<>
      <Header>
        <div style={{opacity: state.samples.length > 1 ? 1 : 0, marginLeft: 11, display: 'flex', flexDirection: 'column'}}>
          <div>
            
          </div>
          {isDeletingAllSamples ? 
            <StyledButton visible style={{pointerEvents: 'none'}}>
              <LoadingSvg  size="18" />
            </StyledButton>
            :
            <StyledButton disabled={!listDeletingSamples.length} visible onClick={on_samples_remove}>
              <MdDelete color="var(--color_red)" size="20" />
            </StyledButton>
          } 
          <Checkbox onCheckedChanged={onToggleAllSamplesChecked} checked={isAllSamplesChecked} />
        </div>
        <div style={{marginLeft: 70, marginTop: 5}}>
        <Button onClick={on_add_sample}>
          <MdAdd size="22" />  
        </Button>
        </div>
      </Header>
      <SamplesContainer>
        {state.samples.map((sample, index) =>
        <SampleCard key={sample.id}>
          {state.samples.length > 1 && <Checkbox checked={listDeletingSamples.includes(sample.id)} onCheckedChanged={() => onSampleCheckToggled(sample)} />}
          <label onClick={on_sample_edit(sample.id)}> {Get_Number(index + 1)}</label>
          {/* <StyledButton onClick={on_sample_edit(sample.id)}>
            <MdEdit color={theme.orange} size="20" />
          </StyledButton> */}
          <StyledButton onClick={on_sample_remove(sample.id)}>
            <MdDelete color="var(--color_red)" size="20" />
          </StyledButton>
        </SampleCard>
        )}
      </SamplesContainer>
      </>}
      {state.is_sampleEditDlg_open &&
        <SampleEditDlg sample_id={state.editing_sample_id} problem_id={props.id} sampleAdded={on_sample_added} close={on_sampleEditDlg_close} />
      }
    </Container>
  )
}

export default SamplesPage