import React, { useState, useEffect, useContext} from 'react'
import { UserContext } from 'contexts/UserContext'
import styled from 'styled-components'
import JN from 'JN'
import Container from 'controls/Container'
import { Redirect } from 'react-router-dom'
import { theme } from 'Themes'
import ContainerFooter from 'controls/ContainersFooter'
import Input from 'controls/Input'
import Button from 'controls/Button'
import DatePicker, { getDefaultLocale } from 'react-datepicker'
import 'react-datepicker/dist/react-datepicker.css'
import Problems from './Problems'
import { MdRemoveRedEye } from 'react-icons/md'
import Button_Float from 'controls/Button_Float'
import { MessageBoxContext } from 'contexts/MessageBoxContext'
import './datetimepicker.css'
import Grid from 'controls/Grid'
import ComboBox from 'controls/ComboBox'
import TimeWidget from 'controls/TimeWidget'
import { Get_dateDiff_toString, dateAdd } from 'Global_Functions'

// import 'date-fns';
// import Grid from '@material-ui/core/Grid';
// import DateFnsUtils from '@date-io/date-fns';
// import {
//   MuiPickersUtilsProvider,
//   KeyboardTimePicker,
//   KeyboardDatePicker,
// } from '@material-ui/pickers';


const DTInput = styled.button `
  border: 1px solid #b8b8b8;
  border-radius: 5px;
  height: 56px;
`

const StyledForm = styled.form `
  /* animation: .3s ease-in-out both fade-in; */
  @keyframes fade-in {
    0% {
      opacity: 0;
      /* transform: scale(0); */
    }
    
    100% {
      opacity: 1;
      /* transform: scale(1); */
    }
  }

  height: 530px;
  /* background-color: red; */
  
`

const Tab_Container = styled.div `
  display: flex;
`

const Tab_Action = styled.button `
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */

  cursor: pointer;
  border: none;
  outline: none;
  text-decoration: none;
  font-size: 14px;
  font-family: ${theme.fontCaps};
  border: 1px solid var(--color_inputBackground);
  padding: .8em 1.5em;
  height: 50px;
  outline: none;
  border-top-left-radius: 5px;
  border-radius: 5px;
  margin-right: 5px;
  margin-bottom: 50px;
  color: var(--color_c2);
  background-color: transparent;
  cursor: pointer;

  &:disabled {
    background: none;
    color: var(--color_c3);
    border: 1px solid transparent;
  }
`

const AddContest = props => {
  const { match } = props

  const { user }  = useContext(UserContext)
  const { toast } = useContext(MessageBoxContext)

  const [state, setState] = useState({
    contest: {title: ''},
    currentDurattion: {name: '2', h: 2, m: 0},
    is_uploading: false,
  })
  const [startDate, setStartDate] = useState(new Date());
  const [endDate,   setEndDate]   = useState(new Date());

  const [tab, setTab] = useState(1)

  useEffect(() => {

    if (!user) {
      return
    }
  
    if (match.params.id) {
      document.title = ` `;
      // 
      JN.get(user, 'admin/contest', {contest_id: match.params.id},
      (data) => {
        setState({...state, contest: {id: match.params.id, title: data.title}})
        setStartDate(new Date(data.start_time))
        setEndDate(new Date(data.end_time))
      },
      () => {

      })
    }
    else {
      document.title = ` `;
      on_combo_itemSelected(state.currentDurattion)
    }

  }, [])

  const on_change = e => {
    setState({
      ...state,
      contest: {...state.contest, title: e.target.value},
    })
  }

  const submit = e => {
    e.preventDefault()

    setState({...state, is_uploading: true})
    if (!state.contest.id) {

      if (startDate >= endDate) {
        toast('  ', 'error')
        setState({...state, is_uploading: false})
        return
      }

      //
      JN.post(user, 'admin/contest', {
        title: state.contest.title,
        start_time: startDate,
        end_time: endDate,
      },
      (data) => {
        toast('', 'success')
        setState({
          ...state,
          contest: {...state.contest, id: data.id},
          is_uploading: false,
        })

        setTab(2)
      },
      (error) => {
      
        if (error.status === 400) {
          toast('  ', 'error')
        }
        setState({...state, is_uploading: false})
      })
    }
    else {

      //
      JN.put(user, 'admin/contest', {
        contest_id: state.contest.id,
        title: state.contest.title,
        start_time: startDate,
        end_time: endDate,
      },
      () => {
        toast('', 'success')
        setState({...state, is_uploading: false})
      },
      () => {
        setState({...state, is_uploading: false})
      })
    }
  }

  const on_startDate_changed = date => {

    setStartDate(date)
    if (endDate < date)
      setEndDate(date.add)

    date = dateAdd(date, 'hour', state.currentDurattion.h)
    date = dateAdd(date, 'minute', state.currentDurattion.m)
    setEndDate(date)
  }

  const on_combo_itemSelected = item => {
    setState({...state, currentDurattion: item})

    let date = startDate
    date = dateAdd(date, 'hour', item.h)
    date = dateAdd(date, 'minute', item.m)
    setEndDate(date)
  }
  
  let currentTab =
  <>
    <StyledForm onSubmit={submit}>
      <Input autocomplete="off" focus required name="title" title="" value={state.contest.title} onChange={on_change} />
  

      <div style={{marginTop: '25px', marginBottom: '155px'}}>
        <Grid columns="3" gap="15px">
          <div style={{marginTop: '15px', marginBotton: '250px', display: 'flex', flexDirection: 'column', justifyContent: 'space-between', textAlign: 'left', fontSize: '14px', paddingLeft: '0px', height: '42px'}}>
            <label></label>
            <DatePicker
              selected={startDate}
              onChange={on_startDate_changed}
              showTimeSelect
              timeFormat="HH:mm"
              timeIntervals={15}
              timeCaption="time"
              dateFormat="HH:mm dd/MM/yyyy"
              minDate={new Date()}
              // withPortal
              className="dt_input"
            />
          </div>
          <div style={{marginTop: '15px', marginBotton: '25px', display: 'flex', flexDirection: 'column', justifyContent: 'space-between', textAlign: 'left', fontSize: '14px', paddingLeft: '4px', height: '42px'}}>
            <label></label>
            <DatePicker
              selected={endDate}
              onChange={date => setEndDate(date)}
              showTimeSelect
              timeFormat="HH:mm"
              timeIntervals={15}
              timeCaption="time"
              dateFormat="HH:mm dd/MM/yyyy"
              minDate={startDate}
              // withPortal
              className="dt_input"
            />
          </div>
          <div style={{marginTop: '15px', marginBotton: '25px', display: 'flex', flexDirection: 'column', justifyContent: 'space-between', textAlign: 'left', fontSize: '14px', paddingLeft: '4px', height: '42px'}}>
            <label></label>
            <ComboBox input
              currentItem={state.currentDurattion.name}
              items={[
                {
                  name: '15',
                  h: 0,
                  m: 15,
                },
                {
                  name: '30',
                  h: 0,
                  m: 30,
                },
                {
                  name: '1',
                  h: 1,
                  m: 0,
                },
                {
                  name: '1 30',
                  h: 1,
                  m: 30,
                },
                {
                  name: '2',
                  h: 2,
                  m: 0,
                },
                {
                  name: '2  30',
                  h: 2,
                  m: 30,
                },
                {
                  name: '3',
                  h: 3,
                  m: 0,
                },
                {
                  name: '3 30',
                  h: 3,
                  m: 30,
                },
                {
                  name: '4',
                  h: 4,
                  m: 0,
                }
              ]}
              itemSelected={on_combo_itemSelected}
            />
          </div>
        </Grid>
        <div style={{height: '56px', marginTop: '85px', fontSize: '24px'}}><TimeWidget size="18px" time={Get_dateDiff_toString(startDate, endDate, false, true)}/></div>
      </div>
      
      <ContainerFooter>
        <Button type="submit" backgroundColor="primary" disabled={state.is_uploading}>
          {state.contest.id ? "" : ""}{state.is_uploading ? '..' : ''}
        </Button>
      </ContainerFooter>

    </StyledForm>
  </>

  if (tab === 2) {
    currentTab = <Problems id={state.contest.id} />
  }


  const on_tab_changed = tab => e => {
    e.preventDefault()
    setTab(tab)
  }

  const Tab_Current = {pointerEvents: 'none', backgroundColor: 'var(--color_b1)'}

  if (!user || user.permission === "user") {
    return (
      <Redirect to="/404" />
    )
  }

  return (
    <Container title={match.params.id ? " " : " "} width="600px">
      <Tab_Container>
        <Tab_Action style={tab === 1 ? Tab_Current : {}} onClick={on_tab_changed(1)}></Tab_Action>
        <Tab_Action style={tab === 2 ? Tab_Current : {}} disabled={!state.contest.id} onClick={on_tab_changed(2)}></Tab_Action>
      </Tab_Container>
      {currentTab}

      {state.contest.id && 
        <Button_Float count="1" to={`/contest/${state.contest.id}`}>
          <MdRemoveRedEye color="var(--color_primary)" size="22" />
        </Button_Float>
      }
    </Container>
  )
}

export default AddContest