import React, { useState, useEffect, useContext } from 'react'
import { UserContext } from 'contexts/UserContext'
import Button from 'controls/Button'
import ProblemEditDlg from './ProblemEditDlg'
import JN from 'JN'
import styled from 'styled-components'
import { MdEdit, MdDelete, MdAdd } from 'react-icons/md'
import LoadingSvg from 'components/LoadingSvg'
import { Get_Number } from 'Global_Functions'
import { Table_A } from 'controls/Table'

const Main = styled.div `

  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */
`

const Header = styled.div `
  text-align: left;
  margin: auto;
  margin-top: 2em;
  overflow: hidden;
`

const StyledButton = styled.button `
  border: none;
  outline: none;
  background: none;
  cursor: pointer;
  opacity: 1;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  margin-left: 3px;
`

const Problem_table = styled.table `

  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */


  margin: auto;
  margin-top: 30px;
  width: 100%;
  /* min-width: 700px; */
  /* max-height: 200px; */
  /* overflow: auto; */
  border-collapse: collapse;
  font-size: 14px;


  margin-bottom: 30px;

  /* border: 1px solid transparent; */
  border-radius: 8px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.4);
  background-color: var(--color_b11);
  color: var(--color_c2);
  overflow: hidden;

  * {
    cursor: pointer;
  }

  tbody {
    display: block;
    overflow: auto;
  }

  tr {
    width: 100%;
    height: 50px;
  }

  tr:hover {
    /* background-color: #f1f1f1; */
  }

  tr:first-child:hover {
    background: none;
  }

  th {
    color: red;
    font-size: 14px;
    font-weight: normal;
    border: none;
    color: var(--color_c2);
    /* border-bottom: 1px solid var(--color_c3); */
    padding: 5px;
    height: 16px;
    background-color: var(--color_b1);

    &:nth-child(2) {
      text-align: left;
      padding-left: 15px;
      width: 99%;
    }

    padding-left: 15px;
    padding-right: 15px;
    &:first-child {
      padding-left: 15px;
    }
  }

  td {
    text-align: center;
    padding: 7px;
    color: var(--color_c2);
    font-size: 14px;
    background-color: var(--color_b11);
    padding-left: 15px;
    padding-right: 15px;
    white-space: nowrap;

    &:nth-child(2) {
      text-align: left;
    }
  }
`

const Problems = props => {

  const { user } = useContext(UserContext)

  const [state, setState] = useState({
    problems: [],
    editingProblem: null,
    is_problemEditDlg_visible: false,
    is_loading: true,
  })

  useEffect(() => {

    setState({...state, is_loading: true,})

    JN.get(user, 'admin/contest/append', {contest_id: props.id},
    (data) => {
      setState({
        ...state,
        problems: data.problems,
        is_loading: false,
      })
    },
    () => {
      setState({...state, is_loading: false,})
    })

    // axios.get(`${Server.address}/admin/contest/append`, {
    //   headers: {
    //     Authorization: 'Bearer ' +  user.access_token
    //   },
    //   params: {
    //     contest_id: props.id,
    //   }
    // })
    // .then(response => {
    //     console.log(response)
    //     setState({
    //       ...state,
    //       problems: response.data.problems,
    //       is_loading: false,
    //     })
    // })
    // .catch(error => {
    //     console.log(error)
    //     setState({...state, is_loading: false,})
        
        
    // })

  }, [])

  const on_btnAddProblem_clicked = e => {
    setState({
      ...state,
      editingProblem: null,
      is_problemEditDlg_visible: true,
    })
  }

  const on_problem_edit = problem => e => {
    e.preventDefault()

    setState({
      ...state,
      editingProblem: problem,
      is_problemEditDlg_visible: true,
    })
  }

  const on_problem_remove = id => e => {
    e.preventDefault()
    if (!window.confirm('  ?!')) {
      return
    }

    setState({...state, problems: state.problems.filter((problem, index) => problem.problem_id !== id)})

    JN.delete(user, 'admin/contest/append', {
      contest_id: props.id,
      problem_id: id,
    })
  }

  const on_problemEditDlg_close = () => {
    setState({...state, is_problemEditDlg_visible: false,})
  }

  return (
    <Main>
      {state.is_loading ? 
        <div style={{marginTop: '10px'}}>
          <LoadingSvg />
        </div>
      :
      <>
        <Header>
          <Button onClick={on_btnAddProblem_clicked} icon={<MdAdd size="22" />}> </Button>
        </Header>
        
        {state.problems.length > 0 && <Problem_table>
          <thead></thead>
          <tbody>
            <tr>
              <th>id</th>
              <th></th>
              <th></th>
              {user && user.permission !== "user" &&
              <>
                <th><MdEdit size="18" /></th>
                <th><MdDelete size="18" /></th>
              </>
              }
            </tr>
          {state.problems.map((problem, index) =>
            <tr key={problem.problem_id}>
              <td onClick={on_problem_edit(problem)}>{Get_Number(problem.problem_id)}</td>
              <td><Table_A to={`/problem/${problem.problem_id}`}>{problem.title}</Table_A></td>
              <td onClick={on_problem_edit(problem)}>{problem.score}</td>
              {user && user.permission !== "user" &&
              <>
                <td><StyledButton onClick={on_problem_edit(problem)}><MdEdit color="var(--color_orange)" size="20" /></StyledButton></td>
                <td><StyledButton onClick={on_problem_remove(problem.problem_id)}><MdDelete color="var(--color_red)" size="20" /></StyledButton></td>
              </>
              }
            </tr>
            )}
          </tbody>
        </Problem_table>}
     
        {state.is_problemEditDlg_visible &&
          <ProblemEditDlg
          contestId={props.id}
          state={state}
          setState={setState}
          problem={state.editingProblem}
          isOpen={state.is_problemEditDlg_visible}
          close={on_problemEditDlg_close}
        />
        }
      </>
      }
    </Main>
  )
}

export default Problems