import React, { useState, useEffect, useContext, useRef } from 'react'
import A from 'controls/A'
import { theme } from 'Themes'
import styled, { css } from 'styled-components'
import { Link, NavLink, Redirect } from 'react-router-dom'
import { MdAdd, MdDone, MdDelete, MdEdit, MdArrowDropUp, MdArrowDropDown } from 'react-icons/md'
import { Get_Number } from 'Global_Functions'
import { UserContext } from 'contexts/UserContext'
import { MessageBoxContext } from 'contexts/MessageBoxContext'
import Button_Float from 'controls/Button_Float'
import PaginationContainer from 'controls/pagination/PaginationContainer'
import LoadingSvg from 'components/LoadingSvg'
import JN from 'JN'
import { FaStar, FaCheck, FaTimes, FaSearch, FaMinus, FaHistory, FaRegStar, FaHeart, FaRegHeart } from 'react-icons/fa'
import Button from 'controls/Button'
import Input from 'controls/Input'
import Helmet from 'react-helmet'
import B from 'controls/B'
import BoldA from 'controls/BoldA'

const MainDiv = styled.div`
  /* background-color: red; */
  box-shadow: 0 6px 16px 0 rgba(0,0,0,.2);
  margin-top: 45px;
  border-radius: 8px;
`

const HoverScaledSpan = styled.span`
  display: flex;
  align-items: center;
  &:hover > svg {
    transition: transform ease 250ms;
    transform: scale(1.4);
  }
`

const FilterContainer = styled.div`
  position: absolute;
  top: 55px;
  background-color: var(--color_b2);
  /* box-shadow: 0 1px 5px rgba(0, 0, 0, .5); */
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  overflow: auto;
  max-height: 250px;
  width: ${props => props.width ? props.width : '100%'};
  z-index: 1;
`

const FilterItem = styled(Link)`
  display: block;
  ${props => props.flex && css`
    display: flex;
    justify-content: flex-start;  

    button {
      margin-left: auto;
    }
  `}

  text-decoration: none;
  color: var(--color_c2);
  font-family: ${theme.font};
  
  padding-left: 10px;
  padding-right: 10px;
  /* height: 50px; */
  line-height: 36px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  outline: none;

  &:hover, &:focus {
    color: var(--color_link);
  }
`

const FavouritesConteiner = styled.div`
  text-align: left;
  /* background-color: red; */
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;

  #searchContainer {
    width: 400px;
    margin-right: 5px;
  }

  @media (max-width: ${theme.mobileSize}) {
    & {
      flex-direction: column;
    }

    margin-top: -90px;

    #topNav {
      width: 100%;
      flex-direction: column;
      padding-left: 8px;
      margin-bottom: 36px;
      button {
        width: 250px !important;
        i {
          display: none;
        }
      }
    }

    #searchContainer {
      width: 100%;
      padding-right: 8px;
      padding-left: 16px;
    }
  }
`

const MDiv = styled.div`
  padding: 56px;
  padding-top: -0px;
  margin: auto;
  margin-top: -56px;
  max-width: 1366px;
  @media (max-width: ${theme.mobileSize}) {
    padding: 0;
  }
`

const Table_A = styled(Link)`
  /* font-size: 14px; */
  color: red;
  text-decoration: none;
  color: var(--color_c2);
  display: inline-flex;
  align-items: center;
  &:hover {
    color: var(--color_link);
  }

  ${props => props.disabled && css`
    pointer-events: none;
    color: var(--color_c3);
  `}

  ${props => props.tag && css`
    @media only screen and (max-width: ${theme.mobileSize}) {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      color: var(--color_c2);
      background-color: var(--color_b1);
      border: 3px solid var(--color_b22);
      padding-left: 15px;
      padding-right: 15px;
      margin-right: 8px;
      margin-bottom: 8px;
      height: 36px;
      border-radius: 5px;
    }
  `}
`

const Ln = styled.input`

`

const TrLoading = styled.tr`
  animation: anim1 1s infinite;
  @keyframes anim1 {
    0% {
      background-color: var(--color_b22);
      /* opacity: 0; */
      /* transform: scale(0); */
    }
    
    100% {
      background-color: var(--color_b2);
      /* opacity: 1; */
      /* transform: scale(1); */
    }
  }
`

const Problem_table = styled.table`
  border-collapse: collapse;
  overflow: hidden;

  width: 100%;
  padding: 25px;
  padding-top: 25px;
  background-color: transparent;
  border-radius: 8px;
  border-bottom: 0;

  ${props => props.count > 0 && css`
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  `}

  tr {
    height: 42px;
    background-color: var(--color_b2);
    border-bottom: 1px solid var(--color_b22);

    &:first-child {
      height: 56px;
      background-color: transparent;
      border-bottom: 0px solid #ddd;      
      color: var(--color_b2);
    }

    &:last-child {
      border: none;
    }
  }

  th {
    padding: 8px;
    font-family: ${theme.fontCaps};
    font-weight: normal;
    font-size: 14px;
    color: var(--color_headerText);
    background-color: var(--color_headerBackground);
    /* white-space: nowrap; */

    &:first-child {
      padding-left: 20px;
    }

    &:last-child {
      padding-right: 20px;
    }

    &:nth-child(3), &:nth-child(4) {
      text-align: left;
    }
    
    &:nth-child(3) {
      
      /* width: 99%; */
    }

    &:nth-child(7) {
      text-align: right;
      justify-content: right;
    }
  }

  td {
    padding: 8px;
    padding-right: 15px;
    font-size: 14px;
    color: var(--color_c2);
    /* white-space: nowrap; */

    &:nth-child(1) {
      color: var(--color_c2);
      font-size: 14px;
      width: 1px;
      padding-left: 20px;
    }

    &:nth-child(2) {
      padding: 0;
      text-align: center;
    }

    &:last-child {
      padding-right: 20px;
    }

    &:nth-child(3) {
      text-align: left;
      padding: 0;

      a {
        width: 100%;
        padding: 8px;
      }
    }
    
    &:nth-child(4) {
      font-size: 14px;
      color: var(--color_c2);
      text-align: left;
      width: 450px;
    }

    &:nth-child(2) {
      
    }
    &:first-child, &:nth-child(2), &:nth-child(5), &:nth-child(6), &:nth-child(7) {
      width: 1px;
    }
  }

  @media only screen and (max-width: ${theme.mobileSize}) {
    tr {
      &:first-child {
        display: none;
      }

      min-height: 125px;

      /* margin-bottom: 8px; */
      border-bottom-width: 8px;
      &:last-child {
        /* margin-bottom: 0; */
        border-bottom-width: 0px;
      }

      height: inherit;
      display: grid;
      grid-template-columns: auto auto auto 100%;
      grid-template-rows: auto;
      grid-template-areas: 
        "id title title title"
        "categories categories categories categories"
        "views favourite like c2";
    }

    td {
      &:nth-child(1) {
        grid-area: id;
        font-size: 14px;
        padding-top: 9px;
        padding-left: 8px;
        padding-right: 5px;
        justify-content: center;
        align-items: center;
        display: block;
        text-align: right;
        width: fit-content;
      }

      &:nth-child(2) {
        grid-area: c2;
        width: auto;
        padding: 0;
        margin-top: -5px;
        font-size: 16px;
        display: flex;
        align-items: center;
      }
    
      &:nth-child(3) {
        grid-area: title;
        padding-left: -15px;
        margin-top: 8px;
        margin-left: -5px;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden; 
      }

      &:nth-child(4) {
        display:block;
        grid-area: categories;
        width: 90%;
      }

      &:nth-child(5) {
        grid-area: views;
        width: 0;
        margin-right: 8px;
      }

      &:nth-child(6) {
        grid-area: favourite;
        width: auto;

      }

      &:nth-child(7) {
        grid-area: like;
        width: auto;
      }
    }
  }
`

const Grid = styled.div`
  display: grid;
  grid-template-columns: auto;
  grid-row-gap: 0px;
  grid-column-gap: 0px;
  @media only screen and (max-width: ${theme.mobileSize}) {
    grid-template-columns: auto;
  }
`

const Header = styled.div`
  display: flex;
  padding-bottom: 5px;
`

const Loading = styled.div`
  text-align: center;
`

function Problems(props) {
  const { match, history } = props;

  const { user } = useContext(UserContext);
  const { toast } = useContext(MessageBoxContext);

  const refTable = useRef(null)

  const [state, setState] = useState({
    problems: [],
    isLoading: true,
    pagination: { current: 0, total: 0 },
    is_error: false,
    order_by_accepted: null,
  })

  const [search, setSearch] = useState('')
  const [filter, setFilter] = useState('')
  const [filteredProblems, setFilteredProblems] = useState([])

  useEffect(() => {

    // if (refTable.current) window.scrollTo(0, refTable.current.offsetTop)
    on_refresh()

  }, [match.url])

  const on_refresh = () => {

    setState({
      ...state,
      isLoading: true,
    })

    JN.get(user, `problems/${match.params.page}`, {
      template: search,
      tag_id: match.params.tagId,
      order_by_accepted: state.filter_by_accepted,
    },
      (data) => {

        window.scrollTo(0, 0)

        setState({
          ...state,
          problems: data.problems,
          isLoading: false,
          page_count: data.page_number,
          pagination: { current: match.params.page, total: data.page_number }
        })

        if (match.params.tagId) {
          setSearch('')
        }
      },
      () => {
        setState({
          ...state,
          problems: [],
          isLoading: false,
        })
      })
  }

  const on_filter_by_accepted_toggled = e => {
    state.filter_by_accepted = state.filter_by_accepted === "D" ? "A" : "D"
    on_refresh()
  }

  const on_problem_delete = id => e => {
    // console.log(state)
    e.preventDefault()
    if (!window.confirm('  ?!')) {
      return
    }

    setState({
      ...state,
      isLoading: true,
    })

    JN.delete(user, `admin/problem/${id}`, {}, () => {
      setState({
        ...state,
        isDeleting: false
      })
      on_refresh()
    },
      () => {
        setState({
          ...state,
          isLoading: false,
        })
      })

  }

  const onProblemDeleted = (id) => {
    setState({
      ...state,
      problems: state.problems.filter((problem, index) => problem.id !== id),
    })
  }

  const on_search_clear = e => {
    // setSearch('')
  }


  const [lastFilterToken, setLastFilterToken] = useState(new Date())
  const [lastRequestToken, setLastRequestToken] = useState(null)

  const [filterText, setFilterText] = useState('')

  const onFilterChanged = e => {
    let ft = e.target.value

    if (filterText.length > 50)
      return

    setFilter(ft)
    if (ft === '') {
      setFilteredProblems([])
      return
    }

    let lft = new Date()
    setLastFilterToken(lft)
    setLastRequestToken(lft)
    setFilterText(ft)
  }

  useEffect(() => {


    if (lastFilterToken !== lastRequestToken)
      return () => { }

    const timer = setTimeout(() => {

      JN.get(user, `problems/1`, { template: filterText },
        (data) => {
          setFilteredProblems(data.problems.slice(0, 5))
        },
        () => setFilteredProblems([]))

    }, 500)


    return () => { clearTimeout(timer); }

  }, [lastFilterToken])

  const onHideFilter = (e) => {
    if (e.currentTarget.contains(e.relatedTarget))
      return

    setFilteredProblems([])

    if (filter !== '') {
      setFilter('')
    }
  }

  const refFilteredProblems = [useRef(null), useRef(null), useRef(null), useRef(null), useRef(null)]

  const onKeyDown = e => {
    if (!filteredProblems.length)
      return

    let ARROW_UP = 38;
    let ARROW_DOWN = 40;
    if (e.keyCode !== ARROW_UP && e.keyCode !== ARROW_DOWN)
      return;

    e.preventDefault();

    let up = e.keyCode === ARROW_UP;

    let activeElement = document.activeElement;
    let nextIndex = 0;

    let i = refFilteredProblems.findIndex(ref => ref.current === activeElement);
    if (i === -1 || i === 0) {
      nextIndex = up ? filteredProblems.length - 1 : i + 1;
    }
    else if (i === filteredProblems.length - 1) {
      nextIndex = up ? i - 1 : 0;
    }
    else {
      nextIndex = up ? i - 1 : i + 1;
    }

    if (refFilteredProblems[nextIndex] && refFilteredProblems[nextIndex].current) {
      refFilteredProblems[nextIndex].current.focus();
    }
  }

  useEffect(() => {
    if (user) {
      JN.get(user, `user/noted_problems`, {}, (data) => setFavourites(data.note_problems))
    }
  }, [])

  const refFaviourites = useRef(null);
  const [isFavouritesVisible, setIsFavouritesVisible] = useState(false);
  const [favourites, setFavourites] = useState([]);

  const isFavourite = id => {
    let i = 0;
    for (i; i < favourites.length; i++) {
      if (favourites[i].id === id) {
        return true;
      }
    }
    return false;
  }

  const [isLikeUpdating, setIsLikeUpdating] = useState(false);
  const onToggleHeart = problem => {
    if (!user || isLikeUpdating) {
      return;
    }

    setIsLikeUpdating(true);
    if (problem.liked) {
      setState({ ...state, problems: state.problems.map(p => p.id !== problem.id ? p : { ...problem, liked: false, likes: problem.likes - 1 }) });
      JN.delete(user, `problem/${problem.id}/like`, {}, () => setIsLikeUpdating(false), () => {
        setIsLikeUpdating(false);
        setState({ ...state, problems: state.problems.map(p => p.id !== problem.id ? p : { ...problem, liked: true, likes: problem.likes + 1 }) });
      });
    }
    else {
      setState({ ...state, problems: state.problems.map(p => p.id !== problem.id ? p : { ...problem, liked: true, likes: problem.likes + 1 }) });
      JN.post(user, `problem/${problem.id}/like`, {}, () => setIsLikeUpdating(false), () => {
        setIsLikeUpdating(false);
        setState({ ...state, problems: state.problems.map(p => p.id !== problem.id ? p : { ...problem, liked: false, likes: problem.likes - 1 }) });
      });
    }
  }

  const onToggleFavourites = problem => {

    if (isFavourite(problem.id)) {
      // 
      setFavourites(favourites.filter(p => p.id !== problem.id));
      JN.delete(user, `user/noted_problems`, { problem_id: problem.id });
    }
    else {
      // 
      setFavourites([...favourites, { id: problem.id, title: problem.title, verdict: problem.verdict }]);
      JN.post(user, `user/noted_problems`, { problem_id: problem.id }, null, (error) => {
        if (error.status === 403) {
          setFavourites(favourites.filter(p => p.id !== problem.id));
          toast('   !', 'error');
        }
      });
    }
  }

  const onToggleFavouritesVisible = e => {
    if (!e.currentTarget.contains(e.relatedTarget)) {
      setIsFavouritesVisible(!isFavouritesVisible);
    }
  }

  const onHideFavourites = e => {
    if (isFavouritesVisible && !e.currentTarget.contains(e.relatedTarget)) {
      setIsFavouritesVisible(false);
    }
    e.preventDefault();
  }

  if (state.is_error) {
    return (
      <Redirect to="/404" />
    )
  }

  return (
    <MDiv>
      <Helmet>
        <meta charSet="utf-8" />
        <title>{match.params.page == 1 ? '' : ` . ${match.params.page}`}</title>
        <meta name="description" content="AiaSoft" />
      </Helmet>
      {/* <Container padding="36px" width="98%" title=""> */}

      {/* <div ref={refTable} style={{height: '1px', marginBottom: '25px'}}></div> */}
      <FavouritesConteiner>
        <div id="topNav" onBlur={onHideFavourites} style={{ display: 'flex' }}>

          <A to="/submissions/0/1"><Button icon={<FaHistory size="16px" />}> </Button></A>

          {user && favourites.length > 0 &&
            <span style={{ marginLeft: '5px', position: 'relative', maxWidth: '100%' }}>
              <Button ref={refFaviourites} marginLeft="0" onClick={onToggleFavouritesVisible} width="180px" icon={<FaStar size="16px" />}> ({favourites.length})</Button>
              {isFavouritesVisible &&
                <FilterContainer width="400px" onKeyDown={onKeyDown}>
                  {favourites.map(problem =>
                    <FilterItem key={problem.id} to={`/problem/${problem.id}`} flex="true">

                      {problem.verdict === 1 ?
                        <span style={{ display: 'flex', alignItems: 'center' }}>
                          <FaCheck color="var(--color_green)" size="12" />&nbsp;
                    </span>
                        :
                        problem.verdict === 2 ?
                          <span style={{ display: 'flex', alignItems: 'center' }}>
                            <FaTimes color="var(--color_red)" size="12" />&nbsp;
                    </span>
                          : <span style={{ width: '18px' }}></span>
                      }

                      {problem.id}.&nbsp;<span style={{ overflow: 'hidden', textOverflow: 'ellipsis' }}>{problem.title}</span>

                      <button onClick={(e) => { e.preventDefault(); onToggleFavourites(problem); if (refFaviourites && refFaviourites.current) refFaviourites.current.focus(); }} style={{ cursor: 'pointer', border: 'none', outline: 'none', background: 'none', color: 'var(--color_c2)' }}><FaTimes /></button>
                    </FilterItem>
                  )}
                </FilterContainer>
              }
            </span>
          }
        </div>
        <div id="searchContainer" onBlur={onHideFilter}>
          <Input reverse onKeyDown={onKeyDown} fontSize="14px" onChange={onFilterChanged} value={filter} placeholder="   ..">
            {filter !== '' &&
              <FilterContainer onKeyDown={onKeyDown}>
                {!filteredProblems.length ?
                  <div>

                  </div>
                  :
                  <>
                    {filteredProblems.map((problem, index) =>
                      <FilterItem flex="true" ref={refFilteredProblems[index]} key={problem.id} to={`/problem/${problem.id}`}>
                        {user && <>
                          {problem.verdict === 1 ?
                            <span style={{ display: 'flex', alignItems: 'center' }}>
                              <FaCheck color="var(--color_green)" size="12" />&nbsp;
                          </span>
                            :
                            problem.verdict === 2 ?
                              <span style={{ display: 'flex', alignItems: 'center' }}>
                                <FaTimes color="var(--color_red)" size="12" />&nbsp;
                          </span>
                              : <span style={{ width: '18px' }}></span>
                          }
                        </>}

                        {problem.id}.&nbsp;<span style={{ overflow: 'hidden', textOverflow: 'ellipsis' }}>{problem.title}</span>

                        {user && <span onClick={(e) => { e.preventDefault(); onToggleFavourites(problem) }} style={{ marginLeft: 'auto', display: 'inline-flex', alignItems: 'center' }}>
                          {isFavourite(problem.id) ?
                            <FaStar color="var(--color_orange)" size="18" />
                            : <FaRegStar color="var(--color_c2)" size="18" />
                          }
                        </span>}

                      </FilterItem>
                    )}
                  </>
                }
              </FilterContainer>
            }
          </Input>
        </div>
      </FavouritesConteiner>

      {user && user.permission !== "user" &&
        <Button_Float to="/addproblem"><MdAdd color="var(--color_green)" size="22" /></Button_Float>
      }

      {/* {state.isLoading && <LoadingSvg />} */}

      <MainDiv>
        <Grid>

          <Problem_table count={state.pagination.total > 1}>
            <thead></thead>
            <tbody>
              {state.problems.length === 0 ? <tr><th></th><th style={{ align: 'left' }} colSpan="15"></th></tr> :
                <tr>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th style={{ height: '56px', cursor: 'pointer', display: 'flex', justifyContent: 'center', alignItems: 'center' }} onClick={on_filter_by_accepted_toggled}>
                    <A to={`/problems/1${match.params.tagId ? '/' + match.params.tagId : ''}`}>
                      <MdDone color="var(--color_headerText)" size="18" />
                      {state.filter_by_accepted && (state.filter_by_accepted === 'A' ? <MdArrowDropUp size="22" color={`var(--color_headerText)`} /> : <MdArrowDropDown size="22" color={`var(--color_headerText)`} />)}
                    </A>
                  </th>
                  {user &&
                    <>
                      <th><span style={{ display: 'flex' }}><FaStar color="var(--color_headerText)" size="16" /></span></th>

                    </>
                  }
                  <th><span style={{ display: 'flex', justifyContent: 'center' }}><FaHeart color="var(--color_headerText)" size="16" /></span></th>
                </tr>
              }

              {!state.isLoading && state.problems.length === 0 &&
                <tr><td colSpan="10" style={{ color: 'var(--color_c2)', fontSize: '14px', textAlign: 'left' }}>
                     <span style={{ color: 'red' }}>!</span>
                </td></tr>
              }

              {state.problems.length === 0 && state.isLoading &&
                <>
                  <TrLoading><td colSpan="8" style={{ fontSize: '14px', textAlign: 'left' }}></td><td></td></TrLoading>
                  <TrLoading><td></td><td colSpan="8" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></TrLoading>
                  <TrLoading><td></td><td colSpan="8" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></TrLoading>
                  <TrLoading><td></td><td colSpan="8" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></TrLoading>
                  <TrLoading><td></td><td colSpan="8" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></TrLoading>
                  <TrLoading><td></td><td colSpan="8" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></TrLoading>
                  <TrLoading><td></td><td colSpan="8" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></TrLoading>
                  <TrLoading><td></td><td colSpan="8" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></TrLoading>
                  <TrLoading><td></td><td colSpan="8" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></TrLoading>
                  <TrLoading><td></td><td colSpan="8" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></TrLoading>
                  <TrLoading><td></td><td colSpan="8" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></TrLoading>
                  <TrLoading><td></td><td colSpan="8" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></TrLoading>
                  <TrLoading><td></td><td colSpan="8" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></TrLoading>
                  <TrLoading><td></td><td colSpan="8" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></TrLoading>
                  <TrLoading><td></td><td colSpan="8" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></TrLoading>
                  <TrLoading><td></td><td colSpan="8" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></TrLoading>
                  <TrLoading><td></td><td colSpan="8" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></TrLoading>
                  <TrLoading><td></td><td colSpan="8" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></TrLoading>
                  <TrLoading><td></td><td colSpan="8" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></TrLoading>
                  <TrLoading><td></td><td colSpan="8" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></TrLoading>
                  <TrLoading><td></td><td colSpan="8" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></TrLoading>
                  <TrLoading><td></td><td colSpan="8" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></TrLoading>
                  <TrLoading><td></td><td colSpan="8" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></TrLoading>
                  <TrLoading><td></td><td colSpan="8" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></TrLoading>
                  <TrLoading><td></td><td colSpan="8" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></TrLoading>
                  <TrLoading><td></td><td colSpan="8" style={{ color: '#666', fontSize: '14px', textAlign: 'left' }}></td></TrLoading>
                </>
              }
              {<>

                {state.problems.map((problem, index) =>
                  <tr style={{ backgroundColor: problem.is_visible ? 'var(--color_b2)' : 'var(--color_b22)' }} key={problem.id}>
                    {/* 0 - , 1 - , 2 -  */}
                    <td>
                      {Get_Number(problem.id)}
                    </td>
                    <td>
                      {problem.verdict === 1 ? <span style={{ display: 'flex', alignItems: 'center' }}><FaCheck color="var(--color_green)" size="12" />&nbsp;</span>
                        :
                        problem.verdict === 2 ? <span style={{ display: 'flex', alignItems: 'center' }}><FaTimes color="var(--color_red)" size="12" />&nbsp;</span>
                          : <></>}
                    </td>
                    <td>
                      <BoldA to={`/problem/${problem.id}`}>{problem.title}</BoldA>
                    </td>
                    <td>
                      {problem.tags.map((tag, index) =>
                        <Table_A tag="true" key={tag.id} onClick={on_search_clear} to={`/problems/1/${tag.id}`} key={tag.id}>
                          {tag.title}
                          {index < problem.tags.length - 1 && window.screen.width > theme.mobileSize.slice(0, -2) ? ',' : ''}
                    &nbsp;
                  </Table_A>
                      )}
                    </td>
                    <td><Table_A to={`/submissions/${problem.id}`}>x{problem.accepted}</Table_A></td>

                    {/*  */}
                    {user &&
                      <>
                        <td>
                          <div style={{ display: 'flex', alignItems: 'center', cursor: 'pointer' }} onClick={() => { onToggleFavourites(problem) }}>
                            <HoverScaledSpan>
                              {isFavourite(problem.id) ?
                                <FaStar color="var(--color_orange)" size="18" />
                                : <FaRegStar color="var(--color_c2)" size="18" />
                              }
                            </HoverScaledSpan>
                          </div>
                        </td>
                      </>
                    }

                    {/*  */}
                    <td>
                      <div style={{ display: 'flex', alignItems: 'center', cursor: 'pointer' }} onClick={() => { onToggleHeart(problem) }}>
                        <HoverScaledSpan>
                          {problem.liked ?
                            <FaHeart color="var(--color_primary)" size="18" />
                            : <FaRegHeart color="var(--color_c2)" size="18" />
                          }<span style={{ marginLeft: 5 }}>{problem.likes}</span>
                        </HoverScaledSpan>
                      </div>
                    </td>
                  </tr>
                )}

              </>}
            </tbody>
          </Problem_table>
        </Grid>


        {<PaginationContainer isLoading={state.isLoading} to={`/problems/:page${match.params.tagId ? `/${match.params.tagId}` : ''}`} pagination={state.pagination} />}
      </MainDiv>
      {/* </Container> */}
    </MDiv>
  )
}

export default Problems