import React from 'react'
import styled from 'styled-components'

const StyledGrid = styled.div `
  display: grid;
  grid-template-columns: repeat(${props => props.columns}, auto);
  grid-column-gap: ${props => props.gap};
  width: 100%;
`

const Grid = props => {

  return (
    <StyledGrid {...props}>
      {props.children}
    </StyledGrid>
  )
}

export default Grid