import React, { useState, useEffect, useContext} from 'react'
import { UserContext } from 'contexts/UserContext'
import { theme } from 'Themes'
import styled, { css } from 'styled-components'
import JN from 'JN'
import { MdAdd, MdEdit, MdDelete, MdEqualizer } from 'react-icons/md'
import { Link } from 'react-router-dom'
import { Get_Number, Convert_date_to_string, Get_contest_status, Get_dateDiff_toString } from 'Global_Functions'
import PaginationContainer from 'controls/pagination/PaginationContainer'
import CountDown from 'controls/CountDown'
import Button_Float from 'controls/Button_Float'
import TimeWidget from 'controls/TimeWidget'
import Checkbox from 'controls/Checkbox'
import { FaUser } from 'react-icons/fa'
import { GlobalContext } from 'contexts/GlobalContext'
import B from 'controls/B'
import BoldA from 'controls/BoldA'

const MainDiv = styled.div `
  width: 100%;
  border-radius: 8px;
  max-width: 1200px;
  margin: auto;
  padding-left: 56px;
  padding-right: 56px;
  @media only screen and (max-width: ${theme.mobileSize}) {
    padding: 0;
  }
`

const Table_A = styled(Link) `
  font-size: 14px;
  color: red;
  text-decoration: none;
  color: var(--color_c2);
  display: inline-flex;
  align-items: center;
  justify-content: center;

  /* width: 100%; */
  height: 100%;


  &:hover {
    color: var(--color_link);
  }
`

const Table = styled.table `
  border-collapse: collapse;
  overflow: hidden;
  margin-top: 15px;
  width: 100%;
  padding: 25px;
  padding-top: 25px;
  background-color: transparent;
  border-radius: 8px;

  ${props => props.count > 0 && css`
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  `}

  tr {
    height: 45px;
    background-color: var(--color_b2);
    border-bottom: 1px solid var(--color_b22);

    &:hover {
      /* background-color: rgba(255, 255, 255, 0.9); */
    }

    &:first-child {
      color: var(--color_headerText);
      background-color: var(--color_headerBackground);
      height: 56px;
      border-bottom: 0px solid #ddd;      
    }
  }

  th {
    padding: 8px;
    padding-left: 15px;
    padding-right: 15px;
    font-weight: normal;
    font-size: 14px;
    /* color: var(--color_c2); */
    font-family: ${theme.fontCaps};
    /* white-space: nowrap; */

    /* &:first-child {
      padding-left: 20px;
    } */

    &:last-child {
      padding-right: 20px;
    }

    &:nth-child(1) {
      text-align: left;
      /* width: 99%; */
    }

    &:nth-child(2) {
      text-align: center;
    }
    
    &:nth-child(3) {

    }
  }

  td {
    padding: 8px;
    padding-left: 15px;
    padding-right: 15px;
    font-size: 14px;
    /* white-space: nowrap; */
    color: var(--color_c2);

    /* &:nth-child(1) {
      font-size: 14px;
      text-align: center;
      padding-left: 20px;
    } */

    &:nth-child(1) {
      text-align: left;
      padding: 0;

      a {
        padding-left: 15px;
      }
    }

    &:nth-child(2) {
      font-size: 14px;
    }

    &:nth-child(3),
    &:nth-child(4),
    &:nth-child(5),
    &:nth-child(6),
    &:nth-child(7),
    &:nth-child(8),{
      text-align: center;
    } 

    &:nth-child(1) {
      width: 99%;
    }
    &:nth-child(3) {
      max-width: 150px;
      min-width: 150px;
      white-space: nowrap;
    }

    &:last-child {
      padding-right: 20px;
    }    
  }

  @media only screen and (max-width: ${theme.mobileSize}) {
    tr {
      padding: 12px;
      &:first-child {
        display: none;
      }

      min-height: 125px;

      border-bottom-width: 8px;
      &:last-child {
        border-bottom-width: 0px;
      }

      height: inherit;
      display: grid;
      grid-template-columns: auto auto auto auto auto;
      grid-template-rows: auto;
      grid-template-areas: 
        "title title title title status"
        "startDate . . . edit"
        "time . . . delete"
        "registered . . . ."
        "standing . . . ."
        "participation participation participation participation participation";
    }

    td {
      margin: 0;
      padding: 0px;

      /* &:nth-child(1) {
        grid-area: id;
        display: none;
      } */

      a {
        padding: 0;
        margin: 0;
      }

      &:nth-child(1) {
        grid-area: title;
        margin-bottom: 8px;
        a {
          padding: 0;
          margin: 0;
        }
      }
    
      &:nth-child(2) {
        grid-area: status;
        margin-top: -12px;
        text-align: right;
      }

      &:nth-child(3) {
        grid-area: startDate;
        text-align: left;
        
      }

      &:nth-child(4) {
        grid-area: time;
        text-align: left;
      }

      &:nth-child(5) {
        grid-area: registered;
      }

      &:nth-child(6) {
        grid-area: standing;
      }

      &:nth-child(7) {
        grid-area: participation;
        display: flex;
        justify-content: center;
        white-space: nowrap;
        &::after {
          content: '';
          font-size: 14px;
        }
      }

      &:nth-child(8) {
        grid-area: edit;
        display: flex;
        padding-right: 5px;
        justify-content: flex-end;
      }

      &:nth-child(9) {
        grid-area: delete;
        display: flex;
        justify-content: flex-end;
        /* padding-right: -25px; */
        margin-right: -20px;
      }
    }
  }
`


const Contests = props => {

  const { match } = props
  const { tr }    = useContext(GlobalContext);
  const { user }  = useContext(UserContext);

  const [state, setState] = useState({
    contests: [],
    is_loading: true,
    pagination: {current: 0, total: 0},
  })

  useEffect(() => {
    on_refresh()
  }, [match.url])

  const on_refresh = () => {
    setState({...state, is_loading: true})
    document.title = ` . ${match.params.page}`;
    JN.get(user, `contests/${match.params.page}`, {},
    (data) => {
      setState({
        ...state,
        contests: data.contests,
        is_loading: false,
        pagination: {current: match.params.page, total: data.page_number}
      });
    },
    () => {
      setState({...state, is_loading: false})
    })
  }

  const on_contest_delete = id => e => {

    e.preventDefault()
    if (!window.confirm('  ?!')) {
      return
    }

    setState({
      ...state,
      isLoading: true,
    })
    JN.delete(user, 'admin/contest', {id: id}, () => {
      setState({...state, isLoading: false})
      on_refresh()
    }, () => {
      setState({...state, isLoading: false})
    })
  }

  const on_registerToContest = contest => e => {
    e.preventDefault()

    setState({...state, contests: state.contests.map((c) => {return c.id !== contest.id ? c : {...c, registered: true, num_registered: c.num_registered + 1}})})

    JN.post(user, `contest/${contest.id}/register`, {}, null, () => setState({...state, contests: state.contests.map((c) => {return c.id !== contest.id ? c : {...c, registered: false, num_registered: c.num_registered - 1}})}))
  }

  return (
    <MainDiv>
      <div style={{borderRadius: '8px', boxShadow: '0 6px 16px rgba(0, 0, 0, 0.2)'}}>
      {user && user.permission === "admin" &&
        <Button_Float to="/addcontest"><MdAdd color="var(--color_green)" size="22" /></Button_Float>
      }

      {/* {state.is_loading &&
        <Container width="250px" height="5000px"><div style={{marginBottom: '25px', display: 'flex', justifyContent: 'center', alignItems: 'center'}}><LoadingSvg /></div></Container>
      } */}

      {(1 || !state.is_loading && state.contests.length > 0) && <>
        <Table count={state.pagination.total > 1 && !state.is_loading}>
          <thead></thead>
          <tbody>
            {state.is_loading ? 
            <tr>
              <th colSpan="9"></th>
            </tr>
            :
            <tr>
              <th>{tr("")}</th>
              <th></th>
              <th></th>
              <th></th>
              <th>x</th>
              <th><MdEqualizer size="18" /></th>
              {user &&
              <>
                <th></th>
              </>
              }
              {user && user.permission === "admin" &&
              <>
                <th><MdEdit size="18" /></th>
                <th><MdDelete size="18" /></th>
              </>
              }
            </tr>
            }

            {!state.is_loading && !state.contests.length && 
              <tr><td colSpan="10" style={{fontSize: '14px', textAlign: 'left'}}>
                <span style={{paddingLeft: 12}}>   <span style={{color: 'var(--color_red)'}}>!</span></span>
              </td></tr>
            }

            {state.is_loading && 
            <>
              <tr><td colSpan="15"></td></tr>
              <tr><td colSpan="15"></td></tr>
              <tr><td colSpan="15"></td></tr>
              <tr><td colSpan="15"></td></tr>
              <tr><td colSpan="15"></td></tr>
              <tr><td colSpan="15"></td></tr>
              <tr><td colSpan="15"></td></tr>
              <tr><td colSpan="15"></td></tr>
              <tr><td colSpan="15"></td></tr>
              <tr><td colSpan="15"></td></tr>
              <tr><td colSpan="15"></td></tr>
              <tr><td colSpan="15"></td></tr>
              <tr><td colSpan="15"></td></tr>
              <tr><td colSpan="15"></td></tr>
              <tr><td colSpan="15"></td></tr>
           </>
            }


          {!state.is_loading && state.contests.map((contest, index) =>
            <tr key={contest.id}>
              <td><BoldA to={`/contest/${contest.id}`}>{contest.title}</BoldA></td>
              <td>
                <Table_A to={`/contest/${contest.id}`}><span style={{fontSize: '14px', color: Get_contest_status(contest.start_time, contest.end_time) === 0 ? 'var(--color_c2)' : Get_contest_status(contest.start_time, contest.end_time) === 1 ? 'var(--color_orange)' : 'var(--color_green)'}}>{Get_contest_status(contest.start_time, contest.end_time) === 0 ? '' : Get_contest_status(contest.start_time, contest.end_time) === 1 ? '' : ''}</span></Table_A>
              </td>
              <td>
              <Table_A to={`/contest/${contest.id}`}>{Get_contest_status(contest.start_time, contest.end_time) === 2 ?
              <> 

                {Get_dateDiff_toString('now', contest.start_time, false, true).days ?
                  Convert_date_to_string(contest.start_time) : 
                  <>{contest.start_time && <CountDown to={contest.start_time} timeout={on_refresh} />}</>
                }
              </>
              :
              <>{Convert_date_to_string(contest.start_time)}</>
              }</Table_A>
              </td>
              <td><TimeWidget time={Get_dateDiff_toString(contest.start_time, contest.end_time, false, true)}/></td>
              <td style={{textAlign: 'left'}}>
                <Table_A to={`/participants/${contest.id}/1`}>
                  <span style={{marginRight: '3px'}}><FaUser size="14" /></span>{contest.num_registered}
                </Table_A>
              </td>
              <td>
                <span style={{display: 'flex', alignItems: 'center'}}>
                  {Get_contest_status(contest.start_time, contest.end_time) !== 2 && <Table_A to={`/contest/${contest.id}/standing`}><MdEqualizer color="var(--color_green)" size="18" /></Table_A>}
                </span>
              </td>
              {user &&
              <>
                <td>
                <span style={{display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                  <Checkbox disabled={Get_contest_status(contest.start_time, contest.end_time) === 0} onClick={Get_contest_status(contest.start_time, contest.end_time) !== 0 && !contest.registered ? on_registerToContest(contest) : null} color="var(--color_green)" checked={contest.registered} />
                </span>
                  {/* {!contest.registered ?
                    Get_contest_status(contest.start_time, contest.end_time) !== 0 && <button style={{outline: 'none', borderRadius: '5px', padding: '4px', cursor: 'pointer', paddingLeft: '8px', paddingRight: '8px', backgroundColor: "var(--color_primary)", color: 'var(--color_headerText)', border: 'none', fontSize: '14px', fontFamily: theme.fontCaps}} onClick={on_registerToContest(contest)}>
                      <></>
                    </button> */}
                      {/* : <Checkbox title="" checked={contest.registered} /> */}
                      {/* <Table_A to={`/contest/${contest.id}`}></Table_A> */}
                  {/* } */}
                </td>
              </>
              }
              {user && user.permission === "admin" &&
              <>
                <td>
                  <span style={{display: 'flex', height: '100%', cursor: 'pointer', alignItems: 'center'}}>
                    <Table_A to={`/editcontest/${contest.id}`}><MdEdit color="var(--color_orange)" size="18" /></Table_A>
                  </span>  
                </td>
                <td>
                  <button style={{display: 'flex', height: '100%', alignItems: 'center', cursor: 'pointer', border: 'none', background: 'none', outline: 'none'}} onClick={on_contest_delete(contest.id)}>
                    <MdDelete color="var(--color_red)" size="18" />
                  </button>
                </td>
              </>
              }
            </tr>
            )}
          </tbody>
        </Table>

        <PaginationContainer isLoading={state.is_loading} to={`/contests/:page`} pagination={state.pagination} />
      </>}
      </div>
    </MainDiv>
  )
}

export default Contests