import React, { useState, createContext } from 'react'
import MessageBox from './MessageBox'
import styled from 'styled-components'

const MessageBoxContainer = styled.div`
  background-color: transparent;
  position: fixed;
  left: 15px;
  bottom: 15px;
  width: 300px;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
`

export const MessageBoxContext = createContext({})

export const MessageBoxProvider = props => {

  const [toasts, setToasts] = useState([
    // {id: 1, message: ' 1', type: 'info'},
    // {id: 2, message: ' 2', type: 'success'},
    // {id: 3, message: ' 3', type: 'error'}
  ])
  const [maxId, setMaxId] = useState(4)

  const [messageBox, setMessageBox] = useState({
    isOpen: false,
    text: '',
    callBack_accepted: null,
  })

  // return () => clearInterval(interval);

  const onTimeout = (id) => {
    setToasts([])
    // clearInterval(interval)

    // setToasts(toasts.slice(1))
  }


  const timeout = id => {
    setToasts(toasts.filter(toast => toast.id !== id))
  }

  const toast = (message = '', type = null) => {
    // setToasts([{id: maxId, message: message, type: type}, ...toasts])
    setToasts([{ id: maxId, message: message, type: type }])
    setMaxId(maxId + 1)
    // setTimeout(onTimeout, 3000)
  }

  const jMb = (text, callBack_accepted) => {
    setMessageBox({ ...messageBox, isOpen: true, text: text, callBack_accepted: callBack_accepted })
  }

  const closeMessageBox = () => {
    setMessageBox({...messageBox, isOpen: false})
  }

  return (
    <MessageBoxContext.Provider value={{
      toast: toast,
      jMb: jMb,
      jMessageBox: messageBox,
      closeMessageBox: closeMessageBox,
    }}>

      <MessageBoxContainer>
        {toasts.map(toast =>
          <MessageBox type={toast.type ? toast.type : 'info'} key={toast.id} timeout={timeout} toast={toast} />
        )}
      </MessageBoxContainer>


      {props.children}
    </MessageBoxContext.Provider>
  )
}