import React, { useState, useContext } from 'react'
import { UserContext } from 'contexts/UserContext'
import styled from 'styled-components'
import Input from 'controls/Input'
import Grid from 'controls/Grid'
import Button from 'controls/Button'
import { Redirect } from 'react-router-dom'

const Main = styled.div`
	padding: 50px;
	margin-top: -150px;
`

const ListContainer = styled.div`
	display: inline-grid;
	grid-auto-flow: column;
	align-items: left;
	margin-top: 50px;
	background-color: white;
	padding: 50px;
	border-radius: 8px;
	box-shadow: 0 6px 16px 0 rgba(0,0,0,.1);
`

const Item = styled.input`
	outline: none;
	border: none;
	text-align: center;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100px;
	height: 100px;
	background-color: var(--color_red);

	&:focus {
		box-shadow: 0 0 0px 2px var(--color_primary);
	}
`

const PropertiesContainer = styled.div`
	display: flex;
	flex-direction: column;
	width: 425px;
	margin: auto;
	margin-top: 50px;
	background-color: var(--color_b2);
	padding: 50px;
	border-radius: 8px;
	box-shadow: 0 6px 16px 0 rgba(0,0,0,.1);
`

const ArrayMaker = () => {
	const { user } = useContext(UserContext)
	const [list, setList] = useState(['0', '1', '2', '3', '4', '5'])
	const [defaultProperties] = useState({
		item: {
			width: 50,
			height: 50,
			color: '#333',
			backgroundColor: '#DBF2E1',
			fontSize: 20,
			borderRadius: 5,
			fontWeight: 800,
		},
		itemContainer: {
			gridGap: 5
		}
	})

	const [properties, setProperties] = useState(defaultProperties)

	const listLengthChanged = (e) => {
		let temp = [...list]
		let newLength = parseInt(e.target.value)
		if (temp.length > newLength) {
			temp = temp.slice(0, newLength)
		}
		else {
			temp = [...temp, ...Array(newLength - temp.length)].map((e, i) => !e ? i.toString() : e)
		}

		setList(temp)
	}


	const handleChange = e => {
		setProperties({ ...properties, item: { ...properties.item, [e.target.name]: e.target.name === 'color' || e.target.name === 'backgroundColor' ? e.target.value : (parseInt(e.target.value) || '') } })
	}


	//     
	if (!user || user.permission === 'user') {
		return (<Redirect to="/"></Redirect>)
	}


	return (
		<Main>

			<ListContainer style={properties.itemContainer}>
				{list.map((element, index) =>
					<Item value={element} onChange={e => setList(list.map((a, i) => i === index ? e.target.value : a))} key={index} style={properties.item} />)}
			</ListContainer>


			<PropertiesContainer>
				<div>
					<input style={{ width: '100%', marginBottom: 10 }} type="range" min="1" max="21" value={list.length} onChange={listLengthChanged} />
				</div>

				<Grid gap="10px" columns="2">
					<Input autocomplete="off" name="width" title="" value={properties.item.width} onChange={handleChange} />
					<Input autocomplete="off" name="height" title="" value={properties.item.height} onChange={handleChange} />
				</Grid>

				<Input title="" value={properties.itemContainer.gridGap} onChange={(e) => setProperties({ ...properties, itemContainer: { ...properties.itemContainer, gridGap: (parseInt(e.target.value) || '') } })} />

				<Input autocomplete="off" autocomplete="off" name="backgroundColor" title=" " value={properties.item.backgroundColor} onChange={handleChange} />

				<Grid gap="10px" columns="2">
					<Input autocomplete="off" autocomplete="off" name="color" title=" " value={properties.item.color} onChange={handleChange} />
					<Input autocomplete="off" name="fontSize" title=" " value={properties.item.fontSize} onChange={handleChange} />
				</Grid>

				<Input name="fontWeight" title=" " value={properties.item.fontWeight} onChange={handleChange} />

				<Input autocomplete="off" name="borderRadius" title=" " value={properties.item.borderRadius} onChange={handleChange} />

				<Button width="100%" style={{ marginTop: 25 }} onClick={e => { e.preventDefault(); setProperties(defaultProperties) }}> </Button>

			</PropertiesContainer>
		</Main>
	)
}

export default ArrayMaker