import React from 'react'
import Flex from './../controls/Flex'
import Container from './../controls/Container'

function Form(props) {
  const onSubmit = (e) => {
    e.preventDefault()
    if (typeof(props.onSubmit) === 'function') {
      props.onSubmit()
    }
  }

  return (
    <Container bg="#f5f5f5" title={props.title} width={props.width}>
      <form onSubmit={onSubmit}>
        {props.children}
      </form>
    </Container>
  )
}

export default Form