import React, { useState, useEffect, useContext } from 'react'
import { theme } from 'Themes'
import styled, { css } from 'styled-components'
import { Link, NavLink, Switch, Route, Redirect } from 'react-router-dom'
import Container from 'controls/Container'
import { Get_Number, Convert_date_to_string, Get_contest_status, Get_dateDiff_toString } from 'Global_Functions'
import A from 'controls/A'
import { MdDone, MdDelete, MdEdit, MdAllOut } from 'react-icons/md'
import ContainerFooter from 'controls/ContainersFooter'
import Button from 'controls/Button'
import { UserContext } from 'contexts/UserContext'
import LoadingSvg from 'components/LoadingSvg'
import Button_Float from 'controls/Button_Float'
import TabStandings from './TabStandings'
import CountDown from 'controls/CountDown'
import { MessageBoxContext } from 'contexts/MessageBoxContext'
import JN from 'JN'
import { FaCheck, FaTimes } from 'react-icons/fa'
import Checkbox from 'controls/Checkbox'

const Tab_Container = styled.div`
  display: flex;
`

const Tab_Action = styled(NavLink)`
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */

  cursor: pointer;
  border: none;
  outline: none;
  text-decoration: none;
  font-size: 16px;
  font-family: ${theme.fontCaps};
  border: 1px solid var(--color_b22);
  padding: .8em 1.5em;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
  outline: none;
  border-top-left-radius: 5px;
  border-radius: 5px;
  margin-right: 5px;
  margin-bottom: 50px;
  color: var(--color_c2);
  background-color: transparent;
  cursor: pointer;

  &:disabled {
    background: none;
    color: var(--color_c3);
    border: 1px solid transparent;
  }
`

const Table_A = styled(Link)`
  font-size: 16px;
  color: red;
  text-decoration: none;
  color: var(--color_c2);
  display: inline-block;

  &:hover {
    color: var(--color_link);
  }
`

const Problem_table = styled.table`
  border-collapse: collapse;
  overflow: hidden;
  margin-top: 15px;
  width: 100%;
  padding: 25px;
  padding-top: 25px;
  box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2); 
  background-color: var(--color_b11);
  border-radius: 8px;
  font-size: 16px;

  tr {
    /* border-bottom: 1px solid var(--color_c3); */
    height: 50px;

    &:first-child {
      border: none;
      color: var(--color_headerText);
      background-color: var(--color_headerBackground);
    }

    &:last-child {
      border: none;
    }
  }

  th {
    padding: 8px;
    font-weight: normal;
    padding-left: 15px;
    padding-right: 15px;

    &:first-child {
      padding-left: 20px;
    }

    &:last-child {
      padding-right: 20px;
    }

    &:nth-child(2) {
      padding: 0;
    }

    &:nth-child(3), &:nth-child(4) {
      text-align: left;
    }

    &:nth-child(3) {
      width: 99%;
    }
  }

  td {
    padding: 8px;
    /* font-size: 16px; */
    white-space: nowrap;
    color: var(--color_c2);
    background-color: var(--color_b11);

    padding-left: 15px;
    padding-right: 15px;

    &:nth-child(1) {
      /* font-size: 14px; */
    }

    &:nth-child(2) {
      padding: 0;
      text-align: right;
    }

    &:nth-child(3) {
      text-align: left;
      padding: 0;

      a {
        width: 100%;
        padding: 8px;
      }
    }
    
    &:nth-child(4) {
      text-align: left;
    }    
  }
`

const FullContest = props => {
  const { match, history } = props
  const { user } = useContext(UserContext)
  const { toast } = useContext(MessageBoxContext)

  const [state, setState] = useState({
    is_loading: true,
    is_registering: false,
    is_updating_problemsVisibility: false,
    is_allProblemsVisible: false,
    update_state: false,
  })

  const [contest, setContest] = useState({ problems: [] })

  useEffect(() => {

    let arr = history.location.pathname.split("/");
    if (!contest.title || arr[arr.length - 1] === 'problems')
      refresh()

  }, [match])

  const refresh = () => {

    JN.get(user, `contest/${match.params.id}`, {},
      (data) => {
        let isAllProblemsVisible = true

        for (let i = 0; i < data.problems.length; i++) {
          if (!data.problems[i].is_visible) {
            isAllProblemsVisible = false
            break
          }
        }

        document.title = `${data.title}`;
        setContest(data)
        setState({ ...state, is_loading: false, is_allProblemsVisible: isAllProblemsVisible })
      },
      () => {
        setState({ ...state, is_loading: false })
      })
  }

  const on_registerToContest = () => {

    setState({ ...state, is_registering: true, })

    JN.post(user, `contest/${match.params.id}/register`, {},
      () => {
        setState({ ...state, is_registering: false, })
        setContest({ ...contest, registered: true, })
      },
      () => {
        setState({ ...state, is_registering: false, })
        setContest({ ...contest, registered: false, })
      })
  }

  const on_contest_delete = e => {
    e.preventDefault()
    if (!window.confirm('  ?!')) {
      return
    }

    setState({ ...state, is_deleting: true })

    JN.delete(user, `admin/contest`, { id: match.params.id },
      () => {
        setState({ ...state, isDeleting: false })
        history.push('/contests/1')
      },
      () => {
        setState({ ...state, is_deleting: false })
      })
  }

  if (state.is_loading) {
    return (
      <Container width="500px">
        <div style={{ textAlign: 'center' }}><LoadingSvg /></div>
      </Container>
    )
  }


  let currentTab =
    <div>
      {contest.problems.length > 0 &&
        <>
          <Problem_table>
            <thead></thead>
            <tbody>
              <tr>
                <th></th>
                <th></th>
                <th></th>
                {/* {Get_contest_status(contest.start_time, contest.end_time) === 0 &&
              <th></th>
            } */}
                <th style={{ height: '50px', cursor: 'pointer', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                  <MdDone size="18" color="var(--color_headerText)" />
                </th>
              </tr>
              {contest.problems.map((problem, index) =>
                <tr key={problem.id}>
                  {/* 0 - , 1 - , 2 -  */}
                  <td>
                    {Get_Number(problem.score)}
                  </td>
                  <td>
                    {problem.verdict === 1 ? <><FaCheck color="var(--color_green)" size="12" />&nbsp;</>
                      :
                      problem.verdict === 2 ? <><FaTimes color="var(--color_red)" size="12" />&nbsp;</>
                        : <></>}
                  </td>
                  <td>
                    <Table_A to={`/problem/${problem.id}`}>{problem.title}</Table_A>
                  </td>
                  {/* {Get_contest_status(contest.start_time, contest.end_time) === 0 &&
              <td>
                {problem.tags.map((tag, index) =>
                  <Table_A key={tag.id} to={`/problems/1/${tag.id}`} key={tag.id}>
                    {tag.title}
                    {index < problem.tags.length - 1 ? ',' : ''}
                    &nbsp;
                  </Table_A>
                )}
              </td>
            } */}
                  <td><Table_A to={`/submissions/${problem.id}`}>x{problem.accepted}</Table_A></td>
                </tr>
              )}
            </tbody>
          </Problem_table>
        </>
      }

      <ContainerFooter>
        {user && contest && <Checkbox disabled={Get_contest_status(contest.start_time, contest.end_time) === 0} onClick={Get_contest_status(contest.start_time, contest.end_time) !== 0 && !contest.registered ? on_registerToContest : null} color="var(--color_green)" title="" checked={contest.registered} />}
      </ContainerFooter>
    </div>

  const on_makeAllProblemsVisible = e => {
    e.preventDefault()

    setState({ ...state, is_updating_problemsVisibility: true, })

    JN.put(user, `admin/contest/problems`, { contest_id: match.params.id },
      () => {
        toast('   ', 'success')
        setState({ ...state, is_updating_problemsVisibility: false, is_allProblemsVisible: true })
      },
      (error) => {
        setState({ ...state, is_updating_problemsVisibility: false, })
        toast(error.statusText)
      })
  }

  const on_start = () => {
    refresh()
    toast(' ', 'success')
  }

  const on_end = () => {
    refresh()
    toast(' ', 'success')
  }

  return (
    <Container title={contest.title} width="100%" maxWidth="1050px"
      titleRight={
        Get_contest_status(contest.start_time, contest.end_time) === 2 ? <>{Get_contest_status(contest.start_time, contest.end_time) === 2 && <span style={{ color: 'var(--color_green)' }}></span>}</>
          : Get_contest_status(contest.start_time, contest.end_time) === 0 ? '' :
            <span style={{ color: 'var(--color_c2)', fontSize: '16px' }}>
              {Get_contest_status(contest.start_time, contest.end_time) === 1 && <CountDown from={contest.end_time} timeout={on_end} />}
            </span>
      }
    >

      {Get_contest_status(contest.start_time, contest.end_time) !== 2 &&
        <Tab_Container>

          <Tab_Action to={`/contest/${match.params.id}/problems`} activeStyle={{ pointerEvents: 'none', backgroundColor: 'var(--color_b1)' }}>
            
        </Tab_Action>
          <Tab_Action to={`/contest/${match.params.id}/standing`} activeStyle={{ pointerEvents: 'none', backgroundColor: 'var(--color_b1)' }}>
            
        </Tab_Action>

          {/* <Tab_Action style={tab === 1 ? Tab_Current : {}} onClick={on_tab_changed(1)}></Tab_Action>
        <Tab_Action style={tab === 2 ? Tab_Current : {}} onClick={on_tab_changed(2)}></Tab_Action> */}
        </Tab_Container>
      }

      {Get_contest_status(contest.start_time, contest.end_time) === 2 &&
        <div style={{ color: 'var(--color_c2)', marginTop: '25px', marginBottom: '75px', fontSize: '24px' }}>
          {Get_dateDiff_toString('now', contest.start_time, false, true).days ?
            Convert_date_to_string(contest.start_time) :
            <>{contest.start_time && <CountDown size="20px" to={contest.start_time} timeout={on_start} />}</>
          }
        </div>
      }

      {user && user.permission === "admin" &&
        <>
          {Get_contest_status(contest.start_time, contest.end_time) === 0 && !state.is_allProblemsVisible && <Button_Float count="3" onClick={on_makeAllProblemsVisible}>{state.is_updating_problemsVisibility ? <LoadingSvg color="var(--color_purple)" width="22" height="22" /> : <MdAllOut color="var(--color_purple)" size="22" />}</Button_Float>}
          <Button_Float count="2" to={`/editcontest/${match.params.id}`}><MdEdit color="var(--color_orange)" size="22" /></Button_Float>
          {!state.is_deleting && <Button_Float onClick={on_contest_delete}><MdDelete color="var(--color_red)" size="22" /></Button_Float>}
        </>
      }

      <div style={{ marginTop: '-40px' }}>
        <Switch>
          <Route exact path={`/contest/:id/standing/:page`} component={TabStandings} />
          <Redirect from={`/contest/:id/standing`} to={`/contest/:id/standing/1`} />
          <Route exact path={`/contest/:id/problems`} component={() => currentTab} />
          <Redirect from={`/contest/:id/`} to={`/contest/:id/problems`} />
        </Switch>
      </div>

    </Container>
  )
}

export default FullContest