import React, { useState, useEffect, useContext } from 'react'
import { theme } from 'Themes'
import { Server } from 'Server'
import axios from 'axios'
import styled, { css } from 'styled-components'
import A from 'controls/A'
import { UserContext } from 'contexts/UserContext'
import { GlobalContext } from 'contexts/GlobalContext'

import { GoogleLogin } from 'react-google-login'
import FacebookLogin from 'react-facebook-login'

import { FaBrain, FaBook, FaStar, FaMedal, FaTrophy, FaPaintBrush } from 'react-icons/fa'
import { Link } from 'react-router-dom'
import Helmet from 'react-helmet'
import Container from 'controls/Container'
import JN from 'JN'
import { Convert_date_to_string, Get_contest_status, Get_dateDiff_toString } from 'Global_Functions'
import CountDown from 'controls/CountDown'
import Checkbox from 'controls/Checkbox'
import TimeWidget from 'controls/TimeWidget'
import { MdTimer, MdEvent } from 'react-icons/md'
import H3 from 'controls/H3'
import B from 'controls/B'

const SA = styled(Link)`
  text-decoration: none;
  color: var(--color_c2);
  width: 160px;
  height: 120px;

  &:hover {
    transition: color ease 250ms;
    color: ${props => props.color};
  }
`

const CartContainer = styled.div`
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-template-columns: repeat(auto-fill, minmax(160px, 160px));
  grid-column-gap: 12px;
  grid-row-gap: 12px;
  justify-content: center;
`

const Cart = styled.div`
  border-radius: 8px;
  box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.2);
  color: red;
  color: var(--color_c1);
  background-color: var(--color_b2);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 160px;
  height: 120px;
  font-size: 16px;
  font-family: ${theme.fontCaps};
  
  span {
    /* color: var(--color_c2); */
  }
  border: 3px solid transparent;


  svg {
    transition: transform ease 250ms;
  }

  &:hover {
    transition: color ease 250ms, border ease 250ms;
    /* color: ${props => props.color}; */
    border: 3px solid var(--color_primary);

   svg {
      transform: rotate(25deg);
   }
  }
`

const ContestsContainer = styled.div`
  font-size: 14px;
  color: var(--color_c2);
  margin: auto;
  margin-top: 50px;
  padding-left: 12px;
  padding-right: 12px;
  display: grid;
  width: 700px;
  max-width: 100%;

  grid-template-columns: repeat(1, minmax(0, 1fr));
  grid-gap: 13px;

  @media (max-width: ${theme.mobileSize}) {
    & {
      margin-top: 100px;
      grid-template-columns: repeat(1, minmax(0, 1fr));
    }
  }
`

const ContestItem = styled(Link)`
  box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.2);
  color: var(--color_c2);
  background-color: var(--color_b2);
  border-radius: 8px;
  padding: 16px;
  padding-top: 0;
  padding-bottom: 0;
  text-align: left;
  text-decoration: none;
  border: 3px solid transparent;

  &:hover {
    transition: border ease 250ms;
    border: 3px solid var(--color_primary);
  }

  p {
    margin-bottom: 10px;
    &:first-child {
      font-size: 18px;
    }
  }
`

const Main = props => {
  const { tr } = useContext(GlobalContext);
  const { user } = useContext(UserContext);
  const [state, setState] = useState({
    plannedContests: [],
  });

  useEffect(() => {
    JN.get(user, `contests/${1}`, { planned: true },
      (data) => {
        setState({ ...state, plannedContests: data.contests });
      })
  }, []);

  const on_google_logedIn = (response) => {
    console.log(response)
  }

  const on_google_error = (response) => {
    console.log(response)
  }

  const on_facebook_login = (response) => {
    console.log(response)
  }

  const on_registerToContest = contest => e => {
    e.preventDefault()

    setState({ ...state, plannedContests: state.plannedContests.map((c) => { return c.id !== contest.id ? c : { ...c, registered: true, num_registered: c.num_registered + 1 } }) })

    JN.post(user, `contest/${contest.id}/register`, {}, null, () => setState({ ...state, contests: state.plannedContests.map((c) => { return c.id !== contest.id ? c : { ...c, registered: false, num_registered: c.num_registered - 1 } }) }))
  }

  const on_refresh = () => {
    JN.get(user, `contests/${1}`, { planned: true },
      (data) => {
        setState({ ...state, plannedContests: data.contests });
      })
  }

  return (
    <div>
      <Helmet>
        <meta charSet="utf-8" />
        <title>AiaSoft</title>
        <meta name="description" content=" " />
      </Helmet>
      <div style={{ margin: 'auto', width: '700px', maxWidth: '100%' }}>
        <CartContainer>

          <SA to="/problems">
            <Cart color="var(--color_blue)">
              <FaBrain size="42" />
              <B style={{ marginTop: '10px' }}>{tr("")}</B>
            </Cart>
          </SA>

          <SA to="/contests">
            <Cart color="var(--color_red)">
              <FaTrophy size="42" />
              <B style={{ marginTop: '10px' }}>{tr("")}</B>
            </Cart>
          </SA>

          <SA to={`/rating`}>
            <Cart color="var(--color_orange)">
              <FaStar size="42" />
              <B style={{ marginTop: '10px' }}>{tr("")}</B>
            </Cart>
          </SA>

          <SA to="/tutorials">
            <Cart color="var(--color_green)">
              <FaBook size="42" />
              <B style={{ marginTop: '10px' }}>{tr("")}</B>
            </Cart>
          </SA>

          {user && user.permission !== 'user' &&
            <SA to={`/paint`}>
              <Cart color="var(--color_orange)">
                <FaPaintBrush size="42" />
                <B style={{ marginTop: '10px' }}>{tr("2")}</B>
              </Cart>
            </SA>
          }

        </CartContainer>
      </div>

      {state.plannedContests.length > 0 &&
        <ContestsContainer>
          {state.plannedContests.map((contest, index) =>
            <ContestItem key={contest.id} to={`/contest/${contest.id}`}>
              <H3>{contest.title}</H3>
              <div style={{ marginBottom: 10, display: 'flex', justifyContent: 'space-between' }}>
                <div style={{ display: 'flex', alignItems: 'center' }}>
                  <MdEvent size="18" />&nbsp;
                {Get_contest_status(contest.start_time, contest.end_time) === 2 ?
                    <>
                      {Get_dateDiff_toString('now', contest.start_time, false, true).days ?
                        Convert_date_to_string(contest.start_time) :
                        <>{contest.start_time && <CountDown to={contest.start_time} timeout={on_refresh} />}</>
                      }
                    </>
                    :
                    <>{Convert_date_to_string(contest.start_time)}</>
                  }
                </div>
              </div>
              <div style={{ marginBottom: 8, display: 'flex', alignItems: 'center' }}>
                <MdTimer size="18" />&nbsp;
                <TimeWidget time={Get_dateDiff_toString(contest.start_time, contest.end_time, false, true)} />
              </div>
              <div style={{ marginBottom: '12px' }}>
                {user &&
                  <span style={{ display: 'flex', alignItems: 'flex-start', justifyContent: 'center' }}>
                    <Checkbox title={tr("")} disabled={Get_contest_status(contest.start_time, contest.end_time) === 0} onClick={Get_contest_status(contest.start_time, contest.end_time) !== 0 && !contest.registered ? on_registerToContest(contest) : null} color="var(--color_green)" checked={contest.registered} />
                  </span>
                }
              </div>
            </ContestItem>
          )}
        </ContestsContainer>
      }

      {/* <div style={{columnCount: 3, width: 700, maxWidth: '100%', margin: 'auto', marginTop: 25}}>
      <div style={{height: 1, height: '1em', borderBottom: '1px solid var(--color_c2)'}}></div>
      <div style={{whiteSpace: 'nowrap'}}><H3> </H3></div>
      <div style={{height: 1, height: '1em', borderBottom: '1px solid var(--color_c2)'}}></div>
    </div> */}

      {/* <GoogleLogin
        clientId="993955986794-4t16h09ml0ursv68jpavnof1n2ucr555.apps.googleusercontent.com"
        buttonText="Login"
        onSuccess={on_google_logedIn}
        onFailure={on_google_error}
        cookiePolicy={'single_host_origin'}
      /> */}

      {/* <br/>
      <br/>
      <FacebookLogin
        appId="2632898383472476"
        autoLoad={false}
        fields="name,email,picture"
        // onClick={componentClicked}
        callback={on_facebook_login}
        scope="user_friends" 
      /> */}


      {/* <Chat /> */}
    </div>
  )
}

export default Main