import React from 'react'
import { Link } from 'react-router-dom'
import styled, { css } from 'styled-components'
import { theme } from 'Themes'

const SLink = styled(Link) `
  color: var(--color_link);
  text-decoration: none;
  display: inline-flex;

  &:hover {
    color: var(--color_link);
  }

  ${props => props.hover && css `
    color: var(--color_c2);
    &:hover {
      color: var(--color_link);
    }
  `}

  ${props => props.disabled && css `
    color: var(--color_c3);
    pointer-events: none;
  `}
`

const A = props => {

  if (props.disabled) 
  {
    return (<span style={{color: 'var(--color_c3)'}}>{props.children}</span>)
  }
  return (
    <SLink {...props} to={props.to} hover={props.hover}>
      {props.children}
    </SLink>
  )
}

export default A