import React, { useState, useEffect, useRef, useContext } from 'react'
import styled from 'styled-components'
import { theme } from 'Themes'
import JN from 'JN'
import { MdFormatBold, MdFormatItalic, MdPhoto, MdTitle } from 'react-icons/md'
import { FaYoutube, FaParagraph, FaCode, FaLink } from 'react-icons/fa'
import { UserContext } from 'contexts/UserContext'
import LoadingSvg from 'components/LoadingSvg'
import B from './B'
import Modal from 'components/Modal'
import CodeEditor from 'components/CodeEditor'
import Button from './Button'
import ComboBox from './ComboBox'
import Input from './Input'
import { MessageBoxContext } from 'contexts/MessageBoxContext'

const Controls = styled.div`
  display: flex;
  margin-bottom: 10px;
  margin-right: 10px;
  padding-bottom: 5px;
  margin-top: -5px;
  border-bottom: 1px solid var(--color_b2);
`

const Label = styled.label`
  display: flex;
  align-items: center;
  cursor: pointer;
  border: none;
  outline: none;
  background: none;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
`

const Btn = styled.button`
  font-family: ${theme.font};
  cursor: pointer;
  border: none;
  outline: none;
  background: none;
  display: flex;
  align-items: center;
  justify-content: center;

  &:first-child {
    padding-bottom: 2px;
  }

  * {
    pointer-events: none;
  }
`

const StyledDiv = styled.div`
  flex-grow: 1;
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0px;
  margin-top: 15px;
  margin-bottom: 15px;
  padding-top: ${props => props.title ? '15px' : '0'};
  box-sizing: border-box;
  margin-right: ${props => props.marginRight ? props.marginRight : "0"};
  &:last-child {
    margin-right: 4px;
  }
`

const Content = styled.div`
  background-color: var(--color_inputBackground);
  border-radius: 5px;
  border: 2px solid var(--color_b1);
  border-color:  var(--color_inputBackground);
  outline: none;
  transition: box-shadow ease 250ms;
  padding: 10px 13px;
  padding-bottom: 0;
  padding-right: 0;

  &:focus-within {
    transition: border-color ease 250ms, background-color ease 250ms;
    border-color:  var(--color_inputFocusBorder);
    background-color: var(--color_inputFocusBackground);
    box-shadow: 0 0 7px rgba(255, 255, 255, 0.2);
  }
`

const StyledTitle = styled.div`
  color: var(--color_inputText);
  display: flex;
  justify-content: space-between;
  font-family: ${theme.font};
  text-align: left;
  font-size: 14px;
  margin-bottom: 5px;
`

const StyledTextarea = styled.textarea`
  color: var(--color_inputText);
  resize: vertical;
  flex-grow: 1;
  width: 100%;
  height: ${props => props.height ? props.height : "70px"};
  font-family: ${props => props.monospace ? 'monospace' : theme.font};
  font-size: 16px;
  padding-right: 10px;
  margin-bottom: -6px;
  outline: none;
  border: none;
  background: none;
`

const StyledHelperText = styled.span`
  color: gray;
  text-align: left;
  font-family: ${theme.font};
  font-size: 12px;
`
const ErrorText = styled.span`
  color: red;
  display: ${props => props.isError ? 'block' : 'none'};
  margin-bottom: 2px;
`

const TextArea = (props) => {
  const { user } = useContext(UserContext);
  const { toast } = useContext(MessageBoxContext)
  const refTextarea = useRef(null);
  const [isImageUploading, setIsImageUploading] = useState(false);
  const [selectionPos, setSelectionPos] = useState({ start: -1, end: -1 });

  const onChanged = (e) => {
    if (typeof (props.onChange) === 'function') {
      props.onChange(e)
    }
  }

  useEffect(() => {
    if (selectionPos.start !== -1) {
      refTextarea.current.selectionStart = selectionPos.start + 2;
      refTextarea.current.selectionEnd = selectionPos.end + 2;
      setSelectionPos({ start: -1, end: -1 });
    }
  }, [props.value]);

  const insertJData = c => {
    let value = props.value;
    if (refTextarea.current.selectionStart === refTextarea.current.selectionEnd) {
      while (refTextarea.current.selectionStart > 0 && value[refTextarea.current.selectionStart] !== ' ' && value[refTextarea.current.selectionStart - 1] !== '\n' && value[refTextarea.current.selectionStart - 1] !== ',' && value[refTextarea.current.selectionStart - 1] !== '.') {
        refTextarea.current.selectionStart -= 1;
      }

      while (refTextarea.current.selectionEnd < value.length && value[refTextarea.current.selectionEnd] !== ' ' && value[refTextarea.current.selectionEnd] !== '\n' && value[refTextarea.current.selectionEnd] !== ',' && value[refTextarea.current.selectionEnd] !== '.') {
        refTextarea.current.selectionEnd += 1;
      }
    }

    while (refTextarea.current.selectionStart < refTextarea.current.selectionEnd) {
      if (value[refTextarea.current.selectionStart] !== ' ')
        break;

      refTextarea.current.selectionStart += 1;
    }

    let start = refTextarea.current.selectionStart;
    while (start < refTextarea.current.selectionEnd) {
      if (value[refTextarea.current.selectionEnd - 1] !== ' ')
        break;

      refTextarea.current.selectionEnd -= 1;
    }

    let end = refTextarea.current.selectionEnd;

    if (start >= end)
      return;


    if (start === end)
      return;

    let first = value.substring(0, start);
    let middle = value.substring(start, end);
    let last = value.substring(end);


    if (!value.length)
      return;

    setSelectionPos({ start: start, end: end });
    onChanged({ target: { name: props.name, value: first + '`' + c + middle + '`' + last } });
  }

  const insertChar = c => {
    let first = props.value.substring(0, refTextarea.current.selectionStart);
    let last = props.value.substring(refTextarea.current.selectionEnd);
    onChanged({ target: { name: props.name, value: first + c + last } });
    setSelectionPos({ start: refTextarea.current.selectionStart - 1, end: refTextarea.current.selectionStart - 1 });
  }

  const onControlsClicked = e => {
    e.preventDefault();
    insertJData(e.target.name);
  }

  const onMouseDown = e => {
    e.preventDefault();
  }

  const onImageUpload = e => {
    e.preventDefault();

  }

  const OnFilePicked = (e) => {

    const file = e.target.files.item(0)
    if (!file)
      return;

    setIsImageUploading(true);

    let reader = new FileReader();
    reader.readAsText(file, "UTF-8");

    reader.onload = (event) => {

      var formData = new FormData();
      formData.append("image", file);

      JN.post(user, 'admin/photo', formData,
        (data) => {
          let first = props.value.substring(0, refTextarea.current.selectionStart);
          let last = props.value.substring(refTextarea.current.selectionEnd);
          onChanged({ target: { name: props.name, value: first + (props.value.length ? '\n' : '') + '`~' + data.public_url + '`' + last } });
          setIsImageUploading(false);
        },
        (error) => {
          console.log(error);
          setIsImageUploading(false);
        });
    }

    reader.onerror = function (evt) {
      console.log("error reading file");
    }

  }

  const onParagraphClicked = e => {
    e.preventDefault();
    insertChar('\t');
  }

  const onNumberClicked = e => {
    e.preventDefault();
    insertChar('');
  }

  const onDotClicked = e => {
    e.preventDefault();
    insertChar('');
  }

  const onCodeClicked = e => {
    e.preventDefault()
    setDlgCodeOpen(true)
  }

  const [dCode, setDCode] = useState({ value: '', name: 'C++ 17', language: 'C++', version: '17', editorName: 'c_cpp', theme: 'monokai' })
  const [code, setCode] = useState(dCode)

  const OnCodeChanged = (c) => {
    setCode({ ...code, value: c })
  }

  // 
  const onLanguageChanged = (e) => {
    setCode({ ...code, language: e.nativeEvent.target[e.nativeEvent.target.selectedIndex].text })
  }


  const on_combo_itemSelected = item => {
    let newCode = { ...code, name: item.name, language: item.language, version: item.version, editorName: item.editorName }

   
    setCode(newCode)
  }


  const [isDlgCodeOpen, setDlgCodeOpen] = useState(false)
  const onCloseDlgCode = () => {
    setDlgCodeOpen(false)
  }

  const onCodeAccepted = e => {
    e.preventDefault()
    setCode(dCode)

    let language = code.editorName
    if (language === "c_cpp") {
      language = "cpp"
    }
    insertChar('`c' + language + `${code.value}` + '`')
    onCloseDlgCode()
  }

  //-  
  const [text, setText] = useState('')
  const [webAddress, setWebAddress] = useState('')
  const [isDlgLinkOpen, setDlgLinkOpen] = useState(false)

  useEffect(() => {
    setText('')
    setWebAddress('')
  }, [])

  const onCloseDlgLink = () => {
    setDlgLinkOpen(false)
  }

  const onLinkAccepted = e => {
    e.preventDefault()

    if (text.trim() === '' || webAddress.trim() === '') {
      toast('  !', 'error')
      return
    }

    insertChar('`l' + webAddress.trim() + `${text.trim()}` + '`')
    onCloseDlgLink()
  }

  const onLinkClicked = e => {
    e.preventDefault()
    setDlgLinkOpen(true)
  }

  return (
    <StyledDiv title={props.title} marginRight={props.marginRight}>
      {props.title &&
        <StyledTitle>
          <B>{props.title}</B>
          {props.actions}
        </StyledTitle>}

      <Content>
        {props.showControls &&
          <Controls>
            <Btn tabIndex="-1" onMouseDown={onMouseDown} onClick={onNumberClicked}><span style={{ color: 'var(--color_c2)', fontSize: '16px', paddingTop: '0px', fontWeight: '600' }}></span></Btn>
            <Btn name="t" tabIndex="-1" onMouseDown={onMouseDown} onClick={onControlsClicked}><MdTitle color="var(--color_c2)" size="22" /></Btn>
            <Btn tabIndex="-1" onMouseDown={onMouseDown} onClick={onCodeClicked}><FaCode color="var(--color_c2)" size="18" /></Btn>
            <Btn tabIndex="-1" onMouseDown={onMouseDown} onClick={onLinkClicked}><FaLink color="var(--color_c2)" size="18" /></Btn>
            <Btn tabIndex="-1" onMouseDown={onMouseDown} onClick={onDotClicked}><span style={{ color: 'var(--color_c2)', fontSize: '18px', paddingTop: '0px', fontWeight: '600' }}></span></Btn>
            <Btn tabIndex="-1" onMouseDown={onMouseDown} onClick={onParagraphClicked}><span style={{ paddingTop: '-5px' }}><FaParagraph color="var(--color_c2)" size="15" /></span></Btn>
            <Btn name="b" tabIndex="-1" onMouseDown={onMouseDown} onClick={onControlsClicked}><MdFormatBold color="var(--color_c2)" size="22" /></Btn>
            <Btn name="i" tabIndex="-1" onMouseDown={onMouseDown} onClick={onControlsClicked}><MdFormatItalic color="var(--color_c2)" size="22" /></Btn>
            <Label htmlFor={`image${props.name}`}>
              <input disabled={isImageUploading} id={`image${props.name}`} style={{ display: 'none' }} type="file" onChange={OnFilePicked} />
              {!isImageUploading ? <MdPhoto color="var(--color_c2)" size="20" /> : <LoadingSvg size="18" />}
            </Label>
            <Btn name="v" tabIndex="-1" onMouseDown={onMouseDown} onClick={onControlsClicked}><FaYoutube color="var(--color_red)" size="22" /></Btn>
          </Controls>}

        <StyledTextarea
          ref={refTextarea}
          autoFocus={props.focus}
          id={props.id}
          name={props.name}
          isError={props.isError}
          placeholder={props.ph}
          value={props.value}
          onChange={onChanged}
          type={props.type}
          required={props.required}
          height={props.height}
          monospace={props.monospace}
          {...props}
        />
      </Content>

      <StyledHelperText>
        <ErrorText isError={props.isError}>*{props.errorText}</ErrorText>
        {props.helper}
      </StyledHelperText>


      {/*    */}
      <Modal width="600px" title="" isOpen={isDlgCodeOpen} close={onCloseDlgCode}>
        <div>
          <CodeEditor focus languageChanged={onLanguageChanged} width={"550px"} height={"400px"} code={code} onChange={OnCodeChanged}
            rightItems={
              <div style={{ marginRight: '5px', width: '170px', justifyContent: 'space-between', alignItems: 'center', fontSize: '14px', display: 'flex' }}>
                <ComboBox
                  currentItem={code.name}
                  items={[
                    {
                      name: 'C++ 17',
                      language: 'C++',
                      version: '17',
                      editorName: 'c_cpp',
                    },
                    {
                      name: 'C++ 14',
                      language: 'C++',
                      version: '14',
                      editorName: 'c_cpp',
                    },
                    {
                      name: 'C++ 11',
                      language: 'C++',
                      version: '11',
                      editorName: 'c_cpp',
                    }, ,
                    {
                      name: 'C',
                      language: 'C',
                      version: '',
                      editorName: 'c_cpp',
                    },
                    {
                      name: 'C#',
                      language: 'C#',
                      version: '',
                      editorName: 'csharp',
                    },
                    {
                      name: 'Pascal',
                      language: 'Pascal',
                      version: '',
                      editorName: 'pascal',
                    },
                    {
                      name: 'Python 3',
                      language: 'Python',
                      version: '3',
                      editorName: 'python',
                    },
                    {
                      name: 'Python 2',
                      language: 'Python',
                      version: '2',
                      editorName: 'python',
                    },
                    {
                      name: 'Java 11',
                      language: 'Java',
                      version: '11',
                      editorName: 'java',
                    },
                    {
                      name: 'JavaScript',
                      language: 'JavaScript',
                      version: '',
                      editorName: 'javascript',
                    },
                    {
                      name: 'Node.js',
                      language: 'Node.js',
                      version: '',
                      editorName: 'javascript',
                    }
                  ]}
                  itemSelected={on_combo_itemSelected}
                />
              </div>
            }
          />
          <Button style={{ marginTop: 30 }} onClick={onCodeAccepted}></Button>
        </div>

      </Modal>
    


      {/*   */}
      <Modal width="600px" title=" - " isOpen={isDlgLinkOpen} close={onCloseDlgLink}>
        <form onSubmit={onLinkAccepted}>
          <Input title="" focus value={text} onChange={(e) => setText(e.target.value)} />
          <Input title="-" value={webAddress} onChange={(e) => setWebAddress(e.target.value)}/>
          <Button style={{ marginTop: 30 }}></Button>
        </form>

      </Modal>
    </StyledDiv>
  )
}

export default TextArea